import { Civilite } from '../enums/civilite.enum';

export class RepresentantOperateurEconomiquePostRequest {
    
    nom: string;
    prenom: string;
    civilite: string;
    qualite: string;
    email: string;
    telephone: string[];
    portable: string[];
    fax: string[];
    nationalite: string; 

    
    constructor (
        nom: string,
        prenom: string,
        civilite: Civilite,
        qualite: string,
        email: string,
        telephone: string[],
        portable: string[],
        fax: string[],
        nationalite: string
    ){
        this.nom = nom,
        this.prenom = prenom,
        this.civilite = civilite,
        this.qualite = qualite,
        this.email = email,
        this.telephone = telephone,
        this.portable = portable,
        this.fax = fax,
        this.nationalite = nationalite
    }

}