import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AdditifCDCService } from 'src/app/domaine/business-logic/additifs-cdc.service';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { UploadService } from 'src/app/upload.service';
import { AdditifCDC } from 'src/app/models/additif-cdc.model';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { AdditifCDCPostRequest } from 'src/app/requests/additif-cdc-post.request';

export interface DialogData {
  animal: string;
  name: string;
}


@Component({
  selector: 'app-create-additif',
  templateUrl: './create-additif.component.html',
  styleUrls: ['./create-additif.component.css']
})
export class CreateAdditifComponent implements OnInit {


  constructor(
    public dialogRef: MatDialogRef<CreateAdditifComponent>,
    private additifCDCService: AdditifCDCService,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, elbCDC: ElaborationCDC},
    private uploadService: UploadService
  ) {
  };







  additifForm: FormGroup;
  isCreate : boolean;


  


  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;


    this.additifForm = new FormGroup({
      'pathAdditifCDC': new FormControl({value: null, disabled: false}, [Validators.required])
    });



  };











  //Data model to submit to the DB server
  additifCDCPostRequest: AdditifCDCPostRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.additifForm);




    // save the data into the database
    let result:Promise<AdditifCDC>;
    if (this.isCreate){
      this.additifCDCPostRequest = new AdditifCDCPostRequest(
        this.additifForm.value.pathAdditifCDC
      );      
      result = this.additifCDCService.postAdditifCDC(this.additifCDCPostRequest, this.data.elbCDC._id);
    };



    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
        /*
        {
          _id: value._id , 
          pathAdditifCDC: value.pathJustificatifProjet,
          sousProjets : value.sousProjets
        }
        */
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.additifForm.patchValue({'pathAdditifCDC' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}
