import { RoleMembreCommissionCompetente } from 'src/app/enums/role-membre-commission-competente.enum';

export class MembreCommissionCompetente {
  _id: string;
  dateCreationMembreCommissionCompetente: Date;
  role : RoleMembreCommissionCompetente;
  nom: string;
  prenom: string;
  qualite: string;
  matricule: string;
  email: string;
  telephone: string;
  penta: string;
  
  constructor(
    role : RoleMembreCommissionCompetente,
    nom: string,
    prenom: string,
    qualite: string,
    matricule: string,
    email: string,
    telephone: string,
    penta: string
  ) {
    this.role = role;
    this.nom= nom;
    this.prenom= prenom;
    this.qualite= qualite;
    this.matricule= matricule;
    this.email= email;
    this.telephone= telephone;
    this.penta= penta;
  }
  
  static getDummy(): MembreCommissionCompetente {
      return new MembreCommissionCompetente(
        RoleMembreCommissionCompetente.PRESIDENT,
          "nom",
          "prenom",
          "qualite",
          "matricule",
          "test@test.dz",
          "026226262",
          "85202"
      )
  }
}