import { Injectable } from '@angular/core';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { SousProjetAccess } from '../data-access/sous-projet.access';
import { SousProjetPostRequest } from 'src/app/requests/sous-projet-post.request';
import { SousProjetPutRequest } from 'src/app/requests/sous-projet-put.request';

@Injectable()
export class SousProjetService {

  constructor(private sousProjetAccess: SousProjetAccess){}

  getSousProjet(idSousProjet: string): Promise<SousProjet> {
    return this.sousProjetAccess.getSousProjet(idSousProjet);
  }

  getAllSousProjets(): Promise<SousProjet[]> {
    return this.sousProjetAccess.getAllSousProjets();
  }

  getAllSousProjetsOfProjet(id : string): Promise<SousProjet[]> {
    return this.sousProjetAccess.getAllSousProjetsOfProjet(id);
  }

  postSousProjet(sousProjet: SousProjetPostRequest, idProjet: string): Promise<SousProjet> {
    return this.sousProjetAccess.postSousProjet(sousProjet, idProjet);
  }

  putSousProjet(sousProjet: SousProjetPutRequest, idSousProjet: string): Promise<SousProjet> {
    return this.sousProjetAccess.putSousProjet(sousProjet, idSousProjet);
  }

  deleteSousProjet(idProjet: string, idSousProjet:string):Promise<SousProjet> {
    return this.sousProjetAccess.deleteSousProjet(idProjet, idSousProjet);
  }
}



