import { Component, ViewChild, ViewChildren, QueryList, Input } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { PassageCommissionCompetente } from 'src/app/models/passage.commission.competente.model';
import { CreateAdditifComponent } from 'src/app/dialogs/create-additif/create-additif.component';
import { CreatePassageCommissionCompetenteComponent } from 'src/app/dialogs/create-passage-commission-competente/create-commission-passage-competente.component';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { TypeProcedure } from 'src/app/enums/type.procedure.enum';
import { CaractereMarche } from 'src/app/enums/caractere.marche.enum';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { CreateElaborationCDCComponent } from 'src/app/dialogs/create-elaboration-cdc/create-elaboration-cdc.component';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { ModePassation } from 'src/app/enums/mode.passation.enum';
import { FormeMarche } from 'src/app/enums/forme.marche.enum';
import { AdditifCDC } from 'src/app/models/additif-cdc.model';
import { UploadService } from 'src/app/upload.service';
import { fileUploadUrl } from 'src/app/config';
import { ProjetCDC } from 'src/app/models/projet-cdc.model';
import { ProjetCDCService } from 'src/app/domaine/business-logic/projets-cdc.service';
import { ProjetCDCPostRequest } from 'src/app/requests/projet-cdc-post.request';
import { ProjetCDCPutRequest } from 'src/app/requests/projet-cdc-put.request';
import { NaturePrestation } from 'src/app/enums/nature.prestation.enum';
import { TypeCommissionCompetente } from 'src/app/enums/type-commission-competente.enum';
import { Niveau } from 'src/app/enums/niveau.enum';
import { AdditifCDCService } from 'src/app/domaine/business-logic/additifs-cdc.service';
import { CreateDepotAdditifCDCSecretariatCommissionCompetenteCDC } from 'src/app/dialogs/create-depot-additif-cdc-secretariat-commission-competente-cdc copy/create-depot-additif-cdc-secretariat-commission-competente-cdc';
import { CreateDepotProjetCDCSecretariatCommissionCompetenteCDC } from 'src/app/dialogs/create-depot-projet-cdc-secretariat-commission-competente-cdc/create-depot-projet-cdc-secretariat-commission-competente-cdc';
import { CreateLancementComponent } from 'src/app/dialogs/create-lancement/create-lancement.component';


@Component({
  selector: 'app-elaboration-cdc',
  templateUrl: './elaboration_cdc.component.html',
  styleUrls: ['./elaboration_cdc.component.css']
})

export class ElaborationCDCComponent {

  @ViewChild("tablePassagesCommissionCompetenteCDC", {static: true}) tablePassagesCommissionCompetenteCDC: MatTable<PassageCommissionCompetente>;
  @ViewChildren("tablePassagesCommissionCompetenteAdditif") tablesPassagesCommissionCompetenteAdditif: QueryList<MatTable<PassageCommissionCompetente>>;

  @Input() elaborationCDC : ElaborationCDC;
  @Input() sousProjet : SousProjet;
  @Input() projet: Projet;



  passageCommissionChecked:boolean;
  additifsChecked:boolean = false;

  passagesCommissionCompetenteAdditifs: Map<string, Array<PassageCommissionCompetente>> = new Map() ;
  passagesCommissionCompetenteCDC: Array<PassageCommissionCompetente> = []
  

  displayedColumnsPassagesCommissionCompetente: string[] = [
    'dateDepotSecretariatCommission',
    'datePassageCommission',
    'decision',
    'dateLeveeReserves',
    'PVPath',
    'dateDecision',
    'numero',
    'decisionPath'
  ]

  constructor(public dialog: MatDialog,
    private uploadService: UploadService,
    private projetCDCService: ProjetCDCService,
    private additifCDCService: AdditifCDCService) { }

  isNewProjetCDCUploaded():boolean{
    if (this.elaborationCDC.projetCDC && this.elaborationCDC.projetCDC.pathProjetCDC == this.pathJustificatifProjetCDC)
    return false;
    return this.pathJustificatifProjetCDC != null;
    ;
  }
  isProjetCDCUploaded():boolean{
    return this.elaborationCDC.projetCDC && this.elaborationCDC.projetCDC.pathProjetCDC != null;
  }
  annulerUploadProjetCDC():void{
    if(this.elaborationCDC.projetCDC){
      this.pathJustificatifProjetCDC = this.elaborationCDC.projetCDC.pathProjetCDC;
    }else{
      this.pathJustificatifProjetCDC = null;
    }
  }


  loadingDataUploadProjetCDC: boolean;
  errorLoadingUploadProjetCDC: boolean;
  uploadProjetCDC():void{
    this.loadingDataUploadProjetCDC = true;
    this.errorLoadingUploadProjetCDC = false;
    
    // save the data into the database
    let result:Promise<ProjetCDC>;

    if(this.elaborationCDC.projetCDC){
      const projetCDCPutRequest:ProjetCDCPutRequest = new ProjetCDCPutRequest(
        this.pathJustificatifProjetCDC
      );
      result = this.projetCDCService.postProjetCDC(projetCDCPutRequest, this.elaborationCDC._id);
    }else{
      const projetCDCPostRequest:ProjetCDCPostRequest = new ProjetCDCPostRequest(
        this.pathJustificatifProjetCDC
      );
      result = this.projetCDCService.postProjetCDC(projetCDCPostRequest, this.elaborationCDC._id);
    }

    result
    .then((value) => {
      this.loadingDataUploadProjetCDC = false;
      console.log("posted", value);
      this.elaborationCDC.projetCDC = value;
      this.pathJustificatifProjetCDC = value.pathProjetCDC;
    })
    .catch(()=>{
      this.loadingDataUploadProjetCDC = false;
      return this.errorLoadingUploadProjetCDC = true;
    });
  }

  loadingDataDeleteProjetCDC: boolean;
  errorLoadingDataDeleteProjetCDC: boolean;
  deleteProjetCDC(){
    this.loadingDataDeleteProjetCDC = true;
    this.errorLoadingDataDeleteProjetCDC = false;
    let result:Promise<ProjetCDC>;
    // delete le S/C data from the database
    result = this.projetCDCService.deleteProjetCDC(this.elaborationCDC._id,this.elaborationCDC.projetCDC._id);
    result
    .then((value) => {
      this.loadingDataDeleteProjetCDC = false;
      console.log("cleared", value);
      this.elaborationCDC.projetCDC.pathProjetCDC = null;
      this.pathJustificatifProjetCDC = null;
    })
    .catch(()=>{
      this.loadingDataDeleteProjetCDC = false;
      return this.errorLoadingDataDeleteProjetCDC = true;
    });
  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;
  pathJustificatifProjetCDC : string;
  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.pathJustificatifProjetCDC =  val.filename;
          console.log(this.pathJustificatifProjetCDC);
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }












  loadingDataDeleteAdditifCDC = {};
  errorLoadingDataDeleteAdditifCDC = {};
  deleteAdditifCDC(idAdditifCDC: string){
    this.loadingDataDeleteAdditifCDC[idAdditifCDC] = true;
    this.errorLoadingDataDeleteAdditifCDC[idAdditifCDC]  = false;
    let result:Promise<AdditifCDC>;
    // delete le S/C data from the database
    var indexToDeleteAdditif = -1;
    var itemToDeleteAdditif = null;
    this.elaborationCDC.additifsCDC.forEach(function(item:AdditifCDC, index) {
      if(item._id == idAdditifCDC){
        indexToDeleteAdditif = index;
        itemToDeleteAdditif =  item;
      }
    });

    if (itemToDeleteAdditif==null || indexToDeleteAdditif ==-1){
      console.log("Can't delete it. Something went wrong.");
    }


    result = this.additifCDCService.deleteAdditifCDC(this.elaborationCDC._id,idAdditifCDC);
    result
    .then((value) => {
      this.loadingDataDeleteAdditifCDC[idAdditifCDC]  = false;
      console.log("cleared", value);
      this.elaborationCDC.additifsCDC.splice(indexToDeleteAdditif,1);
    })
    .catch(()=>{
      this.loadingDataDeleteAdditifCDC[idAdditifCDC]  = false;
      return this.errorLoadingDataDeleteAdditifCDC[idAdditifCDC]  = true;
    });

  }




  isPassageCommissionInProgress(): boolean {
    /* 
    //In case it is a list
    return this.elaborationCDC.projetCDC && 
    this.elaborationCDC.projetCDC.passagesCommissionMarches &&
    this.elaborationCDC.projetCDC.passagesCommissionMarches.length > 0;
     */
    return this.elaborationCDC.projetCDC && this.elaborationCDC.projetCDC.passagesCommissionMarches != null ;
  }

  isPassageCommissionMandatory():boolean{
    return this.getCommissionCompetente() !== null;
  }
  isPassageCommissionDisabled():boolean {
    // disabled if
    // - procedure formalisee
    // - ou il y a deja des passage commission en cours.
    return this.isPassageCommissionMandatory() || this.isPassageCommissionInProgress()

  }

  getPathFile(filename:string){
    return `${fileUploadUrl}/sendMePDF/${filename}`;
  }
  
  ngOnInit() {
    console.log("Elaboration:", this.elaborationCDC);
    this.passageCommissionChecked = this.isPassageCommissionDisabled();
    this.additifsChecked = false

    if(this.elaborationCDC.projetCDC){
      this.pathJustificatifProjetCDC = this.elaborationCDC.projetCDC.pathProjetCDC;
    }else{
      this.pathJustificatifProjetCDC = null;
    }

    this.additifsChecked = (this.elaborationCDC.additifsCDC && this.elaborationCDC.additifsCDC.length > 0);
  }







  openDialogDeleteElaborationCDC(): void{
    const dialogRef = this.dialog.open(CreateElaborationCDCComponent, {
      width: '800px',
      data: {action: DialogAction.DELETE, elbCDC: this.elaborationCDC, sousPrj: this.sousProjet, prj: this.projet}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(this.sousProjet && result) {
        let elm = this.sousProjet.elaborationsCDC.find(e => e._id === result._id);
        const index = this.sousProjet.elaborationsCDC.indexOf(elm);
        this.sousProjet.elaborationsCDC.splice(index,1);
      }
    });    
  };
  openDialogUpdateElaborationCDC(): void{
    const dialogRef = this.dialog.open(CreateElaborationCDCComponent, {
      width: '800px',
      data: {action: DialogAction.MODIFY, elbCDC: this.elaborationCDC, sousPrj: this.sousProjet, prj: this.projet}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(this.sousProjet && result) {
        let elm = this.sousProjet.elaborationsCDC.find(e => e._id === result._id);
        for(var k in result) elm[k]=result[k];
      }
    });  
  };


  

  openDialogCreateAdditif(): void {
    const dialogRef = this.dialog.open(CreateAdditifComponent, {
      width: '800px',
      data:  {action: DialogAction.CREATE, elbCDC: this.elaborationCDC}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed', result);
      if (result){
        if (this.elaborationCDC.additifsCDC){
          this.elaborationCDC.additifsCDC.push(result);
        }else{
          this.elaborationCDC.additifsCDC = [result]
        }
      }
    });

  }

  openDialogCreatePassageCommissionCompetenteAdditifs(numero:string): void {
    const dialogRef = this.dialog.open(CreatePassageCommissionCompetenteComponent, {
      width: '800px',
      data: "nouveauAdditif"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    //this.passagesCommissionCompetenteAdditifs.get(numero).push(PassageCommissionCompetente.getDummy())

    if (this.passagesCommissionCompetenteAdditifs.has(numero)){
      const passages = this.passagesCommissionCompetenteAdditifs.get(numero);
      passages.push(PassageCommissionCompetente.getDummy());
    } else {
      const passages: Array<PassageCommissionCompetente> = []
      passages.push(PassageCommissionCompetente.getDummy())
      this.passagesCommissionCompetenteAdditifs.set(numero,passages)
    }

    //console.log(this.passagesCommissionCompetenteAdditifs)
    if(this.tablesPassagesCommissionCompetenteAdditif){
      this.tablesPassagesCommissionCompetenteAdditif.forEach( (element) => {
        //don't render if empty table
        if (element.dataSource){
          element.renderRows();
        }
      });
    }
  }

  openDialogCreatePassageCommissionCompetenteCDC(): void {
    const dialogRef = this.dialog.open(CreatePassageCommissionCompetenteComponent, {
      width: '800px',
      data: "nouveauPassageCommComp"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    this.passagesCommissionCompetenteCDC.push(PassageCommissionCompetente.getDummy())
    //console.log(this.litiges);
    this.tablePassagesCommissionCompetenteCDC.renderRows();

  }

  isInternational(){
    return CaractereMarche[this.elaborationCDC.caractereMarche] === CaractereMarche.INTERNATIONAL;
  }

  getTypeProcedure(): string {
    return TypeProcedure[this.elaborationCDC.typeProcedure];
  }

  getModePassation(): string {
    return ModePassation[this.elaborationCDC.modePassation];
  }

  getFormeMarche(): string {
    return FormeMarche[this.elaborationCDC.formeMarche];
  }

  getCaractereMarche(): string {
    return CaractereMarche[this.elaborationCDC.caractereMarche];
  }




  getCommissionCompetente(): TypeCommissionCompetente{ // change return to TypeCommissionCompetente
    switch(NaturePrestation[this.sousProjet.naturePrestation]){
      case NaturePrestation.TRAVAUX:
      case NaturePrestation.FOURNITURES:
      switch(Niveau[this.projet.serviceContractant.niveau]){
        case Niveau.CENTRAL:
          if(this.sousProjet.estimationAdministrative>=20_000_000) return TypeCommissionCompetente.CCME;
          else return null; //"Passage par la commission n'est pas obligatoire"
          break;
        case Niveau.REGIONAL:
        case Niveau.UNITE: 
        if(this.sousProjet.estimationAdministrative>=40_000_000) return TypeCommissionCompetente.CCME;
        else if(this.sousProjet.estimationAdministrative>=12_000_000) return TypeCommissionCompetente.CMU;
        else return null; //"Passage par la commission n'est pas obligatoire"
          break
      }

      break;
      case NaturePrestation.ETUDES:
      case NaturePrestation.SERVICES: 
      switch(Niveau[this.projet.serviceContractant.niveau]){
        case Niveau.CENTRAL:
          if(this.sousProjet.estimationAdministrative>=12_000_000) return TypeCommissionCompetente.CCME;
          else return null; //"Passage par la commission n'est pas obligatoire"
          break;
        case Niveau.REGIONAL:
        case Niveau.UNITE: 
          if(this.sousProjet.estimationAdministrative>=12_000_000) return TypeCommissionCompetente.CCME;
          else if(this.sousProjet.estimationAdministrative>=6_000_000) return TypeCommissionCompetente.CMU;
          else return null; //"Passage par la commission n'est pas obligatoire"
          break
      }      
      break;      
    }
      
  }


  
  openDialogCreateDepotProjetCDCSecretariatCommissionCompetenteCDC(): void {
    const dialogRef = this.dialog.open(CreateDepotProjetCDCSecretariatCommissionCompetenteCDC, {
      width: '800px',
      data: {action: DialogAction.CREATE, elbCDC: this.elaborationCDC, sousPrj: this.sousProjet, prj: this.projet, typCommComp: this.getCommissionCompetente()}
    });

    dialogRef.afterClosed().subscribe(result => {
        console.log("After create depot result:", result);

        if (this.elaborationCDC && result) {
          this.elaborationCDC.projetCDC.passagesCommissionMarches = result;
          console.log("Cool updating", this.elaborationCDC.projetCDC);
        }

    });

  }


  openDialogCreateLancement(): void {
    const dialogRef = this.dialog.open(CreateLancementComponent, {
      width: '800px',
      data: {action: DialogAction.CREATE, elb_cdc: this.elaborationCDC, lncm: null}
    });

    dialogRef.afterClosed().subscribe(result => {
        console.log("After create depot result:", result);
        console.log("Whaaat:", this.elaborationCDC.lancements);
        console.log("check this; ", this.elaborationCDC);
        if (this.elaborationCDC && result) {
          this.elaborationCDC.lancements.push(result);
          console.log("Cool updating", this.elaborationCDC.projetCDC);
        }

    });

  }

  openDialogCreateDepotAdditifCDCSecretariatCommissionCompetenteCDC(additifCDCId: string): void {
    const dialogRef = this.dialog.open(CreateDepotAdditifCDCSecretariatCommissionCompetenteCDC, {
      width: '800px',
      data: {action: DialogAction.CREATE, 
        elbCDC: this.elaborationCDC, 
        sousPrj: this.sousProjet, 
        prj: this.projet, 
        additifCDCId: additifCDCId,
        typCommComp: this.getCommissionCompetente()}
    });

    dialogRef.afterClosed().subscribe(result => {
        console.log("After create depot result:", result);

        if (this.elaborationCDC && this.elaborationCDC.additifsCDC && result) {
          this.elaborationCDC.projetCDC.passagesCommissionMarches = result;
          console.log("Cool updating", this.elaborationCDC.projetCDC);




          var indexToChange = -1;
          this.elaborationCDC.additifsCDC.forEach(function(item:AdditifCDC, index) {
            if(item._id == additifCDCId){
              indexToChange = index;
            }
          });
          if (indexToChange===-1){
            console.log("Oups somthing went wrong when update the page additifs", result);
          }else{
          this.elaborationCDC.additifsCDC[indexToChange].passagesCommissionMarches = result;
          }




        }

    });

  }

}
