export class NotificationOperateurEconomiquePostRequest {
    
    operateurEconomiqueId: string;
    dateNotification: Date;
    pathJustificatif: string;
    moyenNotification: string; 
    
    constructor(
        operateurEconomiqueId: string,
        dateNotification: Date,
        pathJustificatif : string,
        moyenNotification: string
        ){
          this.operateurEconomiqueId = operateurEconomiqueId;
          this.dateNotification = dateNotification;
          this.pathJustificatif = pathJustificatif;
          this.moyenNotification = moyenNotification;
        }
}