export class Facture {
    hasPenaliteRetard: boolean
    numFacture: string
    dateReception: Date
    montant: number
    facturePath: string

    constructor(
        hasPenaliteRetard: boolean,
        numFacture: string,
        dateReception: Date,
        montant: number,
        facturePath: string
    ) {
        this.hasPenaliteRetard = hasPenaliteRetard,
            this.numFacture = numFacture,
            this.dateReception = dateReception,
            this.montant = montant,
            this.facturePath = facturePath
    }

    static getDummy(): Facture {
        return new Facture(
            true,
            "numFacture",
            new Date(),
            5000,
            "facturePath"
        )
    }
}