import { Component, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';
import HC_exporting from 'highcharts/modules/exporting';
HC_exporting(Highcharts);

@Component({ 
  selector: 'app-bar-chart',
  templateUrl: './bar-chart.component.html',
  styleUrls: ['./bar-chart.component.css']
})
export class BarChartComponent implements OnInit {

  constructor() { }

  Highcharts: typeof Highcharts = Highcharts;

  chartOptions: Highcharts.Options = {
    chart: {
        type: 'column'
    },
    title: {
        text: 'Budjet par Wilaya 2017'
    },
    subtitle: {
        text: 'Source: <a href="http://poste.dz">Algérie Poste</a>'
    },
    xAxis: {
        type: 'category',
        labels: {
            rotation: -45,
            style: {
                fontSize: '13px',
                fontFamily: 'Verdana, sans-serif'
            }
        }
    },
    yAxis: {
        min: 0,
        title: {
            text: 'Population (millions)'
        }
    },
    legend: {
        enabled: false
    },
    tooltip: {
        pointFormat: 'Budget in 2017: <b>{point.y:.1f} millions</b>'
    },
    series: [{
        name: 'Budget',
        type: 'bar',
        data: [
            ['Alger', 24.2],
            ['Oran', 20.8],
            ['Constantine', 14.9],
            ['Batna', 13.7],
            ['Boumerdes', 13.1],
            ['Tipaza', 12.7],
            ['Adrar', 11.1],
            ['Bouira', 10.6],
            ['Ghilizan', 10.6],
            ['Tindouf', 9.3]
        ],
        dataLabels: {
            enabled: true,
            rotation: 0,
            color: '#FFFFFF',
            align: 'right',
            format: '{point.y:.1f}', // one decimal
            y: 10, // 10 pixels down from the top
            style: {
                fontSize: '8px',
                fontFamily: 'Verdana, sans-serif'
            }
        }
    }]
    };


  
  ngOnInit() {
  }

}
