import { ServiceContractant } from '../service.contractant.model'
import { SourceFinancement } from 'src/app/enums/source.financement.enum'
import { SousProjet } from './sous-projet.model';
import { Budget } from 'src/app/enums/budget.enum';

export class Projet {

    public sousProjets : SousProjet[];
    public _id: string;
    public serviceContractant: ServiceContractant;
    public dateCreationProjet: Date;
    public intituleProjet: string;
    public codeProjet: string;
    public montantProjet: number;
    public yearPlanAction: string;
    public pourLaStructureRattachement: string;
    public sourcesFinancement: SourceFinancement[];
    public siAutreSourceFinancement: string;
    public budget: Budget;
    public isHorsPlan: boolean;
    public pathJustificatifProjet: string;
    

    constructor(
        serviceContractant: ServiceContractant,
        dateCreationProjet: Date,
        intituleProjet: string,
        codeProjet: string,
        montantProjet: number,
        yearPlanAction: string,
        pourLaStructureRattachement: string,
        sourcesFinancement: SourceFinancement[],
        siAutreSourceFinancement: string,
        budget: Budget,
        isHorsPlan: boolean,
        pathJustificatifProjet: string

    ) {
        this.serviceContractant= serviceContractant;
        this.dateCreationProjet = dateCreationProjet;
        this.intituleProjet = intituleProjet;
        this.codeProjet = codeProjet;
        this.montantProjet = montantProjet;
        this.yearPlanAction = yearPlanAction;
        this.pourLaStructureRattachement = pourLaStructureRattachement;
        this.sourcesFinancement = sourcesFinancement;
        this.siAutreSourceFinancement = siAutreSourceFinancement;
        this.budget = budget;
        this.isHorsPlan = isHorsPlan;
        this.pathJustificatifProjet = pathJustificatifProjet;

        this.sousProjets = [];
    }

    addSousProjet(sousProjet: SousProjet) : void {
        this.sousProjets.push(sousProjet);
    }

    static getDummy(): Projet {
        return new Projet(
            ServiceContractant.getDummy(),
            new Date(),
            "Opération d'acquisition Matériel Informatique",
            "codeProjetTest",
            Math.random(),
            "2020",
            "Bureau de poste Alpha",
            [SourceFinancement.BUDGET_ETAT, SourceFinancement.FNI, SourceFinancement.AUTRE],
            "FMI",
            Budget.FONCTIONNEMENT,
            true,
            "pathJustificatifProjet: string"
        );
    }
}