import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { CommissionCompetente } from 'src/app/models/commission-competente/commission-competente.model';
import { CommissionCompetentePostRequest } from 'src/app/requests/commission-competente-post.request';
import { CommissionCompetentePutRequest } from 'src/app/requests/commission-competente-put.request';
import { MembreCommissionCompetente } from 'src/app/models/commission-competente/membre-commission-competente.model';


@Injectable()
export class CommissionCompetenteAccess {

  constructor(private http: HttpClient){}


  async getAllCommissionsCompetentes(): Promise<CommissionCompetente[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<CommissionCompetente[]>(
          `${apiEndpoint}/commissions-competentes`,
          )
        .subscribe(
          commissionsCompetentes => { resolve(commissionsCompetentes) },
          error => reject(error)
        )

      });
  };

  async getMembresActifsCommissionCompetente(idCommissionCompetente): Promise<MembreCommissionCompetente[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<MembreCommissionCompetente[]>(
          `${apiEndpoint}/commissions-competentes/membres/${idCommissionCompetente}`,
          )
        .subscribe(
          membres => { resolve(membres) },
          error => reject(error)
        )

      });
  }


  async postCommissionCompetente(commissionCompetente: CommissionCompetentePostRequest): Promise<CommissionCompetente> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<CommissionCompetente>(
          `${apiEndpoint}/commissions-competentes`, 
          commissionCompetente, 
          httpOptions
          )
        .subscribe(
          commissionCompetente => { resolve(commissionCompetente) },
            error => reject(error)
        )
      }
    );
  };


  async putCommissionCompetente(commissionCompetente: CommissionCompetentePutRequest, id: String): Promise<CommissionCompetente> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<CommissionCompetente>(
          `${apiEndpoint}/commissions-competentes/${id}`, 
          commissionCompetente, 
          httpOptions
          )
        .subscribe(
          commissionCompetente => { resolve(commissionCompetente) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteCommissionCompetente(id: String): Promise<CommissionCompetente> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<CommissionCompetente>(
          `${apiEndpoint}/commissions-competentes/${id}`
          )
        .subscribe(
            commissionCompetente => { resolve(commissionCompetente) },
            error => reject(error)
        )
      }
    );
  }
  
};