import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetService } from 'src/app/domaine/business-logic/projets.service';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { ProjetEnAttentePassageCommissionMarches } from 'src/app/models/passage-commission/projet.en.attente.passage.commission.marches.model';
import { SeanceCommissionCompetente } from 'src/app/models/passage-commission/seance.commission.competente.model';
import { fileUploadUrl } from 'src/app/config';
import { MembreCommissionCompetente } from 'src/app/models/commission-competente/membre-commission-competente.model';
import { CommissionCompetenteService } from 'src/app/domaine/business-logic/commissions-competentes.service';
import { SeanceCommissionCompetentePostRequest } from 'src/app/requests/seance-commission-competente-post.request';
import { SeanceCommissionCompetenteService } from 'src/app/domaine/business-logic/seances-commission-competente.service';


@Component({
  selector: 'app-create-seance-commission-competente',
  templateUrl: './create-seance-commission-competente.component.html',
  styleUrls: ['./create-seance-commission-competente.component.css']
})
export class CreateSeanceCommissionCompetenteComponent implements OnInit {





  constructor(
    public dialogRef: MatDialogRef<CreateSeanceCommissionCompetenteComponent>,
    private seanceCommissionCompetenteService: SeanceCommissionCompetenteService,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, 
      doss_att: ProjetEnAttentePassageCommissionMarches[], 
      snc: SeanceCommissionCompetente,
      id_comm: string},
    public commissionCompetenteService: CommissionCompetenteService
  ) {



  };




  seanceForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

  loadingRapporteur: boolean =false;
  optionsRapporteur: MembreCommissionCompetente[] = [];
  filteredOptionsRapporteur: Observable<MembreCommissionCompetente[]>;

  private _filterRapporteur(valueInput: string): MembreCommissionCompetente[] {
    const filterValue = valueInput.toLowerCase();
    return this.optionsRapporteur.filter(
      (rpr : MembreCommissionCompetente) => {
        let option:string = rpr.nom + " " + rpr.prenom; 
        return option.toLowerCase().includes(filterValue);
      }
    );
  }

  getSelectedRapporteur(_id : string) : MembreCommissionCompetente{
    if (_id){
      return this.optionsRapporteur.find(e => e._id === _id);
    } else {
      return null;
    }
  }

  getSelectedRapporteurPrenom(): string{
    const result = this.getSelectedRapporteur(this.seanceForm.value.rapporteur._id);
    if (result) {return result.prenom;}
    return "";
  };

  getSelectedRapporteurNom(): string{
    const result = this.getSelectedRapporteur(this.seanceForm.value.rapporteur._id);
    if (result) {return result.nom;}
    return "";
  };

  getSelectedRapporteurMatricule(): string{
    const result = this.getSelectedRapporteur(this.seanceForm.value.rapporteur._id);
    if (result) {return result.matricule;}
    return "";
  };

  getSelectedRapporteurRole(): string{
    const result = this.getSelectedRapporteur(this.seanceForm.value.rapporteur._id);
    if (result) {return result.role;}
    return "";
  };

  getSelectedRapporteurEmail(): string{
    const result = this.getSelectedRapporteur(this.seanceForm.value.rapporteur._id);
    if (result) {return result.email;}
    return "";
  };

  getSelectedRapporteurTelephone(): string{
    const result = this.getSelectedRapporteur(this.seanceForm.value.rapporteur._id);
    if (result) {return result.telephone;}
    return "";
  };


  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.snc.dateCreationSeance);
    }




    this.seanceForm = new FormGroup({
      'dateCreationSeance': new FormControl({value: null, disabled: true}, [Validators.required]),
      'dateProgrammationSeance': new FormControl({value: null, disabled: false}, [Validators.required],[this.dateAnterieur.bind(this)]),
      'rapporteur': new FormGroup({
        '_id' : new FormControl({value: null, disabled: false}, [Validators.required],[this.existsRapporteur.bind(this)]),
        'nom' : new FormControl({value: null, disabled: true}, [Validators.required]),
        'prenom' : new FormControl({value: null, disabled: true}, [Validators.required]),

        'matricule' : new FormControl({value: null, disabled: true}, [Validators.required]),
        'role' : new FormControl({value: null, disabled: true}, [Validators.required]),
        'email' : new FormControl({value: null, disabled: true}, [Validators.required]),
        'telephone' : new FormControl({value: null, disabled: true}, [Validators.required])
      })
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.seanceForm.patchValue(this.data.snc);
    }


    // load les rapporteurs for admin
    this.loadingRapporteur = true;
    const result:Promise<MembreCommissionCompetente[]> = this.commissionCompetenteService.getMembresActifsCommissionCompetente(this.data.id_comm);
    result
    .then((value) =>{
      this.loadingRapporteur = false;
      this.optionsRapporteur = value;

      // recheck once the data is loaded
      const rapporteurForm : AbstractControl =  this.seanceForm.get("rapporteur");
      (<FormGroup>rapporteurForm).controls["_id"].updateValueAndValidity();

      // generate the filtered data
      this.filteredOptionsRapporteur = this.seanceForm.get("rapporteur").get("_id").valueChanges
      .pipe(
        startWith(''),
        map( valueRapporteur => this._filterRapporteur(valueRapporteur) )
      );

      return;
    })
    .catch(()=>{
      this.loadingRapporteur = false ;
      return this.optionsRapporteur = null;
    });

  };


  existsRapporteur(control: FormControl): Promise<any> | Observable<any> {
    const _id = control.value;
    const result:MembreCommissionCompetente = this.getSelectedRapporteur(_id);
    return new Promise<any>(
      (resolve, reject) => {
          if (result != null) {
            resolve(null);
          } else {
            resolve({ "rapporteurInvalid": true });
          }
        }
    )
  }

  dateAnterieur(control: FormControl): Promise<any> | Observable<any> {
    const date = control.value;
    const dateAnterieur:boolean = new Date(date) > new Date() ;
    return new Promise<any>(
      (resolve, reject) => {
          if (dateAnterieur) {
            resolve(null);
          } else {
            resolve({ "datePasAnterieur": true });
          }
        }
    )
  }
  





  






  //Data model to submit to the DB server
  seanceCommissionCompetentePostRequest: SeanceCommissionCompetentePostRequest;
  projetPutRequest: ProjetPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.seanceForm);




    // save the data into the database
    let result:Promise<SeanceCommissionCompetente>;

    if (this.isCreate){
      this.seanceCommissionCompetentePostRequest = new SeanceCommissionCompetentePostRequest(
        this.seanceForm.value.dateProgrammationSeance,
        this.seanceForm.value.rapporteur._id,
        this.data.doss_att.map(e => e.idPassageCommissionMarches),
        this.data.id_comm
      );
      console.log("Rapporteur ***-*", this.seanceForm.getRawValue());
      //console.log(this.seanceForm.value);
      result = this.seanceCommissionCompetenteService.postSeanceCommissionCompetente(this.seanceCommissionCompetentePostRequest, this.data.id_comm);

    };
    /*
    // update the data in the database
    if(this.isModify){
      this.projetPutRequest = new ProjetPutRequest(
        this.projetForm.value.serviceContractant._id,
        this.projetForm.value.intituleProjet,
        this.projetForm.value.codeProjet,
        this.projetForm.value.montantProjet,
        this.projetForm.value.yearPlanAction,
        this.projetForm.value.pourLaStructureRattachement,
        this.projetForm.value.sourcesFinancement,
        this.projetForm.value.siAutreSourceFinancement,
        this.projetForm.value.budget,
        this.projetForm.value.isHorsPlan,
        this.projetForm.value.pathJustificatifProjet
      );      
      //result = this.projetService.putProjet(this.projetPutRequest, this.data.prj._id);
    };
    */


    // delete le S/C data from the database
    if(this.isDelete){
      //result = this.projetService.deleteProjet(this.data.prj._id);
    };
    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        {
          _id: value._id , 
          rapporteur:   value.rapporteur,
          dateProgrammationSeance: value.dateProgrammationSeance,
          passagesCommissionMarches: value.passagesCommissionMarches,
          commissionCompetente: value.commissionCompetente,
          dateCreationSeance: value.dateCreationSeance
        }
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }



  dateNow: Date = new Date();
  getWaitingDays(dateFrom){
    var diffDays = this.dateNow.getDate() - new Date(dateFrom).getDate();
    return diffDays;
  }
  getPathFile(filename:string){
    return `${fileUploadUrl}/sendMePDF/${filename}`;
  }

  
}
