export class RallongeFinanciere {
    serviceContractant:string
    dateDemande:Date
    montantDemande: number
    demandePath: string
    isAnswered: boolean // if true, the three below fields are used
    reponse: string //positive ou negative, ou aucun.
    montantAccorde:number
    reponsePath: string 
    dateReponse: Date 

    constructor (
        serviceContractant: string,
        dateDemande:Date,
        montantDemande: number,
        demandePath: string,
        isAnswered: boolean, // if true, the three below fields are used
        reponse: string, //positive ou negative, ou aucun.
        montantAccorde:number,
        reponsePath: string, 
        dateReponse: Date 
    ){
        this.serviceContractant = serviceContractant,
        this.dateDemande=dateDemande,
        this.montantDemande=montantDemande,
        this.demandePath=demandePath,
        this.isAnswered=isAnswered,
        this.reponse=reponse,
        this.montantAccorde=montantAccorde,
        this.reponsePath=reponsePath, 
        this.dateReponse=dateReponse
    }

    static getDummy(): RallongeFinanciere {
        const rand = Math.random()
        if (rand < 0.5) 
            return new RallongeFinanciere(
                "serviceContractant",
                new Date(),
                500,
                "demandePath",
                false,
                null,
                null,
                null,
                null
            )
        else
            return new RallongeFinanciere(
                "serviceContractant",
                new Date(),
                500,
                "demandePath",
                true,
                "reponse",
                200,
                "reponsePath", 
                new Date()
            )
      }
}