export class PassageComiteTechnique {
    comiteTechnique: string
    dateSaisie: Date
    demandePath: string
    isAnswered: boolean // if true, the three below fields are used
    reponse: string //positive ou negative, ou aucun.
    reponsePath: string 
    dateReponse: Date 

    constructor (
        comiteTechnique: string,
        dateSaisie: Date,
        demandePath:string,
        isAnswered: boolean,
        reponse: string,
        reponsePath: string ,
        dateReponse: Date 
    ){
        this.comiteTechnique = comiteTechnique,
        this.dateSaisie = dateSaisie,
        this.demandePath = demandePath,
        this.isAnswered = isAnswered,
        this.reponse = reponse,
        this.reponsePath = reponsePath ,
        this.dateReponse = dateReponse 
    }
    static getDummy(): PassageComiteTechnique {
        const rand = Math.random()
        if (rand < 0.5) 
            return new PassageComiteTechnique(
                "CT#",
                new Date(),
                "demandePath",
                false,
                null,
                null,
                null
            )
        else
            return new PassageComiteTechnique(
                "CT#",
                new Date(),                
                "demandePath",
                true,
                "reponse",
                "reponsePath" ,
                new Date() 
            )
      }
}