import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatDialog, MatTableDataSource, MatTable } from '@angular/material'; 
import { fileUploadUrl } from '../config'; 
import { COPEO } from '../models/copeo/copeo.model';
import { COPEOService } from '../domaine/business-logic/copeos.service';
import { CreateResultatCopeoOuverturePlisComponent } from '../dialogs/create-resultat-copeo-ouverture-plis/create-resultat-copeo-ouverture-plis.component';
import { Lancement } from '../models/lancement/lancement.model';
import { Projet } from '../models/repertoire/projet.model';
import { ElaborationCDC } from '../models/elaboration.cdc.model';
import { SousProjet } from '../models/repertoire/sous-projet.model';
import { NotificationCOPEO } from '../models/copeo/notification-copeo.model';
import { DialogAction } from '../enums/dialog-action.enum';
import { DeliberationCOPEO } from '../models/copeo/deliberation-copeo.model';
import { CreateDemandeComplementComponent } from '../dialogs/create-demande-complement/create-demande-complement.component';


@Component({
  selector: 'app-bilans-copeos',
  templateUrl: './bilans-copeos.component.html',
  styleUrls: ['./bilans-copeos.component.css']
})
export class BilansCOPEOsComponent implements OnInit {

  idCOPEO : string ;    
  copeo : COPEO ;
  loadingDataCOPEO : boolean ;

  
  date:Date =  new Date();
    
  constructor(
    private route: ActivatedRoute,
    public dialog: MatDialog,
    public COPEOService: COPEOService,
  ) {
    this.idCOPEO = route.snapshot.paramMap.get('idCOPEO'); 
  }


  ngOnInit() {
     
    this.loadingDataCOPEO = true;
    const result:Promise<COPEO> = this.COPEOService.getCOPEOById(this.idCOPEO);
    result
    .then((value) =>{
      this.loadingDataCOPEO = false;
      return this.copeo = value;
    })
    .catch(()=>{
      this.loadingDataCOPEO = false;
      return this.copeo = null;
    });


  }
 
  openDialogCreateResultatPassageOuverturePlisCOPEO(notificationCOPEO: NotificationCOPEO){
    
    console.log("Notif: ", notificationCOPEO);

    const dialogRef = this.dialog.open(CreateResultatCopeoOuverturePlisComponent, {
      width: '800px',
      data: {
        action: DialogAction.CREATE, 
        notif_copeo: notificationCOPEO,
        dlbr_copeo: null  
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      if(result && notificationCOPEO){
        notificationCOPEO.deliberation  = result;
      }
    });

  }



  openDialogModifyResultatPassageOuverturePlisCOPEO(notificationCOPEO: NotificationCOPEO){
  

    const dialogRef = this.dialog.open(CreateResultatCopeoOuverturePlisComponent, {
      width: '800px',
      data: {
        action: DialogAction.MODIFY, 
        notif_copeo: notificationCOPEO,
        dlbr_copeo: notificationCOPEO.deliberation  
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      if(result && notificationCOPEO){
        notificationCOPEO.deliberation  = result;
      }
    });

  }
  


  openDialogDeleteResultatPassageOuverturePlisCOPEO(notificationCOPEO: NotificationCOPEO){
  

    const dialogRef = this.dialog.open(CreateResultatCopeoOuverturePlisComponent, {
      width: '800px',
      data: {
        action: DialogAction.DELETE, 
        notif_copeo: notificationCOPEO,
        dlbr_copeo: notificationCOPEO.deliberation 
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      if(result && notificationCOPEO){
        notificationCOPEO.deliberation  = null;
      }
    });

  }



















  openDialogCreateDemandeComplement(notificationCOPEO: NotificationCOPEO){
    
    console.log("Notif: ", notificationCOPEO);

    const dialogRef = this.dialog.open(CreateDemandeComplementComponent, {
      width: '800px',
      data: {
        action: DialogAction.CREATE, 
        notif_copeo: notificationCOPEO,
        dlbr_copeo: null  
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      if(result && notificationCOPEO){
        //notificationCOPEO.deliberation  = result;
      }
    });

  }


  getPathFile(filename:string){
    return `${fileUploadUrl}/sendMePDF/${filename}`;
  }


}
