import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { SourceFinancement } from 'src/app/enums/source.financement.enum';
import { Budget } from 'src/app/enums/budget.enum';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetService } from 'src/app/domaine/business-logic/projets.service';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { UploadService } from 'src/app/upload.service';
import { ServiceContractantService } from 'src/app/domaine/business-logic/services-contractants.service';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';


@Component({
  selector: 'app-create-deliberer-commission-competente',
  templateUrl: './create-deliberer-commission-competente.component.html',
  styleUrls: ['./create-deliberer-commission-competente.component.css']
})
export class CreateDelibererCommissionCompetenteComponent implements OnInit {



  constructor(
    public dialogRef: MatDialogRef<CreateDelibererCommissionCompetenteComponent>,
    private projetService: ProjetService,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, pass_comm_comp: string[]},
    private uploadService: UploadService,
    public serviceContractantService: ServiceContractantService
  ) {



  };




  projetForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

  loadingSC: boolean =false;
  //all the SC list
  optionsSC: ServiceContractant[] = [];
  //the filtered SC list to show to the user
  filteredOptionsSC: Observable<ServiceContractant[]>;

  private _filterSC(valueInput: string): ServiceContractant[] {
    const filterValue = valueInput.toLowerCase();
    return this.optionsSC.filter(
      (sc : ServiceContractant) => {
        let option:string = sc.identifiant + " " + sc.nomStructure; 
        return option.toLowerCase().includes(filterValue);
      }
    );
  }

  getSelectedSC(_id : string) : ServiceContractant{
    if (_id){
      return this.optionsSC.find(e => e._id === _id);
    } else {
      return null;
    }
  }

  getSelectedSCIdentifiant(): string{
    const result = this.getSelectedSC(this.projetForm.value.serviceContractant._id);
    if (result) {return result.identifiant;}
    return "";
  };

  getSelectedSCNomStructure(): string{
    const result = this.getSelectedSC(this.projetForm.value.serviceContractant._id);
    if (result) {return result.nomStructure;}
    return "";
  };

  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      //this.date = new FormControl(this.data.prj.dateCreationProjet);
    }




    this.projetForm = new FormGroup({
      'dateCreationProjet': new FormControl({value: null, disabled: true}, [Validators.required]),
      'serviceContractant': new FormGroup({
        '_id' : new FormControl({value: null, disabled: false}, [Validators.required],[this.existsSC.bind(this)]),
        'identifiant' : new FormControl({value: null, disabled: true}, [Validators.required]),
        'nomStructure' : new FormControl({value: null, disabled: true}, [Validators.required])
      }),
      'intituleProjet': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'codeProjet': new FormControl({value: null, disabled: this.isDelete}, [Validators.required],this.codeProjetExists.bind(this)),
      'montantProjet': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'yearPlanAction': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'pourLaStructureRattachement': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'sourcesFinancement': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'siAutreSourceFinancement': new FormControl({value: '', disabled: this.isDelete}, [this.isRequiredAutreSourceFinancement.bind(this)]),
      'budget': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'isHorsPlan': new FormControl({value: false, disabled: this.isDelete}, [Validators.required]),
      'pathJustificatifProjet': new FormControl({value: null, disabled: false}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      //this.projetForm.patchValue(this.data.prj);
    }


    // load les servicesContractant for admin
    this.loadingSC = true;
    const result:Promise<ServiceContractant[]> = this.serviceContractantService.getAllServicesContractants();
    result
    .then((value) =>{
      this.loadingSC = false;
      this.optionsSC = value;

      // recheck once the data is loaded
      const serviceContractantForm : AbstractControl =  this.projetForm.get("serviceContractant");
      (<FormGroup>serviceContractantForm).controls["_id"].updateValueAndValidity();

      // generate the filtered data
      this.filteredOptionsSC = this.projetForm.get("serviceContractant").get("_id").valueChanges
      .pipe(
        startWith(''),
        map( valueSC => this._filterSC(valueSC) )
      );

      return;
    })
    .catch(()=>{
      this.loadingSC = false ;
      return this.optionsSC = null;
    });

  };


  existsSC(control: FormControl): Promise<any> | Observable<any> {
    const _id = control.value;
    const result:ServiceContractant = this.getSelectedSC(_id);
    return new Promise<any>(
      (resolve, reject) => {
          if (result != null) {
            resolve(null);
          } else {
            resolve({ "serviceContractantInvalid": true });
          }
        }
    )
  }


  // Sources de financement
  sourcesFinancement = Object.keys(SourceFinancement).slice(0);
  parseValueSourceFinancement(value: string) {
    return SourceFinancement[value].toString();
  }

  //Budget
  budgets = Object.keys(Budget).slice(0);
  parseValueBudget(value : string) {
    return Budget[value].toString();
  }

  siAutreSourceFinancement(): boolean {
    if (this.projetForm.value.sourcesFinancement && 
      this.projetForm.value.sourcesFinancement.indexOf("AUTRE")>=0) {
      return true;
    }
    return false;
  }



  
  isRequiredAutreSourceFinancement(control: FormControl): any {
    //console.log("control.parent", control.parent);
    console.log("control.value", control.value);
    console.log("control", control);
    
    if (!control.parent) return null;
    const parentControl:FormControl = control.parent.get('sourcesFinancement') as FormControl;
    if (!parentControl) return null;
    //https://stackoverflow.com/questions/31788681/angular2-validator-which-relies-on-multiple-form-fields
    parentControl.valueChanges.subscribe(() => {
      control.updateValueAndValidity();
    });

    if (!parentControl.value) return null;
    if (parentControl.value.indexOf("AUTRE") < 0) return null;
      
    const value = control.value;
    if (!value || value.length == 0) {
        return { "siAutreSourceFinancement": {required: true }};
    } else {
        return null;
    }
    
  }

  
  codeProjetExists(control: FormControl): Promise<any> | Observable<any> {
    const codeProjet = control.value;

    // if action is modify or delete, and identifiant not changed, ignore it
    /*
    if ( (this.data.action === DialogAction.MODIFY 
      || this.data.action === DialogAction.DELETE) && codeProjet === this.data.prj.codeProjet)
    return new Promise<any>((resolve, reject) =>  resolve(null))
    */
    const result:Promise<Projet> = this.projetService.getProjetByCode(codeProjet);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(projet => {
          if (projet != null) {
            resolve({ "codeProjetExist": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }





  //Data model to submit to the DB server
  projetPostRequest: ProjetPostRequest;
  projetPutRequest: ProjetPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.projetForm);




    // save the data into the database
    let result:Promise<Projet>;
    if (this.isCreate){
      this.projetPostRequest = new ProjetPostRequest(
        this.projetForm.value.serviceContractant._id,
        this.projetForm.value.intituleProjet,
        this.projetForm.value.codeProjet,
        this.projetForm.value.montantProjet,
        this.projetForm.value.yearPlanAction,
        this.projetForm.value.pourLaStructureRattachement,
        this.projetForm.value.sourcesFinancement,
        this.projetForm.value.siAutreSourceFinancement,
        this.projetForm.value.budget,
        this.projetForm.value.isHorsPlan,
        this.projetForm.value.pathJustificatifProjet
      );      
      result = this.projetService.postProjet(this.projetPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.projetPutRequest = new ProjetPutRequest(
        this.projetForm.value.serviceContractant._id,
        this.projetForm.value.intituleProjet,
        this.projetForm.value.codeProjet,
        this.projetForm.value.montantProjet,
        this.projetForm.value.yearPlanAction,
        this.projetForm.value.pourLaStructureRattachement,
        this.projetForm.value.sourcesFinancement,
        this.projetForm.value.siAutreSourceFinancement,
        this.projetForm.value.budget,
        this.projetForm.value.isHorsPlan,
        this.projetForm.value.pathJustificatifProjet
      );      
      //result = this.projetService.putProjet(this.projetPutRequest, this.data.prj._id);
    };

    // delete le S/C data from the database
    if(this.isDelete){
      //result = this.projetService.deleteProjet(this.data.prj._id);
    };
    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        {
          _id: value._id , 
          serviceContractant: {
            _id: value.serviceContractant._id,
            identifiant: value.serviceContractant.identifiant,
            nomStructure: value.serviceContractant.nomStructure
          },
          intituleProjet : value.intituleProjet,
          codeProjet: value.codeProjet,
          montantProjet: value.montantProjet,
          dateCreationProjet: value.dateCreationProjet,
          yearPlanAction: value.yearPlanAction,
          pourLaStructureRattachement: value.pourLaStructureRattachement,
          sourcesFinancement: value.sourcesFinancement,
          siAutreSourceFinancement: value.siAutreSourceFinancement,
          budget: value.budget,
          isHorsPlan: value.isHorsPlan,
          pathJustificatifProjet: value.pathJustificatifProjet,
          sousProjets : value.sousProjets
        }
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.projetForm.patchValue({'pathJustificatifProjet' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}
