import { Component, ViewChild } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { MiseAuPoint } from 'src/app/models/mise.au.point.model';
import { PassageCommissionCompetente } from 'src/app/models/passage.commission.competente.model';
import { CreatePassageCommissionCompetenteComponent } from 'src/app/dialogs/create-passage-commission-competente/create-commission-passage-competente.component';
import { CreateMiseAuPointComponent } from 'src/app/dialogs/create-mise-au-point/create-mise-au-point.component';


@Component({
  selector: 'app-elaboration-marche',
  templateUrl: './elaboration_marche.component.html',
  styleUrls: ['./elaboration_marche.component.css']
})

export class ElaborationMarcheComponent {



  @ViewChild("tableMisesAuPoint", {static: true}) tableMisesAuPoint: MatTable<MiseAuPoint>;
  @ViewChild("tablePassagesCommissionCompetenteContrat", {static: true}) tablePassagesCommissionCompetenteContrat: MatTable<PassageCommissionCompetente>;

  


  dataSourceMisesAuPoint: MiseAuPoint [] = []
  displayedColumnsMisesAuPoint: string[] = [
    "dateDemande",
    "dateMiseAuPoint",
    "PVPath",
    "resume",
    "montant",
    "delai"
  ];



  passagesCommissionCompetenteContrat: PassageCommissionCompetente[] = []
  displayedColumnsPassagesCommissionCompetente: string[] = [
    'dateDepotSecretariatCommission',
    'datePassageCommission',
    'decision',
    'dateLeveeReserves',
    'PVPath',
    'dateDecision',
    'numero',
    'decisionPath'
  ]

  miseAuPointChecked: boolean
  passageCommissionContratChecked: boolean 

  constructor(public dialog: MatDialog) { }


  ngOnInit() {
    this.miseAuPointChecked = false
    this.passageCommissionContratChecked = false 
  }


  
  openDialogCreatePassageCommissionCompetenteContrat(): void {
    const dialogRef = this.dialog.open(CreatePassageCommissionCompetenteComponent, {
      width: '800px',
      data: "nouveauPassageCommComp"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    this.passagesCommissionCompetenteContrat.push(PassageCommissionCompetente.getDummy())
    //console.log(this.litiges);
    this.tablePassagesCommissionCompetenteContrat.renderRows();

  }
       

  openDialogCreateMiseAuPoint():void{

    const dialogRef = this.dialog.open(CreateMiseAuPointComponent, {
      width: '800px',
      data: "ajouter une mise ua point"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    })

    this.dataSourceMisesAuPoint.push(MiseAuPoint.getDummy())
    this.tableMisesAuPoint.renderRows()
  }

}
