import { HttpClient, HttpHeaders } from  '@angular/common/http';
import { Injectable } from '@angular/core';
import { fileUploadUrl, fileUploadImageUrl } from 'src/app/config';

interface ApiUploadResult {
	url: string;
}
 
export interface UploadResult {
	name: string;
	type: string;
	size: number;
	url: string;
}

@Injectable({
  providedIn: 'root'
})
export class UploadService {

  public async get(filename: string) {
    const getURL = `${this.SERVER_FILE_URL}/sendMePDF/${filename}`;
    let headers = new HttpHeaders();
    headers = headers.set('Accept', 'application/pdf');

    this.httpClient
    .get(getURL, { headers: headers, responseType: 'blob' })
    .subscribe(res => {
      const fileURL = URL.createObjectURL(res);
      window.URL.revokeObjectURL(fileURL);
    });
    
  }

	SERVER_FILE_URL: string = fileUploadUrl;
	SERVER_IMAGE_URL: string = fileUploadImageUrl;
  constructor(private httpClient: HttpClient) { };


  public async uploadFile( file: File ) : Promise<UploadResult> {
    let uploadURL = `${this.SERVER_FILE_URL}/uploadfile`;
		var result = await this.httpClient
			.post<ApiUploadResult>(
				uploadURL,
				file, // Send the File Blob as the POST body.
				{
					// NOTE: Because we are posting a Blob (File is a specialized Blob
					// object) as the POST body, we have to include the Content-Type
					// header. If we don't, the server will try to parse the body as
					// plain text.
					headers: {
						"Content-Type": file.type
					},
					params: {
						clientFilename: file.name,
						mimeType: file.type
					}
				}
			)
			.toPromise()
		;
 
		return({
			name: file.name,
			type: file.type,
			size: file.size,
			url: result.url
		});
 
	}

  public upload(file: File) {
    let uploadURL = `${this.SERVER_FILE_URL}/uploadfile`;

    let formData = new FormData(); 
    formData.append('file', file, file.name);

    return this.httpClient.post(uploadURL, formData);

  }

	public uploadImage(file: File) {
    let uploadURL = `${this.SERVER_IMAGE_URL}/uploadfile`;

    let formData = new FormData(); 
    formData.append('file', file, file.name);

    return this.httpClient.post(uploadURL, formData);

  }



}
