export class OperateurEconomiquePostRequest {

    denomination: string;
    nif: string;
    pathLogo : string;
    formeJuridique : string;
    estExclu: boolean;


  constructor(
    denomination: string,
    nif: string,
    formeJuridique : string,
    estExclu: boolean,
    pathLogo : string
    ){
      this.denomination = denomination;
      this.nif = nif;
      this.formeJuridique = formeJuridique;
      this.estExclu = estExclu;
      this.pathLogo = pathLogo;
    }

}