import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { Projet } from '../models/repertoire/projet.model';
import { SousProjet } from '../models/repertoire/sous-projet.model';
import { Filter } from '../models/repertoire/filter.model';
import { CreateSousProjetComponent } from '../dialogs/create-sous-projet/create-sous-projet.component';
import { ProjetService } from '../domaine/business-logic/projets.service';
import { CreateProjetComponent } from '../dialogs/create-projet/create-projet.component';
import { DialogAction } from '../enums/dialog-action.enum';
import { UploadService } from '../upload.service';
import { fileUploadUrl } from '../config';


@Component({
  selector: 'app-projets',
  templateUrl: './projets.component.html',
  styleUrls: ['./projets.component.css']
})

export class ProjetsComponent implements OnInit {


  projets: Projet[];
  loadingData = false;

  constructor(
    public dialog: MatDialog, 
    public serviceProjet: ProjetService,
    private uploadService: UploadService
  ) {
    this.projets = [];
  };

  ngOnInit() {
    this.loadingData = true;
    const result:Promise<Projet[]> = this.serviceProjet.getAllProjets();
    result
    .then((value) =>{
      this.loadingData = false;
      return this.projets = value;
    })
    .catch(()=>{
      this.loadingData = false;
      return this.projets = null;
    })
  }



  onFilterApplied(filterData: Filter){
    console.log("Filter Applied", filterData)
  }



  openDialogCreateProjet(): void {
    const dialogRef = this.dialog.open(CreateProjetComponent, 
      {
      width: '800px',
      data: {action: DialogAction.CREATE, prj: null}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      console.log("Return result", result);
      if(this.projets && result) {
        this.projets.push(result);
      }
    });
  }


  openDialogUpdateProjet(projet:Projet): void{
    const dialogRef = this.dialog.open(CreateProjetComponent, 
      {
      width: '800px',
      data: {action: DialogAction.MODIFY, prj: projet}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      console.log("updating frontend with ", result);
      if(this.projets && result) {
        let prj = this.projets.find(e => e._id === result._id);
        console.log("prj to update: ", prj);
        console.log("prj to update sousProjets: ", prj.sousProjets);
        for(var k in result){
          console.log("arg", k);
           prj[k]=result[k];
        }
      }
    });
  };

  openDialogDeleteProjet(projet:Projet): void {
    const dialogRef = this.dialog.open(CreateProjetComponent, 
      {
      width: '800px',
      data: {action: DialogAction.DELETE, prj: projet}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      if(this.projets && result) {
        let sc = this.projets.find(e => e._id === result._id);
        const index = this.projets.indexOf(sc);
        this.projets.splice(index,1)
      }
    });    
  };



  openDialogCreateSousProjet(projet: Projet): void {

    const dialogRef = this.dialog.open(CreateSousProjetComponent, {
      width: '800px',
      data: {action: DialogAction.CREATE, prj: projet, sousProjet: null}
    });

    dialogRef.afterClosed().subscribe(result => {
      if (projet && result) {
        projet.sousProjets.push(result);
      }
    });
  }



  openDialogDeleteSousProjet(projet:Projet, sousProjet: SousProjet): void{
    console.log("prj:", projet);
    console.log("sousPrj", sousProjet);
    const dialogRef = this.dialog.open(CreateSousProjetComponent, {
      width: '800px',
      data: {action: DialogAction.DELETE, prj: projet, sousPrj: sousProjet}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(projet && result) {
        let sousPrj = projet.sousProjets.find(e => e._id === result._id);
        const index = projet.sousProjets.indexOf(sousPrj);
        projet.sousProjets.splice(index,1);
      }
    });    
  };
  openDialogUpdateSousProjet(projet:Projet, sousProjet:SousProjet): void{
    const dialogRef = this.dialog.open(CreateSousProjetComponent, {
      width: '800px',
      data: {action: DialogAction.MODIFY, prj: projet, sousPrj: sousProjet}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(Projet && result) {
        let sousPrj = projet.sousProjets.find(e => e._id === result._id);
        for(var k in result) sousPrj[k]=result[k];
      }
    });  
  };

  downloadJustificatif(filename : string) : void {
    this.uploadService.get(filename);
  }

  getPathFile(filename:string){
    return `${fileUploadUrl}/sendMePDF/${filename}`;
  }
}
