import { Component, OnInit } from '@angular/core';
import { OperateurEconomique } from '../models/operateur.economique.model';
import { MatDialog } from '@angular/material';
import { OperateurEconomiqueService } from '../domaine/business-logic/operateurs-economiques.service';
import { CreateOperateurEconomiqueComponent } from '../dialogs/create-operateur-economique/create-operateur-economique.component';
import { DialogAction } from '../enums/dialog-action.enum';
import { fileUploadUrl, fileUploadImageUrl } from '../config';

@Component({
  selector: 'app-operateurs-economiques',
  templateUrl: './operateurs-economiques.component.html',
  styleUrls: ['./operateurs-economiques.component.css']
})
export class OperateursEconomiquesComponent implements OnInit {




  operateursEconomiques: OperateurEconomique[];
  loadingData = false

  constructor(public dialog: MatDialog, public serviceOperateurEconomique: OperateurEconomiqueService) {
    this.operateursEconomiques = []
  }


  ngOnInit() {
    this.loadingData = true
    const result:Promise<OperateurEconomique[]> = this.serviceOperateurEconomique.getAllOperateursEconomiques();
    result
    .then((value) =>{
      this.loadingData = false
      return this.operateursEconomiques = value
    })
    .catch(()=>{
      this.loadingData = false 
      return this.operateursEconomiques = null
    })
  }


  openDialogCreateOperateurEconomique(){
    const dialogRef = this.dialog.open(CreateOperateurEconomiqueComponent, 
      {
      width: '800px',
      data: {action: DialogAction.CREATE, oper_econ: null}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      console.log("Return result", result);

      if(this.operateursEconomiques && result) {
        this.operateursEconomiques.push(result)
      }

    });
  }

  openDialogUpdateOperateurEconomique(operateurEconomique: OperateurEconomique){
    console.log("operateurEconomique", operateurEconomique)

    const dialogRef = this.dialog.open(CreateOperateurEconomiqueComponent, 
      {
      width: '800px',
      data: {action: DialogAction.MODIFY, oper_econ: operateurEconomique}
      }
    );

    dialogRef.afterClosed().subscribe(result => {

      if(this.operateursEconomiques && result) {
        let operEcon = this.operateursEconomiques.find(e => e._id === result._id);
        for(var k in operEcon) operEcon[k]=result[k];
      }

    });
  }

  openDialogDeleteOperateurEconomique(operateurEconomique: OperateurEconomique){

    const dialogRef = this.dialog.open(CreateOperateurEconomiqueComponent, 
      {
      width: '800px',
      data: {action: DialogAction.DELETE, oper_econ: operateurEconomique}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      console.log("Return result", result);
      
      if(this.operateursEconomiques && result) {
        let operEcon = this.operateursEconomiques.find(e => e._id === result._id);
        const index = this.operateursEconomiques.indexOf(operEcon);
        this.operateursEconomiques.splice(index,1);
      }
      
    });
  }


  openDialogConfirmOperateurEconomique(operateurEconomique: OperateurEconomique){

    const dialogRef = this.dialog.open(CreateOperateurEconomiqueComponent, 
      {
      width: '800px',
      data: {action: DialogAction.CONFIRM, oper_econ: operateurEconomique}
      }
    );

    dialogRef.afterClosed().subscribe(result => {

      if(this.operateursEconomiques && result) {
        let operEcon = this.operateursEconomiques.find(e => e._id === result._id);
        for(var k in operEcon) operEcon[k]=result[k];
      }

    });
  }



  isDefaultLogo(operateurEconomique: OperateurEconomique): boolean {
    if (operateurEconomique.pathLogo == "default-company-logo.png") return true;
    return false;
  }

  getLogoPath(filename:string): string {
    if (filename=="default-company-logo.png") return "assets/img/default-company-logo.png";
    else return `${fileUploadImageUrl}/sendMePNG/${filename}`;
  }

}



