export class ServiceContractantPostRequest {
  
    identifiant: string;
    nomStructure: string;
    email: string;
    telephone: string[];
    fax: string[];
    adresse: string;
    wilaya: string;
    codePostal: string;
    niveau: string;
 

    constructor(
        identifiant: string,
        nomStructure: string,
        email: string,
        telephone: string[],
        fax: string[],
        adresse: string,
        wilaya: string,
        codePostal: string,
        niveau: string
    ){
        this.identifiant=identifiant;
        this.nomStructure=nomStructure;
        this.email=email;
        this.telephone=telephone;
        this.fax=fax;
        this.adresse=adresse;
        this.wilaya=wilaya;
        this.codePostal=codePostal;
        this.niveau = niveau;
 
    }
 
  

}