import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';

import { DialogAction } from 'src/app/enums/dialog-action.enum'; 
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { ServiceContractantService } from 'src/app/domaine/business-logic/services-contractants.service';
import { TypeCOPEO } from 'src/app/enums/type-copeo.enum';
import { COPEOService } from 'src/app/domaine/business-logic/copeos.service';
import { COPEO } from 'src/app/models/copeo/copeo.model';
import { AttributionCOPEOAOPostRequest } from 'src/app/requests/attribution-copeoao-post.request';
import { AttributionCOPEOCPostRequest } from 'src/app/requests/attribution-copeoc-post.request';
import { AttributionCOPEORPostRequest } from 'src/app/requests/attribution-copeor-post.request';
import { AttributionCOPEOUPostRequest } from 'src/app/requests/attribution-copeou-post.request';
import { AttributionCOPEOAOPutRequest } from 'src/app/requests/attribution-copeoao-put.request';
import { AttributionCOPEOCPutRequest } from 'src/app/requests/attribution-copeoc-put.request';
import { AttributionCOPEOUPutRequest } from 'src/app/requests/attribution-copeou-put.request';
import { AttributionCOPEORPutRequest } from 'src/app/requests/attribution-copeor-put.request';

@Component({
  selector: 'app-create-attribution-copeo-service-contractant',
  templateUrl: './create-attribution-copeo-service-contractant.html',
  styleUrls: ['./create-attribution-copeo-service-contractant.css']
})

export class CreateAttributionCOPEOServiceContractant  implements OnInit {
  
    constructor(
      public dialogRef: MatDialogRef<CreateAttributionCOPEOServiceContractant>,
      private copeoService: COPEOService,
      private serviceContractantService: ServiceContractantService,
      @Inject(MAT_DIALOG_DATA) public data: {
        action: DialogAction, 
        sc: ServiceContractant, 
        typ_copeo: TypeCOPEO
      }
    ) {
  
    };
  
    loadingCOPEO: boolean = false;
    optionsCOPEO: COPEO[] = [];
    filteredOptionsCOPEO: Observable<COPEO[]>;
  
  
    attributionCOPEOForm: FormGroup;
  
    isCreate : boolean;
    isModify : boolean;
    isDelete : boolean;


  
    
    ngOnInit() {


      this.isCreate = this.data.action === DialogAction.CREATE;
      this.isModify = this.data.action === DialogAction.MODIFY;
      this.isDelete = this.data.action === DialogAction.DELETE;

 

      this.attributionCOPEOForm = new FormGroup({
        'copeo': new FormGroup({
          '_id' : new FormControl({value: null, disabled: this.isDelete}, [Validators.required],[this.existsCOPEO.bind(this)]),
          'designation' : new FormControl({value: null, disabled: true}, [Validators.required]),
          'numero' : new FormControl({value: null, disabled: true}, [Validators.required])
        })
      });


      if (this.data.action === DialogAction.MODIFY ||
        this.data.action === DialogAction.DELETE ){

          switch(this.data.typ_copeo){
            case TypeCOPEO.CCAO:
              this.attributionCOPEOForm.get("copeo").patchValue({_id: this.data.sc.COPEO_AO._id});
              break;
            case TypeCOPEO.CCC:
              this.attributionCOPEOForm.get("copeo").patchValue({_id: this.data.sc.COPEO_C._id});
              break;
            case TypeCOPEO.CR:
              this.attributionCOPEOForm.get("copeo").patchValue({_id: this.data.sc.COPEO_R._id});
              break;              
            case TypeCOPEO.CU:
              this.attributionCOPEOForm.get("copeo").patchValue({_id: this.data.sc.COPEO_U._id});
              break;              
            default:
              console.log("Check your code!");
          }


      }


      // load les commissions for admin
      this.loadingCOPEO = true;
      const result:Promise<COPEO[]> = this.copeoService.getAllCOPEOSs();


      result
      .then((value) =>{
        this.loadingCOPEO = false;
        this.optionsCOPEO = value; 

        // recheck once the data is loaded
        const copeoForm : AbstractControl =  this.attributionCOPEOForm.get("copeo");
        (<FormGroup>copeoForm).controls["_id"].updateValueAndValidity();

        // generate the filtered data
        this.filteredOptionsCOPEO = this.attributionCOPEOForm.get("copeo").get("_id").valueChanges
        .pipe(
          startWith(''),
          map( valueCOPEO => this._filterCOPEO(valueCOPEO) )
        );

        return;
      })
      .catch(()=>{
        this.loadingCOPEO = false ;
        return this.optionsCOPEO = null;
      });

    };
  
  


    loadingData: boolean = false ;
    errorLoadingData: boolean = false ;
  
    onSubmit() {
      this.loadingData = true ;
      this.errorLoadingData = false ;
      console.log("Submitted", this.attributionCOPEOForm) ;
  
  
  
      
      // save the data into the database
      let result:Promise<COPEO>;
      if (this.isCreate){
        switch(this.data.typ_copeo){
          case TypeCOPEO.CCAO:
            let attributionCOPEOAOPostRequest:AttributionCOPEOAOPostRequest = new AttributionCOPEOAOPostRequest(
              this.attributionCOPEOForm.value.copeo._id
            );
            result = this.serviceContractantService.postAttributionCOPEOAO(this.data.sc._id, attributionCOPEOAOPostRequest);            
            break;          
          
          case TypeCOPEO.CCC:
            let attributionCOPEOCPostRequest: AttributionCOPEOCPostRequest = new AttributionCOPEOCPostRequest(
              this.attributionCOPEOForm.value.copeo._id
            );
            result = this.serviceContractantService.postAttributionCOPEOC(this.data.sc._id, attributionCOPEOCPostRequest);  
            break;  

          case TypeCOPEO.CR:
            let attributionCOPEORPostRequest: AttributionCOPEORPostRequest = new AttributionCOPEORPostRequest(
              this.attributionCOPEOForm.value.copeo._id
            );
            result = this.serviceContractantService.postAttributionCOPEOR(this.data.sc._id, attributionCOPEORPostRequest);  
            break; 

          case TypeCOPEO.CU:
            let attributionCOPEOUPostRequest: AttributionCOPEOUPostRequest = new AttributionCOPEOUPostRequest(
              this.attributionCOPEOForm.value.copeo._id
            );
            result = this.serviceContractantService.postAttributionCOPEOU(this.data.sc._id, attributionCOPEOUPostRequest);  
            break; 
          default:
            console.log("Check your code!");
        }
      };

      if (this.isModify){
        switch(this.data.typ_copeo){
          case TypeCOPEO.CCAO:
            let attributionCOPEOAOPutRequest:AttributionCOPEOAOPutRequest = new AttributionCOPEOAOPutRequest(
              this.attributionCOPEOForm.value.copeo._id
            );
            result = this.serviceContractantService.putAttributionCOPEOAO(this.data.sc._id, attributionCOPEOAOPutRequest);            
            break;          
          
          case TypeCOPEO.CCC:
            let attributionCOPEOCPutRequest: AttributionCOPEOCPutRequest = new AttributionCOPEOCPutRequest(
              this.attributionCOPEOForm.value.copeo._id
            );
            result = this.serviceContractantService.putAttributionCOPEOC(this.data.sc._id, attributionCOPEOCPutRequest);  
            break;  

          case TypeCOPEO.CR:
            let attributionCOPEORPutRequest: AttributionCOPEORPutRequest = new AttributionCOPEORPutRequest(
              this.attributionCOPEOForm.value.copeo._id
            );
            result = this.serviceContractantService.putAttributionCOPEOR(this.data.sc._id, attributionCOPEORPutRequest);  
            break; 

          case TypeCOPEO.CU:
            let attributionCOPEOUPutRequest: AttributionCOPEOUPutRequest = new AttributionCOPEOUPutRequest(
              this.attributionCOPEOForm.value.copeo._id
            );
            result = this.serviceContractantService.putAttributionCOPEOU(this.data.sc._id, attributionCOPEOUPutRequest);  
            break; 
          default:
            console.log("Check your code!");
        }
      };

  
      if (this.isDelete){
        switch(this.data.typ_copeo){
          case TypeCOPEO.CCAO:
            result = this.serviceContractantService.deleteAttributionCOPEOAO(this.data.sc._id);            
            break;          
          
          case TypeCOPEO.CCC:
            result = this.serviceContractantService.deleteAttributionCOPEOC(this.data.sc._id);  
            break;  

          case TypeCOPEO.CR:
            result = this.serviceContractantService.deleteAttributionCOPEOR(this.data.sc._id);  
            break; 

          case TypeCOPEO.CU:
            result = this.serviceContractantService.deleteAttributionCOPEOU(this.data.sc._id);  
            break; 
            
          default:
            console.log("Check your code!");
        }
      };


      result
      .then((value) => {
        console.log(value);
        this.loadingData = false;
        this.dialogRef.close(
          value 
        )
      })
      .catch(()=>{
        this.loadingData = false;
        return this.errorLoadingData = true;
      });
    


  
    }
  
  
  


    existsCOPEO(control: FormControl): Promise<any> | Observable<any> {
      const _id = control.value;
      const result:COPEO = this.getSelectedCOPEO(_id);
      return new Promise<any>(
        (resolve, reject) => {
            if (result != null) {
              resolve(null);
            } else {
              resolve({ "copeoInvalide": true });
            }
          }
      )
    }

    getSelectedCOPEO(_id : string) : COPEO{
      if (_id){
        return this.optionsCOPEO.find(e => e._id === _id);
      } else {
        return null;
      }
    }

    getSelectedCOPEODesignation(): string{
      const result = this.getSelectedCOPEO(this.attributionCOPEOForm.value.copeo._id);
      if (result) {return result.designation;}
      return "";
    };
  
    getSelectedCOPEONumero(): string{
      const result = this.getSelectedCOPEO(this.attributionCOPEOForm.value.copeo._id);
      if (result) {return result.numero;}
      return "";
    };

  
    private _filterCOPEO(valueInput: string): COPEO[] {
      const filterValue = valueInput.toLowerCase();
      return this.optionsCOPEO.filter(
        (cc : COPEO) => {
          let option:string = cc.designation + " " + cc.numero; 
          return option.toLowerCase().includes(filterValue);
        }
      );
    }

 
  }
  