import { Civilite } from 'src/app/enums/civilite.enum';
import { Component, OnInit, Inject } from '@angular/core';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { Observable } from 'rxjs';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { Utilisateur } from 'src/app/models/utilisateur.model';
import { UtilisateurService } from 'src/app/domaine/business-logic/utilisateurs.service';

@Component({
  selector: 'app-create-user',
  templateUrl: './create-user.component.html',
  styleUrls: ['./create-user.component.css']
})
export class CreateUserComponent implements OnInit {

  // hide or show password
  hide = true;

  //Civilites 
  civilites = Object.keys(Civilite).slice(0);
  parseValueCivilite(value: string) {
    return Civilite[value].toString();
  }

  INFO_TEL = "Forme: 023232323 ou +21323232323. Si plusieurs numéros alors séparez-les par des virgules ',' Ex: 023102030,021102030";
  PHONENUMBER_PATTERN: string = "^[+]*[0-9]+(,[+]*[0-9]+)*$"

  constructor(public dialogRef: MatDialogRef<CreateUserComponent>,
    private utilisateurService: UtilisateurService,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, user: Utilisateur}
    ) { }


  utilisateurForm: FormGroup;
  
  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

  ngOnInit() {

    console.log("Dealing with:", this.data.user);

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    this.utilisateurForm = new FormGroup({
      'identifiant': new FormControl({value: null, disabled: this.isDelete}, [Validators.required], this.identifiantExists.bind(this)),
      'civilite': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'nom': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'prenom': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'qualite': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'email': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.email], [this.emailExists.bind(this)]),
      'password': new FormControl({value: null, disabled: this.isDelete}, [], [this.validatePassword.bind(this)] ),
      'telephone': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.pattern(this.PHONENUMBER_PATTERN)]),
      'fax': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.pattern(this.PHONENUMBER_PATTERN)]),
      'portable': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.pattern(this.PHONENUMBER_PATTERN)])
    });

    /*
    this.utilisateurForm.valueChanges.subscribe(
      (value)=>{console.log(value)}
    )


    this.utilisateurForm.statusChanges.subscribe(
      (value) => { console.log(value) }
    )
    */

    console.log("this.data.user1", this.data.user);
    console.log("This form1:", this.utilisateurForm.value);

    // fill the form with the previous data
    if(this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.utilisateurForm.patchValue(this.data.user);
      this.utilisateurForm.patchValue(
        {
          "telephone": this.data.user.telephone.toString(),
          "fax": this.data.user.fax.toString(),
          "portable": this.data.user.portable.toString()
        }
      );
    }
    console.log("this.data.user2", this.data.user);
    console.log("This form:2", this.utilisateurForm.value);
  }

  validatePassword(control: FormControl): Promise<any> | Observable<any> {
    // check password only for the creation
    if (!this.isCreate) 
    return new Promise<any>((resolve, reject) => resolve(null));
    console.log("isCreate", this.isCreate);

    const password = control.value;
    console.log("password", password);
    if (password && password.length >= 8)
    return new Promise<any>((resolve, reject) => resolve(null));

    console.log("check failed");
    return new Promise<any>((resolve, reject) => {resolve({ "8CharsPasswordRequired": true })});

  }



  emailExists(control: FormControl): Promise<any> | Observable<any> {

    const email = control.value;

    // if action is modify od delete, and email not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && email === this.data.user.email)
    return new Promise<any>((resolve, reject) =>  resolve(null));

    const result:Promise<Utilisateur> = this.utilisateurService.getUtilisateurByEmail(email);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(user => {
          console.log("user", user)
          if (user != null) {
            resolve({ "emailExist": true });
          } else {
            resolve(null);
          }
        })
        .catch(error => resolve({ "checking failed": true }));
      }
    )
  }

  identifiantExists(control: FormControl): Promise<any> | Observable<any> {
    const id = control.value;

    // if action is modify or delete, and identifiant not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) 
    && id === this.data.user.identifiant)
    return new Promise<any>((resolve, reject) =>  resolve(null));

    const result:Promise<Utilisateur> = this.utilisateurService.getUtilisateurByIdentifiant(id);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(user => {
          console.log("user", user)
          if (user != null) {
            resolve({ "identifiantExist": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }




  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.utilisateurForm);
    const telephone: string[] = this.utilisateurForm.value.telephone.split(",");
    const fax: string[] = this.utilisateurForm.value.fax.split(",");
    const portable: string[] = this.utilisateurForm.value.portable.split(",");

    this.utilisateur = new Utilisateur(
      this.utilisateurForm.value.identifiant,
      this.utilisateurForm.value.civilite,
      this.utilisateurForm.value.nom,
      this.utilisateurForm.value.prenom,
      this.utilisateurForm.value.qualite,
      this.utilisateurForm.value.email,
      telephone,
      fax,
      portable
    );
    console.log("Value", this.utilisateur);



    // save the data into the database
    let result:Promise<Utilisateur>;
    if (this.isCreate){
      this.utilisateur.password = this.utilisateurForm.value.password;
      result = this.utilisateurService.postUtilisateur(this.utilisateur);
    }

    // update the data in the database
    if(this.isModify){
      result = this.utilisateurService.putUtilisateur(this.utilisateur, this.data.user._id);
    }

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.utilisateurService.deleteUtilisateur(this.data.user._id);
    };
    console.log("Delete", result);

    result
    .then((value) =>{
      this.loadingData = false
      this.dialogRef.close(
        {
          _id: value._id , 
          ...this.utilisateur
        }
      )
    })
    .catch(()=>{
      this.loadingData = false 
      return this.errorLoadingData = true
    })



  }


  //Data model
  utilisateur: Utilisateur;






}

