import { Injectable } from '@angular/core';
import { AdditifCDC } from 'src/app/models/additif-cdc.model';
import { AdditifCDCAccess } from '../data-access/additif-cdc.access';
import { AdditifCDCPostRequest } from 'src/app/requests/additif-cdc-post.request';
import { AdditifCDCPutRequest } from 'src/app/requests/additif-cdc-put.request';

@Injectable()
export class AdditifCDCService {

  constructor(private additifCDCAccess: AdditifCDCAccess){}

  getAdditifCDC(idAdditifCDC: string): Promise<AdditifCDC> {
    return this.additifCDCAccess.getAdditifCDC(idAdditifCDC);
  }

  getAllAdditifsCDC(): Promise<AdditifCDC[]> {
    return this.additifCDCAccess.getAllAdditifsCDC();
  }

  postAdditifCDC(additifCDC: AdditifCDCPostRequest, idElaborationCDC: string): Promise<AdditifCDC> {
    return this.additifCDCAccess.postAdditifCDC(additifCDC, idElaborationCDC);
  }

  putAdditifCDC(additifCDC: AdditifCDCPutRequest, idAdditifCDC: string): Promise<AdditifCDC> {
    return this.additifCDCAccess.putAdditifCDC(additifCDC, idAdditifCDC);
  }

  deleteAdditifCDC(idElaborationCDC: string, idAdditifCDC:string):Promise<AdditifCDC> {
    return this.additifCDCAccess.deleteAdditifCDC(idElaborationCDC, idAdditifCDC);
  }
}



