import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { ModePassation } from 'src/app/enums/mode.passation.enum';
import { TypeProcedure } from 'src/app/enums/type.procedure.enum';
import { FormeMarche } from 'src/app/enums/forme.marche.enum';
import { NaturePrestation } from 'src/app/enums/nature.prestation.enum';
import { SourceFinancement } from 'src/app/enums/source.financement.enum';
import { Filter } from 'src/app/models/repertoire/filter.model';
import { CaractereMarche } from 'src/app/enums/caractere.marche.enum';

@Component({
  selector: 'app-projets-filtre',
  templateUrl: './projets-filtre.component.html',
  styleUrls: ['./projets-filtre.component.css']
})
export class ProjetsFiltreComponent implements OnInit {




  //ModesPassation
  modesPassation = Object.keys(ModePassation).slice(0)
  parseValueModePassation(value: string) {
    return ModePassation[value].toString();
  }

  //TypesProcedure
  typesProcedure = Object.keys(TypeProcedure).slice(0)
  parseValueTypeProcedure(value: string) {
    return TypeProcedure[value].toString();
  }

  //FormesMarches
  formesMarche = Object.keys(FormeMarche).slice(0)
  parseValueFormeMarche(value: string) {
    return FormeMarche[value].toString();
  }

  //caracteresMarches
  caracteresMarche = Object.keys(CaractereMarche).slice(0);
  parseValueCaractereMarche(value: string) {
    return CaractereMarche[value].toString();
  }

  //NaturePrestation
  naturePrestation = Object.keys(NaturePrestation).slice(0)
  parseValueNaturePrestation(value: string) {
    return NaturePrestation[value].toString();
  }

  //SourceFinancement
  sourceFinancement = Object.keys(SourceFinancement).slice(0)
  parseValueSourceFinancement(value: string) {
    return SourceFinancement[value].toString();
  }

  constructor() { }

  ngOnInit() {
  }

  @Output() filterApplied: EventEmitter<Filter> = new EventEmitter<Filter>();

  applyFilter(){
    this.filterApplied.emit(
      Filter.getDummy()
    )
  }
}
