export class Litige {
                introduitPar: string 
                partieAdverse: string //S/C ou P/C (mettre le code?)
                comment: string
                dateDeSaisie: Date
                dateLimite: Date
                dateLitige: Date 
                dateReponse: Date
                litigePath: string //file
                reponsePath: string //file
                datePassageCommission: Date
                decisionCommission: string //4choix irrecevable, dans la forme, fonde, non fonde, 
                PVPath: string //file
                decisionPath: string //file


  constructor(
    introduitPar: string,
    partieAdverse: string,
    comment: string,
    dateDeSaisie: Date,
    dateLimite: Date,
    dateLitige: Date,
    dateReponse: Date,
    litigePath: string,
    reponsePath: string,
    datePassageCommission: Date,
    decisionCommission: string,
    PVPath: string,
    decisionPath: string
      ){
        this.introduitPar = introduitPar,
        this.partieAdverse = partieAdverse,
        this.comment = comment,
        this.dateDeSaisie = dateDeSaisie,
        this.dateLimite = dateLimite,
        this.dateLitige = dateLitige,
        this.dateReponse =dateReponse,
        this.litigePath = litigePath,
        this.reponsePath=reponsePath,
        this.datePassageCommission  = datePassageCommission,
        this.decisionCommission = decisionCommission,
        this.PVPath  =PVPath ,
        this.decisionPath = decisionPath
  }

  static getDummy(): Litige {
    return new Litige(
      "introduitPar",
      "partieAdverse",
      "comment",
      new Date(),
      new Date(),
      new Date(),
      new Date(),
      "litigePath",
      "reponsePath",
      new Date(),
      "decisionCommission",
      "PVPathFile",
      "decisionPathFile"
    )
  }
}