export class DesistementAttribution {
    attributaire: string
    dateDesistement: Date
    raison: string
    justificatifPath: string
    
    
    constructor(
        attributaire: string,
        dateDesistement: Date,
        raison: string,
        justificatifPath: string
    ) {
        this.attributaire= attributaire,
        this.dateDesistement= dateDesistement,
        this.raison= raison,
        this.justificatifPath= justificatifPath
    }
    
    static getDummy(): DesistementAttribution {
        return new DesistementAttribution(
            "attributaire",
            new Date(),
            "raison desistement",
            "justificatifPath"
        )
    }
}