import { Component, OnInit, ViewChildren, QueryList } from '@angular/core';
import { MatDialog, MatTable } from '@angular/material';
import { DialogAction } from '../enums/dialog-action.enum';
import { CreateCOPEOComponent } from '../dialogs/create-copeo/create-copeo.component';
import { COPEO } from '../models/copeo/copeo.model';
import { UploadService } from '../upload.service';
import { COPEOService } from '../domaine/business-logic/copeos.service';
import { fileUploadUrl } from '../config';
import { CreateModificationCOPEOComponent } from '../dialogs/create-modification-copeo/create-modification-copeo.component';
import { ModificationCOPEO } from '../models/copeo/modification-copeo.model';
import { CreateMembreCOPEOComponent } from '../dialogs/create-membre-copeo/create-membre-copeo.component';
import { MembreCOPEO } from '../models/copeo/membre-copeo.model';
import { MembreCOPEOPostRequest } from '../requests/membre-copeo-post.request';

@Component({
  selector: 'app-copeos-component',
  templateUrl: './copeos.component.html',
  styleUrls: ['./copeos.component.css']
})
export class COPEOsComponent implements OnInit {


  @ViewChildren("tableMembresCOPEO") tableMembresCOPEO: QueryList<MatTable<MembreCOPEO>>;
  @ViewChildren("tableMembresModificationCOPEO") tableMembresModificationCOPEO: QueryList<MatTable<MembreCOPEO>>;
  
  copeos: COPEO[];

  loadingData = false;
  displayedColumnsMembres: string[] = ['role', 'nom', 'prenom', 'qualite', 'matricule', 'email', 'telephone', 'penta', 'action'];
  constructor(
    public dialog: MatDialog, 
    public serviceCOPEO: COPEOService,
    private uploadService: UploadService
  ) {
    this.copeos = [];
  };

  ngOnInit() {
    this.loadingData = true;
    const result:Promise<COPEO[]> = this.serviceCOPEO.getAllCOPEOSs();
    result
    .then((value) =>{
      this.loadingData = false;
      return this.copeos = value;
    })
    .catch(()=>{
      this.loadingData = false;
      return this.copeos = null;
    })
  }



  openDialogCreateCOPEO(): void {
    const dialogRef = this.dialog.open(CreateCOPEOComponent, 
      {
      width: '800px',
      data: {action: DialogAction.CREATE, copeo: null}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      console.log("Return result", result);
      if(this.copeos && result) {
        this.copeos.push(result);
      }
    });
  } 

  openDialogModifyCOPEO(copeo:COPEO): void {
    const dialogRef = this.dialog.open(CreateCOPEOComponent, 
      {
      width: '800px',
      data: {action: DialogAction.MODIFY, copeo: copeo}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      if(this.copeos && result) {
        let co = this.copeos.find(e => e._id === result._id);
        for(var k in result){
          co[k]=result[k];
        }
      }
    });
  };

  openDialogDeleteCOPEO(copeo:COPEO): void {
    const dialogRef = this.dialog.open(CreateCOPEOComponent, 
      {
      width: '800px',
      data: {action: DialogAction.DELETE, copeo: copeo}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      if(this.copeos && result) {
        let co = this.copeos.find(e => e._id === result._id);
        const index = this.copeos.indexOf(co);
        this.copeos.splice(index,1)
      }
    });    
  };




  

  openDialogCreateModificationCOPEO(copeo: COPEO): void {

    const dialogRef = this.dialog.open(CreateModificationCOPEOComponent, {
      width: '800px',
      data: {action: DialogAction.CREATE, copeo: copeo, modif_copeo: null}
    });

    dialogRef.afterClosed().subscribe(result => {
      if (copeo && result) {
        copeo.modifications.push(result);
      }
    });
  }



  openDialogDeleteModificationCOPEO(copeo: COPEO, modificationCOPEO: ModificationCOPEO): void{
    const dialogRef = this.dialog.open(CreateModificationCOPEOComponent, {
      width: '800px',
      data: {action: DialogAction.DELETE, copeo: copeo, modif_copeo: modificationCOPEO}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(copeo && result) {
        let modif = copeo.modifications.find(e => e._id === result._id);
        const index = copeo.modifications.indexOf(modif);
        copeo.modifications.splice(index,1);
      }
    });    
  };

  openDialogUpdateModificationCOPEO(copeo:COPEO, modificationCOPEO:ModificationCOPEO): void{
    const dialogRef = this.dialog.open(CreateModificationCOPEOComponent, {
      width: '800px',
      data: {action: DialogAction.MODIFY, copeo: copeo, modif_copeo: modificationCOPEO}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(copeo && result) {
        let modif = copeo.modifications.find(e => e._id === result._id);
        for(var k in result) modif[k]=result[k];
      }
    });  
  };




  
  openDialogCreateMembreCOPEO(copeo: COPEO): void {

    const dialogRef = this.dialog.open(CreateMembreCOPEOComponent, {
      width: '800px',
      data: {
        action: DialogAction.CREATE, 
        mbr: null, 
        copeo: copeo, 
        modif_copeo: null
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (copeo && result) {
        copeo.membres.push(result);
        this.tableMembresCOPEO.forEach( (element) => {
          if (element.dataSource) element.renderRows();
        });
      }
    });

  }


  

  openDialogModifyMembreCOPEO(copeo: COPEO, membre: MembreCOPEO): void {
    const dialogRef = this.dialog.open(CreateMembreCOPEOComponent, {
      width: '800px',
      data:  {
        action: DialogAction.MODIFY, 
        mbr: membre, 
        copeo: copeo, 
        modif_copeo: null
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);

      if (result && membre){

        for(var k in result) membre[k]=result[k];

        this.tableMembresCOPEO.forEach( (element) => {
          if (element.dataSource) element.renderRows();
        });

      }

    });

  }



  
  openDialogDeleteMembreCOPEO(copeo: COPEO, membre: MembreCOPEO): void {
    const dialogRef = this.dialog.open(CreateMembreCOPEOComponent, {
      width: '800px',
      data:  {
        action: DialogAction.DELETE, 
        mbr: membre, 
        copeo: copeo, 
        modif_copeo: null
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);

      if (result && copeo){

        let indx = copeo.membres.findIndex(e => e._id == result._id);
        if (indx>=0) copeo.membres.splice(indx,1);
        
        this.tableMembresCOPEO.forEach( (element) => {
          if (element.dataSource) element.renderRows();
        });

      }


    });

  }












  
  openDialogCreateMembreModificationCOPEO(modification: ModificationCOPEO): void {

    const dialogRef = this.dialog.open(CreateMembreCOPEOComponent, {
      width: '800px',
      data: {
        action: DialogAction.CREATE, 
        mbr: null, 
        copeo: null, 
        modif_copeo: modification
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (modification && result) {
        modification.membres.push(result);
        this.tableMembresModificationCOPEO.forEach( (element) => {
          if (element.dataSource) element.renderRows();
        });
      }
    });

  }


  

  openDialogModifyMembreModificationCOPEO(modification: ModificationCOPEO, membre: MembreCOPEO): void {
    const dialogRef = this.dialog.open(CreateMembreCOPEOComponent, {
      width: '800px',
      data:  {
        action: DialogAction.MODIFY, 
        mbr: membre, 
        copeo: null, 
        modif_copeo: modification
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);

      if (result && membre){

        for(var k in result) membre[k]=result[k];

        this.tableMembresModificationCOPEO.forEach( (element) => {
          if (element.dataSource) element.renderRows();
        });

      }

    });

  }



  
  openDialogDeleteMembreModificationCOPEO(modification: ModificationCOPEO, membre: MembreCOPEO): void {
    const dialogRef = this.dialog.open(CreateMembreCOPEOComponent, {
      width: '800px',
      data:  {
        action: DialogAction.DELETE, 
        mbr: membre, 
        copeo: null, 
        modif_copeo: modification
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);

      if (result && modification){

        let indx = modification.membres.findIndex(e => e._id == result._id);
        if (indx>=0) modification.membres.splice(indx,1);
        
        this.tableMembresModificationCOPEO.forEach( (element) => {
          if (element.dataSource) element.renderRows();
        });

      }


    });

  }











  getPathFile(filename:string){
    return `${fileUploadUrl}/sendMePDF/${filename}`;
  }
}
