export class ComplementOffre {
    soumissionnaire: string
    demandePath: string
    dateNotification: Date
    moyenNotification: string
    isAnswered: boolean // if true, the three below fields are used
    reponse: string //positive ou negative, ou aucun.
    reponsePath: string 
    dateReponse: Date 

    constructor (
        soumissionnaire: string,
        demandePath:string,
        dateNotification: Date,
        moyenNotification: string,
        isAnswered: boolean,
        reponse: string,
        reponsePath: string ,
        dateReponse: Date 
    ){
        this.soumissionnaire = soumissionnaire,
        this.demandePath = demandePath,
        this.dateNotification = dateNotification,
        this.moyenNotification = moyenNotification,
        this.isAnswered = isAnswered,
        this.reponse = reponse,
        this.reponsePath = reponsePath ,
        this.dateReponse = dateReponse 
    }
    static getDummy(): ComplementOffre {
        const rand = Math.random()
        if (rand < 0.5) 
            return new ComplementOffre(
                "soumissionnaire",
                "demandePath",
                new Date(),
                "moyenNotification",
                false,
                null,
                null ,
                null
            )
        else
            return new ComplementOffre(
                "soumissionnaire",
                "demandePath",
                new Date(),
                "moyenNotification",
                true,
                "reponse",
                "reponsePath" ,
                new Date() 
            )
      }
}