import { Civilite } from '../enums/civilite.enum'

export class Utilisateur {
    public _id?: string;
    public password? : string;
    public identifiant: string;
    public civilite: Civilite;
    public nom: string;
    public prenom: string;
    public email: string;
    public qualite: string;
    public portable: string[];
    public telephone: string[];
    public fax: string[];

    constructor(
        identifiant: string,
        civilite: Civilite,
        nom: string,
        prenom: string,
        qualite: string,
        email: string,
        telephone: string[],
        fax: string[],
        portable: string[]
    ){
        this.identifiant = identifiant;
        this.civilite = civilite;
        this.nom = nom;
        this.prenom= prenom;
        this.email=email;
        this.qualite=qualite;
        this.portable=portable;
        this.telephone=telephone;
        this.fax= fax ;
    }
  }