import { Component, ViewChild, Input } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { Soumissionnaire } from 'src/app/models/soumissionnaire.model';
import { CreateSoumissionnaireComponent } from 'src/app/dialogs/create-soumissionnaire/create-soumissionnaire.component';
import { Prolongation } from 'src/app/models/prolongation.model';
import { CreateProlongationComponent } from 'src/app/dialogs/create-prolongation/create-prolongation.component';
import { FormControl } from '@angular/forms';
import { Lancement } from 'src/app/models/lancement/lancement.model';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { fileUploadUrl } from 'src/app/config';
import { CreateDepotOffresComponent } from 'src/app/dialogs/create-depot-offres/create-depot-offres.component';
import { DialogAction } from 'src/app/enums/dialog-action.enum';


@Component({
  selector: 'app-depot-offres',
  templateUrl: './depot_offres.component.html',
  styleUrls: ['./depot_offres.component.css']
})

export class DepotOffresComponent {

  @Input() lancement : Lancement;
  @Input() elaborationCDC : ElaborationCDC;
  @Input() sousProjet : SousProjet;
  @Input() projet : Projet;
  

  //@ViewChild(MatTable, null) tableLitiges: MatTable<any>;  //fix this bug What does null and any?
  @ViewChild("tableSoumissionnaires", {static: true}) tableSoumissionnaires: MatTable<Soumissionnaire>; 
 
   
  constructor(public dialog: MatDialog) { }


  ngOnInit() {
 
  }
  
 


  

      
  openDialogCreateDepotOffres() {
    const dialogRef = this.dialog.open(CreateDepotOffresComponent, {
        width: '800px',
      data:  {
        action: DialogAction.CREATE, 
        dpt_off: null,
        id_lncm: this.lancement._id
      }
    });

    dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        console.log(result);
        if (result && this.lancement){
            this.lancement.depotOffres=result;
        }

    });

}


openDialogModifyDepotOffres(): void {
    const dialogRef = this.dialog.open(CreateDepotOffresComponent, {
      width: '800px',
      data:  {
        action: DialogAction.MODIFY, 
        dpt_off: this.lancement.depotOffres,
        id_lncm: this.lancement._id
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);

      if (result && this.lancement ){
        this.lancement.depotOffres=result;
      }
      
    });

  }


  openDialogDeleteDepotOffres(): void {
    const dialogRef = this.dialog.open(CreateDepotOffresComponent, {
      width: '800px',
      data:  {
        action: DialogAction.DELETE, 
        dpt_off: this.lancement.depotOffres,
        id_lncm: this.lancement._id 
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      if (result && this.lancement ){
        this.lancement.depotOffres=null;
      }

    });

  }























  dataSourceSoumissionnaires: Soumissionnaire [] = []
  displayedColumnsSoumissionnaires: string[] = [
    'position',
    'denominationEntreprise',
    'dateDepot',
    'heureDepot'
    ];
  openDialogCreateSoumissionnaire(){
    const dialogRef = this.dialog.open(CreateSoumissionnaireComponent, {
      width: '800px',
      data: "nouveau Soumissionnaire"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    this.dataSourceSoumissionnaires.push(Soumissionnaire.getDummy())
    //console.log(this.recours);
    this.tableSoumissionnaires.renderRows();    
  }

    getPathFile(filename:string){
      return `${fileUploadUrl}/sendMePDF/${filename}`;
    }
    
}
