import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Utilisateur } from 'src/app/models/utilisateur.model';


@Injectable()
export class UtilisateurAccess {

  constructor(private http: HttpClient){}


  async getAllUtilisateurs(): Promise<Utilisateur[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Utilisateur[]>(
          `${apiEndpoint}/utilisateurs`,
          )
        .subscribe(
          utilisateurs => { resolve(utilisateurs) },
          error => reject(error)
        )

      });
  }



  async getUtilisateurByIdentifiant(id: String): Promise<Utilisateur>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Utilisateur[]>(
          `${apiEndpoint}/utilisateurs/identifiants/${id}`,
        )
        .subscribe(
          Utilisateur => {
            if(Utilisateur.length > 0)
              resolve(Utilisateur[0])
            else 
              resolve(null)
          },
          error => reject(error)
        )

      });

  }

  async getUtilisateurByEmail(email: String): Promise<Utilisateur>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Utilisateur[]>(
          `${apiEndpoint}/utilisateurs/emails/${email}`,
        )
        .subscribe(
          utilisateur => {
            if(utilisateur.length > 0)
              resolve(utilisateur[0])
            else 
              resolve(null)
          },
          error => reject(error)
        )

      });

  }


  async postUtilisateur(utilisateur: Utilisateur): Promise<Utilisateur> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<Utilisateur>(
          `${apiEndpoint}/utilisateurs`, 
          utilisateur, 
          httpOptions
          )
        .subscribe(
            utilisateur => { resolve(utilisateur) },
            error => reject(error)
        )
      }
    );
  }


  async putUtilisateur(utilisateur: Utilisateur, id: String): Promise<Utilisateur> {
    console.log("The id:", id)
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<Utilisateur>(
          `${apiEndpoint}/utilisateurs/${id}`, 
          utilisateur, 
          httpOptions
          )
        .subscribe(
            utilisateur_result => { resolve(utilisateur_result) },
            error => reject(error)
        )
      }
    );
  }
  

  async deleteUtilisateur(id: String): Promise<Utilisateur> {
    console.log("The id:", id)

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<Utilisateur>(
          `${apiEndpoint}/utilisateurs/${id}`
          )
        .subscribe(
            utilisateur => { resolve(utilisateur) },
            error => reject(error)
        )
      }
    );
  }
  
} 