import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetService } from 'src/app/domaine/business-logic/projets.service';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { UploadService } from 'src/app/upload.service';
import { ServiceContractantService } from 'src/app/domaine/business-logic/services-contractants.service';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { Langue } from 'src/app/enums/langue.enum';
import { OrganePresse } from 'src/app/models/organe.presse.model';
import { OrganePressePostRequest } from 'src/app/requests/organe-presse-post.request';
import { OrganePressePutRequest } from 'src/app/requests/organe-presse-put.request';
import { OrganePresseService } from 'src/app/domaine/business-logic/organes-presse.service';
import { Prolongation } from 'src/app/models/prolongation.model';


@Component({
  selector: 'app-create-organe-presse',
  templateUrl: './create-organe-presse.component.html',
  styleUrls: ['./create-organe-presse.component.css']
})

export class CreateOrganePresseComponent implements OnInit {





  constructor(
    public dialogRef: MatDialogRef<CreateOrganePresseComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, 
      orgn_prss: OrganePresse
      id_lcm_cnrr: string
      is_prlng : boolean
      prlng : Prolongation
    },
    private uploadService: UploadService,
    public organePresseService: OrganePresseService
  ) {



  };




  organePresseForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;



  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.orgn_prss.dateCreationOrganePresse);
    }




    this.organePresseForm = new FormGroup({
      'dateCreationOrganePresse': new FormControl({value: null, disabled: true}, [Validators.required]),
      'dateParution':new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'denomination': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'langue': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'pathOrganePresse': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.organePresseForm.patchValue(this.data.orgn_prss);
    }



  };




  //Langue
  langues = Object.keys(Langue).slice(0);
  parseValueLangue(value : string) {
    return Langue[value].toString();
  }










  //Data model to submit to the DB server
  organePressePostRequest: OrganePressePostRequest;
  organePressePutRequest: OrganePressePutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.organePresseForm);




    // save the data into the database
    let result:Promise<OrganePresse>;
    if (this.isCreate){
      this.organePressePostRequest = new OrganePressePostRequest(
        this.organePresseForm.value.dateParution,
        this.organePresseForm.value.denomination,
        this.organePresseForm.value.langue,
        this.organePresseForm.value.pathOrganePresse
      );
      if (this.data.is_prlng)
        result = this.organePresseService.postOrganePresseProlongation(this.data.prlng._id, this.organePressePostRequest);
      else 
        result = this.organePresseService.postOrganePresse(this.data.id_lcm_cnrr, this.organePressePostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.organePressePutRequest = new OrganePressePutRequest(
        this.organePresseForm.value.dateParution,
        this.organePresseForm.value.denomination,
        this.organePresseForm.value.langue,
        this.organePresseForm.value.pathOrganePresse
      );      
      result = this.organePresseService.putOrganePresse(this.data.orgn_prss._id, this.organePressePutRequest);
    };

    // delete le S/C data from the database
    if(this.isDelete){

      if (this.data.is_prlng)
        result = this.organePresseService.deleteOrganePresseProlongation(this.data.prlng._id, this.data.orgn_prss._id);
      else
        result = this.organePresseService.deleteOrganePresse(this.data.id_lcm_cnrr, this.data.orgn_prss._id);
    };

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value/*
        {
          _id: value._id , 
          serviceContractant: {
            _id: value.serviceContractant._id,
            identifiant: value.serviceContractant.identifiant,
            nomStructure: value.serviceContractant.nomStructure
          },
          intituleProjet : value.intituleProjet,
          codeProjet: value.codeProjet,
          montantProjet: value.montantProjet,
          dateCreationProjet: value.dateCreationProjet,
          yearPlanAction: value.yearPlanAction,
          pourLaStructureRattachement: value.pourLaStructureRattachement,
          sourcesFinancement: value.sourcesFinancement,
          siAutreSourceFinancement: value.siAutreSourceFinancement,
          budget: value.budget,
          isHorsPlan: value.isHorsPlan,
          pathJustificatifProjet: value.pathJustificatifProjet,
          sousProjets : value.sousProjets
        }
        */
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.organePresseForm.patchValue({'pathOrganePresse' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}




