import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { SourceFinancement } from 'src/app/enums/source.financement.enum';
import { Budget } from 'src/app/enums/budget.enum';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetService } from 'src/app/domaine/business-logic/projets.service';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { UploadService } from 'src/app/upload.service';
import { ServiceContractantService } from 'src/app/domaine/business-logic/services-contractants.service';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { OperateurEconomiqueService } from 'src/app/domaine/business-logic/operateurs-economiques.service';
import { DemandeEclaircissement } from 'src/app/models/demande.eclaircissement.model';
import { DemandeEclaircissementPostRequest } from 'src/app/requests/demande-eclaircissement-post.request';
import { DemandeEclaircissementPutRequest } from 'src/app/requests/demande-eclaircissement-put.request';
import { DemandeEclaircissementService } from 'src/app/domaine/business-logic/demandes-eclaircissement.service';
import { DemandeEclaircissementAccess } from 'src/app/domaine/data-access/demande-eclaircissement.access';
 


@Component({
  selector: 'app-create-demande-eclaircissement',
  templateUrl: './create-demande-eclaircissement.component.html',
  styleUrls: ['./create-demande-eclaircissement.component.css']
})
export class CreateDemandeEclaircissementComponent implements OnInit {
 
  constructor(
    public dialogRef: MatDialogRef<CreateDemandeEclaircissementComponent>,
    private projetService: ProjetService,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      dmd: DemandeEclaircissement,
      id_rtr_cdc: string
    },
    private uploadService: UploadService,
    public serviceContractantService: ServiceContractantService,
    private operateurEconomiqueService: OperateurEconomiqueService,
    private demandeEclaircissementService: DemandeEclaircissementService
  ) {
 
  };




  demandeEclaircissementForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

  loadingOE: boolean =false;
  //all the SC list
  optionsOE: OperateurEconomique[] = [];
  //the filtered SC list to show to the user
  filteredOptionsOE: Observable<OperateurEconomique[]>;

  private _filterOE(valueInput: string): OperateurEconomique[] {
    const filterValue = valueInput.toLowerCase();
    return this.optionsOE.filter(
      (oe : OperateurEconomique) => {
        let option:string = oe.denomination + " " + oe.formeJuridique; 
        return option.toLowerCase().includes(filterValue);
      }
    );
  }

  getSelectedOE(_id : string) : OperateurEconomique{
    if (_id){
      return this.optionsOE.find(e => e._id === _id);
    } else {
      return null;
    }
  }

  getSelectedOEDenomination(): string{
    const result = this.getSelectedOE(this.demandeEclaircissementForm.value.operateurEconomique._id);
    if (result) {return result.denomination;}
    return "";
  };

  getSelectedOEFormeJuridique(): string{
    const result = this.getSelectedOE(this.demandeEclaircissementForm.value.operateurEconomique._id);
    if (result) {return result.formeJuridique;}
    return "";
  };

  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.dmd.dateCreation);
    }




    this.demandeEclaircissementForm = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
      'dateDemande': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'numero': new FormControl({value: null, disabled: this.isDelete}, [Validators.required],[this.numeroExists.bind(this)]),      
      'dateNotificationSC': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'pathDemande': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'operateurEconomique': new FormGroup({
        '_id' : new FormControl({value: null, disabled: this.isDelete}, [Validators.required],[this.existsOE.bind(this), this.excludedOE.bind(this)]),
        'denomination' : new FormControl({value: null, disabled: true}, [Validators.required]),
        'formeJuridique' : new FormControl({value: null, disabled: true}, [Validators.required])
      })
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.demandeEclaircissementForm.patchValue(this.data.dmd);
    }


      // load les operateurs 
      this.loadingOE = true;
      const result:Promise<OperateurEconomique[]> = this.operateurEconomiqueService.getAllOperateursEconomiquesWithRepresentants();
      result
      .then((value) =>{
        this.loadingOE = false;
        this.optionsOE = value;
  
        // recheck once the data is loaded
        const operateurEconomiqueForm : AbstractControl =  this.demandeEclaircissementForm.get("operateurEconomique");
        (<FormGroup>operateurEconomiqueForm).controls["_id"].updateValueAndValidity();
        
  
        // generate the filtered data
        this.filteredOptionsOE = this.demandeEclaircissementForm.get("operateurEconomique").get("_id").valueChanges
        .pipe(
          startWith(''),
          map( valueOE => this._filterOE(valueOE) )
        );
  
        return;
      })
      .catch(()=>{
        this.loadingOE = false ;
        return this.optionsOE = null;
      });

  };




  existsOE(control: FormControl): Promise<any> | Observable<any> {
    const operateurEconomiqueId = control.value;
    // if action is modify od delete, and field not changed, ignore it

    
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && 
    operateurEconomiqueId === this.data.dmd.operateurEconomique._id)
    return new Promise<any>((resolve, reject) =>  resolve(null));
    

    const result:OperateurEconomique = this.getSelectedOE(operateurEconomiqueId);
    return new Promise<any>(
      (resolve, reject) => {
          if (result != null) {
            resolve(null);
          } else {
            resolve({ "operateurEconomiqueInvalid": true });
          }
        }
    )
  }


  excludedOE(control: FormControl): Promise<any> | Observable<any> {
    const operateurEconomiqueId = control.value;
    
    // if action is modify od delete, and field not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && 
    operateurEconomiqueId === this.data.dmd.operateurEconomique._id)
    return new Promise<any>((resolve, reject) =>  resolve(null));
    

    const result:OperateurEconomique = this.getSelectedOE(operateurEconomiqueId);
    return new Promise<any>(
      (resolve, reject) => {
          if (result && result.estExclu) {
            resolve({ "operateurEconomiqueExcluded": true });
          } else {
            resolve(null);
          }
        }
    )
  }


  


  numeroExists(control: FormControl): Promise<any> | Observable<any> {

    const numero = control.value;

    // if action is modify od delete, and email not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && 
    numero === this.data.dmd.numero)
    return new Promise<any>((resolve, reject) =>  resolve(null));


    const result:Promise<DemandeEclaircissement> = this.demandeEclaircissementService.getDemandeEclaircissementByNumero(this.data.id_rtr_cdc, numero);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(rpr => {
          if (rpr != null) {
            resolve({ "numeroExist": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }





  //Data model to submit to the DB server
  demandeEclaircissementPostRequest: DemandeEclaircissementPostRequest;
  demandeEclaircissementPutRequest: DemandeEclaircissementPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.demandeEclaircissementForm);




    // save the data into the database
    let result:Promise<DemandeEclaircissement>;
    if (this.isCreate){
      this.demandeEclaircissementPostRequest = new DemandeEclaircissementPostRequest(
        this.demandeEclaircissementForm.value.dateDemande,
        this.demandeEclaircissementForm.value.operateurEconomique._id,
        this.demandeEclaircissementForm.value.dateNotificationSC,
        this.demandeEclaircissementForm.value.numero,
        this.demandeEclaircissementForm.value.pathDemande
      ); 
      result = this.demandeEclaircissementService.postDemandeEclaircissement(this.data.id_rtr_cdc,this.demandeEclaircissementPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.demandeEclaircissementPutRequest = new DemandeEclaircissementPutRequest(
      this.demandeEclaircissementForm.value.dateDemande,
      this.demandeEclaircissementForm.value.operateurEconomique._id,
      this.demandeEclaircissementForm.value.dateNotificationSC,
      this.demandeEclaircissementForm.value.numero,
      this.demandeEclaircissementForm.value.pathDemande
    ); 
    result = this.demandeEclaircissementService.putDemandeEclaircissement(this.data.id_rtr_cdc,this.data.dmd._id, this.demandeEclaircissementPutRequest);
  };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.demandeEclaircissementService.deleteDemandeEclaircissement(this.data.id_rtr_cdc, this.data.dmd._id);
    };
    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.demandeEclaircissementForm.patchValue({'pathDemande' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}
