export class CanalAffichagePostRequest {
  dateParution: Date;
  denomination: string;
  type: string;
  pathJustificatif: string;

  constructor(
    dateParution: Date,
    denomination: string,
    type: string,
    pathJustificatif: string
    ){

      this.dateParution = dateParution;
      this.denomination = denomination;
      this.type = type;
      this.pathJustificatif = pathJustificatif;

    }

}