export class SeanceCommissionCompetentePostRequest {
  public dateProgrammationSeance: string;
  public rapporteur: string;
  public passagesCommissionMarches: string[];
  public commissionCompetente: string;


  constructor(
    dateProgrammationSeance: string,
    rapporteur: string,
    passagesCommissionMarches: string[],
    commissionCompetente: string
    ){
      this.dateProgrammationSeance = dateProgrammationSeance;
      this.rapporteur = rapporteur;
      this.passagesCommissionMarches = passagesCommissionMarches;
      this.commissionCompetente  = commissionCompetente;
    }

}