import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { MembreCOPEO } from 'src/app/models/copeo/membre-copeo.model';
import { COPEO } from 'src/app/models/copeo/copeo.model';
import { ModificationCOPEO } from 'src/app/models/copeo/modification-copeo.model';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { RoleMembreCOPEO } from 'src/app/enums/role-membre-copeo.enum';
import { MembreCOPEOPostRequest } from 'src/app/requests/membre-copeo-post.request';
import { MembreCOPEOPutRequest } from 'src/app/requests/membre-copeo-put.request';
import { COPEOService } from 'src/app/domaine/business-logic/copeos.service';


@Component({
  selector: 'app-create-membre-copeo',
  templateUrl: './create-membre-copeo.component.html',
  styleUrls: ['./create-membre-copeo.component.css']
})
export class CreateMembreCOPEOComponent implements OnInit {
  
    constructor(
      public dialogRef: MatDialogRef<CreateMembreCOPEOComponent>,
      private copeoService: COPEOService,
      @Inject(MAT_DIALOG_DATA) public data: {
        action: DialogAction, 
        mbr: MembreCOPEO,
        copeo: COPEO, 
        modif_copeo: ModificationCOPEO
      } 
    ) {
  
    };
  
  
  
  
    membreCOPEOForm: FormGroup;
  
    isCreate : boolean;
    isModify: boolean;
    isDelete: boolean;
  
  
    // date creation to show (if create: today, if delete of modify: date creation )
    date;
    
    ngOnInit() {


      this.isCreate = this.data.action === DialogAction.CREATE;
      this.isModify = this.data.action === DialogAction.MODIFY;
      this.isDelete = this.data.action === DialogAction.DELETE;
  
      //set the date of creation
      if (this.isCreate){
        this.date = new FormControl(new Date());
      }else{
          this.date = new FormControl(this.data.mbr.dateCreation);
      }

  
      this.membreCOPEOForm = new FormGroup({
        'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
        'role': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'nom': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'prenom': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'qualite': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'matricule': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'email': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.email]),
        'telephone': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'penta': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
      });

  
      if (this.data.action === DialogAction.MODIFY ||
        this.data.action === DialogAction.DELETE ){
        this.membreCOPEOForm.patchValue(this.data.mbr);
      }
  



    };

  
  
    //Roles des membres
    rolesMembresCOPEO = Object.keys(RoleMembreCOPEO).slice(0);
    parseValueRoleMembreCOPEO(value : string) {
      return RoleMembreCOPEO[value].toString();
    }
  
 



    //Data model to submit to the DB server
    membreCOPEOPostRequest: MembreCOPEOPostRequest;
    membreCOPEOPutRequest: MembreCOPEOPutRequest;
  
    loadingData: boolean= false;
    errorLoadingData: boolean = false;
  
    onSubmit() {
      this.loadingData = true;
      this.errorLoadingData = false;
      console.log("Submitted", this.membreCOPEOForm);
  


      // save the data into the database
      let result:Promise<MembreCOPEO>;
      if (this.isCreate){
        this.membreCOPEOPostRequest = new MembreCOPEOPostRequest(
          this.membreCOPEOForm.value.role,
          this.membreCOPEOForm.value.nom,
          this.membreCOPEOForm.value.prenom,
          this.membreCOPEOForm.value.qualite,
          this.membreCOPEOForm.value.matricule,
          this.membreCOPEOForm.value.email,
          this.membreCOPEOForm.value.telephone,
          this.membreCOPEOForm.value.penta
        );      
        if(this.data.copeo)
          result = this.copeoService.postMembreCOPEO(this.data.copeo._id, this.membreCOPEOPostRequest);

        if(this.data.modif_copeo)
          result = this.copeoService.postMembreModificationCOPEO(this.data.modif_copeo._id, this.membreCOPEOPostRequest);

      };
  
      // update the data in the database
      if(this.isModify){
        this.membreCOPEOPutRequest = new MembreCOPEOPutRequest(
          this.membreCOPEOForm.value.role,
          this.membreCOPEOForm.value.nom,
          this.membreCOPEOForm.value.prenom,
          this.membreCOPEOForm.value.qualite,
          this.membreCOPEOForm.value.matricule,
          this.membreCOPEOForm.value.email,
          this.membreCOPEOForm.value.telephone,
          this.membreCOPEOForm.value.penta
        );      

        if(this.data.copeo)
          result = this.copeoService.putMembreCOPEO(this.data.mbr._id, this.membreCOPEOPutRequest);
        if(this.data.modif_copeo)         
          result = this.copeoService.putMembreModificationCOPEO(this.data.mbr._id, this.membreCOPEOPutRequest);

      };
  
      // delete le S/C data from the database
      if(this.isDelete){

        if(this.data.copeo)
          result = this.copeoService.deleteMembreCOPEO(this.data.copeo._id, this.data.mbr._id);
        if(this.data.modif_copeo)            
          result = this.copeoService.deleteMembreModificationCOPEO(this.data.modif_copeo._id, this.data.mbr._id);  

      };
      console.log("Result", result);
  
      result
      .then((value) => {
        this.loadingData = false;
        this.dialogRef.close(
          value
        )
      })
      .catch(()=>{
        this.loadingData = false;
        return this.errorLoadingData = true;
      });
  
  
  
    }
   
  
  
    
  }
  