import { MembreCommissionCompetente } from '../models/commission-competente/membre-commission-competente.model';

export class CommissionCompetentePutRequest {
  numero: string;
  designation: string;
  typeCommission: string;
  membresCommissionCompetente : MembreCommissionCompetente[];
  pathJustificatifDecision: string;

  constructor(
    numero: string,
    designation: string,
    typeCommission: string,
    membresCommissionCompetente : MembreCommissionCompetente[],
    pathJustificatifDecision: string
    ){
      this.numero = numero;
      this.designation = designation;
      this.typeCommission = typeCommission;
      this.membresCommissionCompetente = membresCommissionCompetente;
      this.pathJustificatifDecision = pathJustificatifDecision;
    }

}