export class Recours {
                soumissionnaire: string 
                objetRecours: string
                type: string //AO: Appel d'offre ou C: consultation
                dateDeSaisie: Date
                dateLimite: Date
                dateRecours: Date 
                dateReponse: Date
                recoursPath: string //file
                reponsePath: string //file
                datePassageCommission: Date
                decisionCommission: string //4choix irrecevable, dans la forme, fonde, non fonde, 
                PVPath: string //file
                decisionPath: string //file


  constructor(
    soumissionnaire: string,
    objetRecours: string,
    type: string,
    dateDeSaisie: Date,
    dateLimite: Date,
    dateRecours: Date,
    dateReponse: Date,
    recoursPath: string,
    reponsePath: string,
    datePassageCommission: Date,
    decisionCommission: string,
    PVPath: string,
    decisionPath: string
      ){
        this.soumissionnaire = soumissionnaire,
        this.objetRecours = objetRecours,
        this.type = type,
        this.dateDeSaisie = dateDeSaisie,
        this.dateLimite = dateLimite,
        this.dateRecours = dateRecours,
        this.dateReponse =dateReponse,
        this.recoursPath = recoursPath,
        this.reponsePath=reponsePath,
        this.datePassageCommission  = datePassageCommission,
        this.decisionCommission = decisionCommission,
        this.PVPath  =PVPath ,
        this.decisionPath = decisionPath
  }

  static getDummy(): Recours {
    return new Recours(
      "Soum. 125",
      "declara. infrut.",
      "Appel d'Offre",
      new Date(),
      new Date(),
      new Date(),
      new Date(),
      "recoursPath",
      "reponsePath",
      new Date(),
      "decisionCommission",
      "PVPathFile",
      "decisionPathFile"
    )
  }
}