import { Injectable } from '@angular/core';
import { SitewebAccess } from '../data-access/siteweb.access';
import { SitewebPostRequest } from 'src/app/requests/siteweb-post.request';
import { Siteweb } from 'src/app/models/siteweb.model';
import { SitewebPutRequest } from 'src/app/requests/siteweb-put.request';

@Injectable()
export class SitewebService {

  constructor(private sitewebAccess: SitewebAccess){}


  postSitewebProlongation(idProlongation: string, siteweb: SitewebPostRequest): Promise<Siteweb> {
    return this.sitewebAccess.postSitewebProlongation(idProlongation, siteweb);
  }

  postSiteweb(idLancementConcurrence: string, siteweb: SitewebPostRequest): Promise<Siteweb> {
    return this.sitewebAccess.postSiteweb(idLancementConcurrence, siteweb);
  }

  putSiteweb(idSiteweb: string, sitewebPutRequest: SitewebPutRequest): Promise<Siteweb> {
    return this.sitewebAccess.putSiteweb(idSiteweb, sitewebPutRequest);
  }

  deleteSitewebProlongation(idProlongation: String, idSiteweb: string):Promise<Siteweb> {
    return this.sitewebAccess.deleteSitewebProlongation(idProlongation, idSiteweb);
  }

  deleteSiteweb(idLancementConcurrence: String, idSiteweb: string):Promise<Siteweb> {
    return this.sitewebAccess.deleteSiteweb(idLancementConcurrence, idSiteweb);
  }
}



