import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { UploadService } from 'src/app/upload.service';
import { DepotOffres } from 'src/app/models/lancement/depot-offres.model';
import { DepotOffresService } from 'src/app/domaine/business-logic/depots-offres.service';
import { DepotOffresPostRequest } from 'src/app/requests/depot-offres-post.request';
import { DepotOffresPutRequest } from 'src/app/requests/depot-offres-put.request';
 


@Component({
  selector: 'app-create-depot-offres',
  templateUrl: './create-depot-offres.component.html',
  styleUrls: ['./create-depot-offres.component.css']
})

export class CreateDepotOffresComponent implements OnInit {
 
  constructor(
    public dialogRef: MatDialogRef<CreateDepotOffresComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      dpt_off: DepotOffres,
      id_lncm: string
    },
    private uploadService: UploadService,
    private depotOffresService: DepotOffresService
  ) {
 
  };




  depotOffresForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

   
  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.dpt_off.dateCreation);
    }




    this.depotOffresForm = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
      'nombreSoumissionaires': new FormControl({value: 0, disabled: this.isDelete}, [Validators.required, Validators.min(0)]),
      'pathListeDepots': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.depotOffresForm.patchValue(this.data.dpt_off);
    }
 
  };

 

  //Data model to submit to the DB server
  depotOffresPostRequest: DepotOffresPostRequest;
  depotOffresPutRequest: DepotOffresPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.depotOffresForm);




    // save the data into the database
    let result:Promise<DepotOffres>;
    if (this.isCreate){
      this.depotOffresPostRequest = new DepotOffresPostRequest(
        this.depotOffresForm.value.nombreSoumissionaires,
        this.depotOffresForm.value.pathListeDepots
      ); 
      result = this.depotOffresService.postDepotOffres(this.data.id_lncm, this.depotOffresPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.depotOffresPutRequest = new DepotOffresPutRequest(
        this.depotOffresForm.value.nombreSoumissionaires,
        this.depotOffresForm.value.pathListeDepots
    ); 
    result = this.depotOffresService.putDepotOffres(this.data.dpt_off._id, this.depotOffresPutRequest);
  };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.depotOffresService.deleteDepotOffres(this.data.id_lncm, this.data.dpt_off._id);
    };
    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.depotOffresForm.patchValue({'pathListeDepots' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}
