import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable} from '@angular/core';
import { Siteweb } from 'src/app/models/siteweb.model';
import { SitewebPostRequest } from 'src/app/requests/siteweb-post.request';
import { SitewebPutRequest } from 'src/app/requests/siteweb-put.request';


@Injectable()
export class SitewebAccess {

  constructor(private http: HttpClient){}

  async postSitewebProlongation (idProlongation: string, siteweb: SitewebPostRequest): Promise<Siteweb> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<Siteweb>(
          `${apiEndpoint}/sitewebs/prolongations/${idProlongation}`, 
          siteweb, 
          httpOptions
          )
        .subscribe(
            siteweb => { resolve(siteweb) },
            error => reject(error)
        )
      }
    );
  };


  async postSiteweb(idLancementConcurrence: string, siteweb: SitewebPostRequest): Promise<Siteweb> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<Siteweb>(
          `${apiEndpoint}/sitewebs/${idLancementConcurrence}`, 
          siteweb, 
          httpOptions
          )
        .subscribe(
            siteweb => { resolve(siteweb) },
            error => reject(error)
        )
      }
    );
  };


  async putSiteweb(idSiteweb: string, sitewebPutRequest: SitewebPutRequest): Promise<Siteweb> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the put query
        this.http.put<Siteweb>(
          `${apiEndpoint}/sitewebs/${idSiteweb}`, 
          sitewebPutRequest, 
          httpOptions
          )
        .subscribe(
          siteweb => { resolve(siteweb) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteSitewebProlongation(idProlongation: String, idSiteweb: string):Promise<Siteweb> {

    return new Promise(
      (resolve, reject) => {
        
        //send the delete query
        this.http.delete<Siteweb>(
          `${apiEndpoint}/sitewebs/prolongations/${idProlongation}/${idSiteweb}`
          )
        .subscribe(
            siteweb => { resolve(siteweb) },
            error => reject(error)
        )
      }
    );
  }

  async deleteSiteweb(idLancementConcurrence: String, idSiteweb: string):Promise<Siteweb> {

    return new Promise(
      (resolve, reject) => {
        
        //send the delete query
        this.http.delete<Siteweb>(
          `${apiEndpoint}/sitewebs/${idLancementConcurrence}/${idSiteweb}`
          )
        .subscribe(
            siteweb => { resolve(siteweb) },
            error => reject(error)
        )
      }
    );
  }
  
};