export class Soumissionnaire {
    id: string
    denominationEntreprise: string
    dateEtHeureDepot:Date

    constructor (
        denominationEntreprise: string,
        dateEtHeureDepot:Date
    ){
        this.denominationEntreprise=denominationEntreprise
        this.dateEtHeureDepot = dateEtHeureDepot
    }
    static getDummy(): Soumissionnaire {
        return new Soumissionnaire(
          "denominationEntreprise",
          new Date()
        )
      }
}