import { Siteweb } from './siteweb.model';
import { OrganePresse } from './organe.presse.model';
import { OperateurEconomique } from './operateur.economique.model';
import { CanalAffichage } from './canal-affichage.model';

export class Prolongation {
    _id: string;
    nombreJours: number
    dateCreation: Date;

    sitewebs: Siteweb[];
    organesPresse: OrganePresse[];
    operateursEconomiques: OperateurEconomique[];
    canauxAffichage: CanalAffichage[];
    
    constructor (_id: string, nombreJours: number, dateCreation: Date){
        this._id = _id;
        this.nombreJours = nombreJours;
        this.dateCreation = dateCreation;

        this.sitewebs= [];
        this.organesPresse= [];
        this.operateursEconomiques= [];
        this.canauxAffichage= [];
    }

    static getDummy():Prolongation {
        return new Prolongation(
            Math.random().toString(),
            14, 
            new Date()
        );
    }
}