import { Injectable } from '@angular/core';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { Lancement } from 'src/app/models/lancement/lancement.model';
import { LancementPostRequest } from 'src/app/requests/lancement-post.request';
import { LancementPutRequest } from 'src/app/requests/lancement-put.request';
import { LancementAccess } from '../data-access/lancement.access';

@Injectable()
export class LancementService {

  constructor(private lancementAccess: LancementAccess){}

  getAllLancements(): Promise<Lancement[]> {
    return this.lancementAccess.getAllLancements();
  }

  getLancement(idLancement: string): Promise<Lancement>{
    return this.lancementAccess.getLancement(idLancement);
  }


  postLancement(idElaborationCDC: string, lancementPostRequest: LancementPostRequest): Promise<Lancement> {
    return this.lancementAccess.postLancement(idElaborationCDC, lancementPostRequest);
  }

  putLancement(idLancement: string, lancementPutRequest: LancementPutRequest): Promise<Lancement> {
    return this.lancementAccess.putLancement(idLancement, lancementPutRequest);
  }

  deleteLancement(idElaborationCDC: string, idLancement: string):Promise<Lancement> {
    return this.lancementAccess.deleteLancement(idElaborationCDC, idLancement);
  }
}



