import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { CalendarEntry } from 'src/app/models/calendar.entry.model';
import { CalendarEntryPostRequest } from 'src/app/requests/calendar-entry-post.request';
import { CalendarEntryPutRequest } from 'src/app/requests/calendar-entry-put.request';
import { CalendarEntryGetRequest } from 'src/app/requests/calendar-entry-get.request';


@Injectable()
export class CalendarEntryAccess {

  constructor(private http: HttpClient){}


  async getAllCalendarEntries(): Promise<CalendarEntry[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<CalendarEntry[]>(
          `${apiEndpoint}/calendar-entries`,
          )
        .subscribe(
          calendar_entries => { resolve(calendar_entries) },
          error => reject(error)
        )

      });
  }


  async getProchainJourOuvrable(date: Date): Promise<CalendarEntry[]> {
    const dateString = encodeURIComponent(date.toDateString());
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<CalendarEntry[]>(
          `${apiEndpoint}/calendar-entries/prochain-jour-ouvrable/${dateString}`
          )
        .subscribe(
          calendar_entries => { resolve(calendar_entries) },
          error => reject(error)
        )

      });
  }


  async postCalendarEntry(calendarEntry: CalendarEntryPostRequest): Promise<CalendarEntry> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<CalendarEntry>(
          `${apiEndpoint}/calendar-entries`, 
          calendarEntry, 
          httpOptions
          )
        .subscribe(
            calendar_entry => { resolve(calendar_entry) },
            error => reject(error)
        )
      }
    );
  }


  async putCalendarEntry(calendarEntry: CalendarEntryPutRequest, id: String): Promise<CalendarEntry> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<CalendarEntry>(
          `${apiEndpoint}/calendar-entries/${id}`, 
          calendarEntry, 
          httpOptions
          )
        .subscribe(
            calendar_entry => { resolve(calendar_entry) },
            error => reject(error)
        )
      }
    );
  }
  

  async deleteCalendarEntry(id: String): Promise<CalendarEntry> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<CalendarEntry>(
          `${apiEndpoint}/calendar-entries/${id}`
          )
        .subscribe(
          calendar_entry => { resolve(calendar_entry) },
            error => reject(error)
        )
      }
    );
  }

  

} 