import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ModificationCommissionCompetente } from 'src/app/models/commission-competente/modification-commission-competente.model';
import { ModificationCommissionCompetentePutRequest } from 'src/app/requests/modification-commission-competente-put.request';
import { ModificationCommissionCompetentePostRequest } from 'src/app/requests/modification-commission-competente-post.request';


@Injectable()
export class ModificationCommissionCompetenteAccess {

  constructor(private http: HttpClient){}


  async getAllModificationsCommissionsCompetentes(): Promise<ModificationCommissionCompetente[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<ModificationCommissionCompetente[]>(
          `${apiEndpoint}/modifications-commissions-competentes`,
          )
        .subscribe(
          modificationsCommissionsCompetentes => { resolve(modificationsCommissionsCompetentes) },
          error => reject(error)
        )

      });
  };




  async postModificationCommissionCompetente(
    modificationCommissionCompetente: ModificationCommissionCompetentePostRequest,
    idCommissionCompetente: string): Promise<ModificationCommissionCompetente> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<ModificationCommissionCompetente>(
          `${apiEndpoint}/modifications-commissions-competentes/${idCommissionCompetente}`, 
          modificationCommissionCompetente, 
          httpOptions
          )
        .subscribe(
          commissionCompetente => { resolve(commissionCompetente) },
            error => reject(error)
        )
      }
    );
  };


  async putModificationCommissionCompetente(modificationCommissionCompetente: ModificationCommissionCompetentePutRequest, idModificationCommissionCompetente: String): Promise<ModificationCommissionCompetente> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<ModificationCommissionCompetente>(
          `${apiEndpoint}/modifications-commissions-competentes/${idModificationCommissionCompetente}`, 
          modificationCommissionCompetente, 
          httpOptions
          )
        .subscribe(
          commissionCompetente => { resolve(commissionCompetente) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteModificationCommissionCompetente(idCommissionCompetente: string, idModificationCommissionCompetente: string): Promise<ModificationCommissionCompetente> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<ModificationCommissionCompetente>(
          `${apiEndpoint}/modifications-commissions-competentes/${idCommissionCompetente}/${idModificationCommissionCompetente}`
          )
        .subscribe(
            modificationCommissionCompetente => { resolve(modificationCommissionCompetente) },
            error => reject(error)
        )
      }
    );
  }
  
};