import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { DeliberationCOPEO } from 'src/app/models/copeo/deliberation-copeo.model';
import { DeliberationCOPEOPostRequest } from 'src/app/requests/deliberation-copeo-post.request';
import { DeliberationCOPEOPutRequest } from 'src/app/requests/deliberation-copeo-put.request';


@Injectable()
export class DeliberationCOPEOAccess {


  


  constructor(private http: HttpClient){}
 
  async getAllDeliberations(): Promise<DeliberationCOPEO[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<DeliberationCOPEO[]>(
          `${apiEndpoint}/deliberations-copeos`,
          )
        .subscribe(
          deliberations => { resolve(deliberations) },
          error => reject(error)
        )

      });
  };


 

  async getDeliberationCOPEO(idDeliberationCOPEO: string): Promise<DeliberationCOPEO> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<DeliberationCOPEO>(
          `${apiEndpoint}/deliberations-copeos/${idDeliberationCOPEO}`,
          )
        .subscribe(
          deliberation => { resolve(deliberation) },
          error => reject(error)
        )

    });
  }
 
 

  async  postDeliberationCOPEO(idNotificationCOPEO: string, deliberationCOPEOPostRequest: DeliberationCOPEOPostRequest): Promise<DeliberationCOPEO> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<DeliberationCOPEO>(
          `${apiEndpoint}/deliberations-copeos/${idNotificationCOPEO}`, 
          deliberationCOPEOPostRequest, 
          httpOptions
          )
        .subscribe(
            deliberation => { resolve(deliberation) },
            error => reject(error)
        )
      }
    );
  };

 

  async putDeliberationCOPEO(idDeliberationCOPEO: String, deliberationCOPEOPutRequest: DeliberationCOPEOPutRequest): Promise<DeliberationCOPEO> {
 
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<DeliberationCOPEO>(
          `${apiEndpoint}/deliberations-copeos/${idDeliberationCOPEO}`,
          deliberationCOPEOPutRequest,
          httpOptions
          )
        .subscribe(
            projet => { resolve(projet) },
            error => reject(error)
        )
      }
    );
  };
  



  async deleteDeliberationCOPEO(idNotificationCOPEO: string, idDeliberationCOPEO: String): Promise<DeliberationCOPEO> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<DeliberationCOPEO>(
          `${apiEndpoint}/deliberations-copeos/${idNotificationCOPEO}/${idDeliberationCOPEO}`
          )
        .subscribe(
            delib => { resolve(delib) },
            error => reject(error)
        )
      }
    );
  }
  
};