import { ReponseEclaircissement } from './reponse.eclaircissement.model'
import { OperateurEconomique } from './operateur.economique.model'
import { RepresentantOperateurEconomiqueAccess } from '../domaine/data-access/representant-operateur-economique.access'
import { OperateurEconomiqueAccess } from '../domaine/data-access/operateur-economique.access'
import { RepresentantOperateurEconomique } from './representant.operateur.economique.model'

export class DemandeEclaircissement {
    _id: string;
    dateCreation: Date;
    operateurEconomique: OperateurEconomique;
    demandeur: RepresentantOperateurEconomique;
    numero: string;
    dateDemande: Date ;
    pathDemande: string  ;
    dateNotificationSC: Date ;
    reponseEclaircissement: ReponseEclaircissement;

    constructor(
        operateurEconomique: OperateurEconomique,
        demandeur: RepresentantOperateurEconomique ,
        numero: string,
        dateDemande: Date ,
        pathDemande: string  ,
        dateNotificationSC: Date ,
        reponseEclaircissement: ReponseEclaircissement
    ){
        this.dateCreation = new Date();
        this.operateurEconomique= operateurEconomique;
        this.demandeur = demandeur;
        this.dateDemande= dateDemande;
        this.numero=numero;
        this.pathDemande=pathDemande;
        this.dateNotificationSC = dateNotificationSC;
        this.reponseEclaircissement=reponseEclaircissement
    }
    static getDummy(){
        return new DemandeEclaircissement(
            OperateurEconomique.getDummy(),
            RepresentantOperateurEconomique.getDummy(),
            "num1", 
            new Date(),
            'demandePath',
            new Date(),
            ReponseEclaircissement.getDummy()
        );
    }
}