import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { Lancement } from 'src/app/models/lancement/lancement.model';
import { LancementPostRequest } from 'src/app/requests/lancement-post.request';
import { LancementPutRequest } from 'src/app/requests/lancement-put.request';
import { LancementService } from 'src/app/domaine/business-logic/lancements.service';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';


@Component({
  selector: 'app-create-lancement',
  templateUrl: './create-lancement.component.html',
  styleUrls: ['./create-lancement.component.css']
})
export class CreateLancementComponent implements OnInit {


  constructor(
    public dialogRef: MatDialogRef<CreateLancementComponent>,
    private lancementService: LancementService,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, elb_cdc: ElaborationCDC, lncm: Lancement}
  ) {

  };




  lancementForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;


  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.lncm.dateCreationLancement);
    }

    this.lancementForm = new FormGroup({
      'dateCreationLancement': new FormControl({value: null, disabled: true}, [Validators.required]),
      'dureePreparationOffres': new FormControl({value: 15, disabled: this.isDelete}, [Validators.required, Validators.min(0)]),
      'dureeValiditeOffres': new FormControl({value: 90, disabled: this.isDelete}, [Validators.required, Validators.min(0)])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.lancementForm.patchValue(
        {
        "dureePreparationOffres": this.data.lncm.lancementConcurrence.dureePreparationOffres, 
        "dureeValiditeOffres" : this.data.lncm.lancementConcurrence.dureeValiditeOffres
        }
      );
    }

  };











  //Data model to submit to the DB server
  lancementPostRequest: LancementPostRequest;
  lancementPutRequest: LancementPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.lancementForm);


    // save the data into the database
    let result:Promise<Lancement>;
    if (this.isCreate){
      this.lancementPostRequest = new LancementPostRequest(
        this.lancementForm.value.dureePreparationOffres,
        this.lancementForm.value.dureeValiditeOffres
      );
      result = this.lancementService.postLancement(this.data.elb_cdc._id, this.lancementPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.lancementPutRequest = new LancementPutRequest(
        this.lancementForm.value.dureePreparationOffres,
        this.lancementForm.value.dureeValiditeOffres
      );      
      result = this.lancementService.putLancement(this.data.lncm._id, this.lancementPutRequest);
    };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.lancementService.deleteLancement(this.data.elb_cdc._id, this.data.lncm._id);
    };

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
        /*
        {
          _id: value._id , 
          intituleProjet : value.intituleProjet,
          codeProjet: value.codeProjet,
        }
        */
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });

  }



  
}
