import { Injectable } from '@angular/core';
import { ProjetAccess } from '../data-access/projet.access';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { Prolongation } from 'src/app/models/prolongation.model';
import { ProlongationPostRequest } from 'src/app/requests/prolongation-post.request';
import { ProlongationPutRequest } from 'src/app/requests/prolongation-put.request';
import { ProlongationAccess } from '../data-access/prolongation.access';

@Injectable()
export class ProlongationService {

  constructor(private prolongationAccess: ProlongationAccess){}

  postProlongation(idLancementConcurrence: string, prolongation: ProlongationPostRequest): Promise<Prolongation> {
    return this.prolongationAccess.postProlongation(idLancementConcurrence, prolongation);
  }

  putProlongation(idProlongation: string, prolongationPutRequest: ProlongationPutRequest): Promise<Prolongation> {
    return this.prolongationAccess.putProlongation(idProlongation, prolongationPutRequest);
  }

  deleteProlongation(idLancementConcurrence: String, idProlongation: string):Promise<Prolongation> {
    return this.prolongationAccess.deleteProlongation(idLancementConcurrence, idProlongation);
  }
}



