import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { PassageCommissionMarches } from 'src/app/models/passage-commission/passage.commission.marches.model';
import { ProjetEnAttentePassageCommissionMarches } from 'src/app/models/passage-commission/projet.en.attente.passage.commission.marches.model';
import { DepotProjetCDCSecretariatCommissionCompetenteCDCPostRequest } from 'src/app/requests/depot-projet-cdc-secretariat-commission-competente-CDC-post.request';
import { DepotAdditifCDCSecretariatCommissionCompetenteCDCPostRequest } from 'src/app/requests/depot-additif-cdc-secretariat-commission-competente-CDC-post.request';


@Injectable()
export class PassageCommissionMarchesAccess {

  async getProjetEnAttenteByCommissionId(idCommissionCompetente: string): Promise<ProjetEnAttentePassageCommissionMarches[]> {
     
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<ProjetEnAttentePassageCommissionMarches[]>(
          `${apiEndpoint}/passages-commissions-marches/projets-en-attente/${idCommissionCompetente}`,
          )
        .subscribe(
          passages => { resolve(passages) },
          error => reject(error)
        )

      });
  
}

  async getPassagesCommissionMarchesByCommissionId(idCommissionCompetente: string): Promise<PassageCommissionMarches[]> {
     
      return new Promise(
        (resolve, reject) => {
  
          // envoyer une requete API
          this.http.get<PassageCommissionMarches[]>(
            `${apiEndpoint}/passages-commissions-marches/${idCommissionCompetente}`,
            )
          .subscribe(
            passages => { resolve(passages) },
            error => reject(error)
          )
  
        });
    
  }

  constructor(private http: HttpClient){}


  async postDepotProjetCDCPassageCommissionMarches(projetCDCId: string, depotProjetCDCSecretariatCommissionCompetenteCDCPostRequest: DepotProjetCDCSecretariatCommissionCompetenteCDCPostRequest): Promise<PassageCommissionMarches> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<PassageCommissionMarches>(
          `${apiEndpoint}/passages-commissions-marches/depot-projet-cdc/${projetCDCId}`, 
          depotProjetCDCSecretariatCommissionCompetenteCDCPostRequest, 
          httpOptions
          )
        .subscribe(
          passageCommissionMarches => { resolve(passageCommissionMarches) },
            error => reject(error)
        )
      }
    );
  };


  async postDepotAdditifCDCPassageCommissionMarches(additifCDCId: string, depotAdditifCDCSecretariatCommissionCompetenteCDCPostRequest: DepotAdditifCDCSecretariatCommissionCompetenteCDCPostRequest): Promise<PassageCommissionMarches> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<PassageCommissionMarches>(
          `${apiEndpoint}/passages-commissions-marches/depot-additif-cdc/${additifCDCId}`, 
          depotAdditifCDCSecretariatCommissionCompetenteCDCPostRequest, 
          httpOptions
          )
        .subscribe(
          passageCommissionMarches => { resolve(passageCommissionMarches) },
            error => reject(error)
        )
      }
    );
  };


  
};