export class MiseAuPoint {
    dateDemande: Date
    dateMiseAuPoint: Date
    PVPath: string 
    resume: string
    montant: number //DZD
    delai: number //jours 

    constructor(
        dateDemande: Date,
        dateMiseAuPoint: Date,
        PVPath: string,
        resume: string,
        montant: number,
        delai: number
    ) {
        this.dateDemande=dateDemande,
        this.dateMiseAuPoint=dateMiseAuPoint,
        this.PVPath=PVPath,
        this.resume=resume,
        this.montant= montant,
        this.delai = delai
    }

    static getDummy(): MiseAuPoint {
        return new MiseAuPoint(
            new Date(),
            new Date(),
            "PVPath",
            "resume",
            5000,
            52
        )
    }
}