import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';

import { DialogAction } from 'src/app/enums/dialog-action.enum'; 
import { CommissionCompetente } from 'src/app/models/commission-competente/commission-competente.model';
import { CommissionCompetenteService } from 'src/app/domaine/business-logic/commissions-competentes.service';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { TypeCommissionCompetente } from 'src/app/enums/type-commission-competente.enum';
import { PassageCommissionMarches } from 'src/app/models/passage-commission/passage.commission.marches.model';
import { PassageCommissionMarchesService } from 'src/app/domaine/business-logic/passages-commissions-marches.service';
import { DepotProjetCDCSecretariatCommissionCompetenteCDCPostRequest } from 'src/app/requests/depot-projet-cdc-secretariat-commission-competente-CDC-post.request';

@Component({
  selector: 'app-create-depot-projet-cdc-secretariat-commission-competente-cdc',
  templateUrl: './create-depot-projet-cdc-secretariat-commission-competente-cdc.html',
  styleUrls: ['./create-depot-projet-cdc-secretariat-commission-competente-cdc.css']
})

export class CreateDepotProjetCDCSecretariatCommissionCompetenteCDC  implements OnInit {
  
    constructor(
      public dialogRef: MatDialogRef<CreateDepotProjetCDCSecretariatCommissionCompetenteCDC>,
      private commissionCompetenteService: CommissionCompetenteService,
      private passageCommissionMarchesService: PassageCommissionMarchesService,
      @Inject(MAT_DIALOG_DATA) public data: {
        action: DialogAction, 
        elbCDC: ElaborationCDC, 
        sousPrj: SousProjet, 
        prj: Projet,
        typCommComp: TypeCommissionCompetente
      }
    ) {
  
    };
  
    loadingCC: boolean = false;
    //all the SC list
    optionsCC: CommissionCompetente[] = [];
    //the filtered SC list to show to the user
    filteredOptionsCC: Observable<CommissionCompetente[]>;
  
  
    depotSecretariatCommissionCompetenteCDCForm: FormGroup;
  
    isCreate : boolean;

  
    // date creation to show (if create: today, if delete of modify: date creation )
    date ;
  
    
    ngOnInit() {


      this.isCreate = this.data.action === DialogAction.CREATE;

  
      //set the date of creation
      if (this.isCreate){
        this.date = new FormControl(new Date());
      }

      this.depotSecretariatCommissionCompetenteCDCForm = new FormGroup({
        'dateDepotSecretariatCommissionCompetenteCDCForm': new FormControl({value: null, disabled: true}, [Validators.required]),
        'commissionCompetente': new FormGroup({
          '_id' : new FormControl({value: null, disabled: false}, [Validators.required],[this.existsCC.bind(this), this.rightCC.bind(this)]),
          'designation' : new FormControl({value: null, disabled: true}, [Validators.required]),
          'typeCommission' : new FormControl({value: null, disabled: true}, [Validators.required])
        })
      });


      // load les servicesContractant for admin
      this.loadingCC = true;
      const result:Promise<CommissionCompetente[]> = this.commissionCompetenteService.getAllCommissionsCompetentes();


      result
      .then((value) =>{
        this.loadingCC = false;
        this.optionsCC = value;

        console.log("Coool ...", value);

        // recheck once the data is loaded
        const commissionCompetenteForm : AbstractControl =  this.depotSecretariatCommissionCompetenteCDCForm.get("commissionCompetente");
        (<FormGroup>commissionCompetenteForm).controls["_id"].updateValueAndValidity();

        console.log("Coool2 ...", value);

        // generate the filtered data
        this.filteredOptionsCC = this.depotSecretariatCommissionCompetenteCDCForm.get("commissionCompetente").get("_id").valueChanges
        .pipe(
          startWith(''),
          map( valueCC => this._filterCC(valueCC) )
        );

        console.log("Coool3 ...", value);
        return;
      })
      .catch(()=>{
        this.loadingCC = false ;
        console.log("Noooot Coool ...");
        return this.optionsCC = null;
      });

    };
  
    //Data model to submit to the DB server
    depotProjetCDCSecretariatCommissionCompetenteCDCPostRequest: DepotProjetCDCSecretariatCommissionCompetenteCDCPostRequest;
  
    loadingData: boolean = false ;
    errorLoadingData: boolean = false ;
  
    onSubmit() {
      this.loadingData = true ;
      this.errorLoadingData = false ;
      console.log("Submitted", this.depotSecretariatCommissionCompetenteCDCForm) ;
  
  
  
      
      // save the data into the database
      let result:Promise<PassageCommissionMarches>;
      if (this.isCreate){
        this.depotProjetCDCSecretariatCommissionCompetenteCDCPostRequest = new DepotProjetCDCSecretariatCommissionCompetenteCDCPostRequest(
          this.depotSecretariatCommissionCompetenteCDCForm.value.commissionCompetente._id
        );
        result = this.passageCommissionMarchesService.postDepotProjetCDCPassageCommissionMarches(this.data.elbCDC.projetCDC._id, this.depotProjetCDCSecretariatCommissionCompetenteCDCPostRequest);
      };

      result
      .then((value) => {
        console.log(value);
        this.loadingData = false;
        this.dialogRef.close(
          value
          /*
          {
            _id: value._id , 
            numero : value.numero,
            designation: value.designation,
            typeCommission: value.typeCommission,
            dateCreationCommissionCompetente: value.dateCreationCommissionCompetente,
            modificationsCommissionCompetente: value.modificationsCommissionCompetente,
            membresCommissionCompetente: value.membresCommissionCompetente,
            pathJustificatifDecision: value.pathJustificatifDecision
          }
          */
        )
      })
      .catch(()=>{
        this.loadingData = false;
        return this.errorLoadingData = true;
      });
    


  
    }
  
  
  
    rightCC(control: FormControl): Promise<any> | Observable<any> {
      const _id = control.value;
      const result : CommissionCompetente = this.getSelectedCC(_id);
      if(!result || !this.data.typCommComp){
        return new Promise<any>(
          (resolve, reject) => {
                resolve(null);
            }
        )
      }
      const typeCommissionSelected: TypeCommissionCompetente = result.typeCommission;
      console.log("comparing: ", TypeCommissionCompetente[typeCommissionSelected])
      console.log("with", this.data.typCommComp)
      return new Promise<any>(
        (resolve, reject) => {
            if (TypeCommissionCompetente[typeCommissionSelected] === this.data.typCommComp) {
              resolve(null);
            } else {
              resolve({ "typeCommissionCompetenteInvalid": true });
            }
          }
      )
    }

    existsCC(control: FormControl): Promise<any> | Observable<any> {
      const _id = control.value;
      const result:CommissionCompetente = this.getSelectedCC(_id);
      return new Promise<any>(
        (resolve, reject) => {
            if (result != null) {
              resolve(null);
            } else {
              resolve({ "commissionCompetenteInvalide": true });
            }
          }
      )
    }

    getSelectedCC(_id : string) : CommissionCompetente{
      if (_id){
        return this.optionsCC.find(e => e._id === _id);
      } else {
        return null;
      }
    }

    getSelectedCCDesignation(): string{
      const result = this.getSelectedCC(this.depotSecretariatCommissionCompetenteCDCForm.value.commissionCompetente._id);
      if (result) {return result.designation;}
      return "";
    };
  
    getSelectedCCTypeCommission(): string{
      const result = this.getSelectedCC(this.depotSecretariatCommissionCompetenteCDCForm.value.commissionCompetente._id);
      if (result) {return result.typeCommission;}
      return "";
    };

  
    private _filterCC(valueInput: string): CommissionCompetente[] {
      const filterValue = valueInput.toLowerCase();
      return this.optionsCC.filter(
        (cc : CommissionCompetente) => {
          let option:string = cc.designation + " " + cc.typeCommission; 
          return option.toLowerCase().includes(filterValue);
        }
      );
    }

    
    getCommissionCompetente(): TypeCommissionCompetente{ // change return to TypeCommissionCompetente
      return this.data.typCommComp;
    }
  }
  