  async postCOPEO(copeo: COPEOPostRequest): Promise<COPEO> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<COPEO>(
          `${apiEndpoint}/copeos`, 
          copeo, 
          httpOptions
          )
        .subscribe(
          cop => { resolve(cop) },
            error => reject(error)
        )
      }
    );
  };


