import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { RepresentantOperateurEconomique } from 'src/app/models/representant.operateur.economique.model';
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { CreateRepresentantOperateurEconomiqueComponent } from 'src/app/dialogs/create-representant-operateur-economique/create-representant-operateur-economique.component';
import { DialogAction } from 'src/app/enums/dialog-action.enum';

@Component({
  selector: 'app-representants',
  templateUrl: './representants.component.html',
  styleUrls: ['./representants.component.css']
})
export class RepresentantsComponent implements OnInit {

  @ViewChild("tableRepresentantsOperateurEconomique", {static: true}) tableRepresentantsOperateurEconomique: MatTable<RepresentantOperateurEconomique>;

  @Input() operateurEconomique : OperateurEconomique;

  displayedColumnsRepresentantsOperateurEconomique: string[] = [
    'dateCreation',
    'civilite',
    'nom',
    'prenom',
    'qualite',
    'email',
    'telephone',
    'fax',
    'portable',
    'nationalite',
    'action'
    ];

  constructor(public dialog: MatDialog) { }

  ngOnInit() {

  }






  openDialogCreateRepresentantOperateurEconomique(): void {
    const dialogRef = this.dialog.open(CreateRepresentantOperateurEconomiqueComponent, {
      width: '800px',
      data: {
        action: DialogAction.CREATE, 
        rprst: null, 
        id_oper_econ: this.operateurEconomique._id,
        id_cnd: null}
    });

    dialogRef.afterClosed().subscribe(result => {
        console.log("After delete depot result:", result);
        if (this.operateurEconomique && result) {
          this.operateurEconomique.representants.push(result);
          this.tableRepresentantsOperateurEconomique.renderRows();
        }

    });

  }





openDialogDeleteRepresentantOperateurEconomique(representant: RepresentantOperateurEconomique): void {
  const dialogRef = this.dialog.open(CreateRepresentantOperateurEconomiqueComponent, {
    width: '800px',
    data: {
      action: DialogAction.DELETE, 
      rprst: representant, 
      id_oper_econ: this.operateurEconomique._id,
      id_cnd: null}
  });

  dialogRef.afterClosed().subscribe(result => {
      if (this.operateurEconomique && result) {
        let index = this.operateurEconomique.representants.findIndex(e => e._id == result._id);
        this.operateurEconomique.representants.splice(index, 1);
        this.tableRepresentantsOperateurEconomique.renderRows();
      }
  });

}


openDialogModifyRepresentantOperateurEconomique(representant: RepresentantOperateurEconomique): void {
  const dialogRef = this.dialog.open(CreateRepresentantOperateurEconomiqueComponent, {
    width: '800px',
    data: {
      action: DialogAction.MODIFY, 
      rprst: representant, 
      id_oper_econ: this.operateurEconomique._id,
      id_cnd: null}
  });

  dialogRef.afterClosed().subscribe(result => {

    if(this.operateurEconomique && result) {
      let rpr = this.operateurEconomique.representants.find(e => e._id === result._id)
      for(var k in rpr) rpr[k]=result[k];
    }

  });

}




}
