import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { ServiceContractantAccess } from '../data-access/service-contractant.access';
import { Injectable } from '@angular/core';
import { ServiceContractantPostRequest } from 'src/app/requests/service-contractant-post.request';
import { ServiceContractantPutRequest } from 'src/app/requests/service-contractant-put.request';
import { AttributionCCMEPostRequest } from 'src/app/requests/attribution-ccme-post.request';
import { CommissionCompetente } from 'src/app/models/commission-competente/commission-competente.model';
import { AttributionCMUPostRequest } from 'src/app/requests/attribution-cmu-post.request';
import { AttributionCCMEPutRequest } from 'src/app/requests/attribution-ccme-put.request';
import { AttributionCMUPutRequest } from 'src/app/requests/attribution-cmu-put.request';
import { COPEO } from 'src/app/models/copeo/copeo.model';
import { AttributionCOPEOUPostRequest } from 'src/app/requests/attribution-copeou-post.request';
import { AttributionCOPEORPostRequest } from 'src/app/requests/attribution-copeor-post.request';
import { AttributionCOPEOCPostRequest } from 'src/app/requests/attribution-copeoc-post.request';
import { AttributionCOPEOAOPostRequest } from 'src/app/requests/attribution-copeoao-post.request';
import { AttributionCOPEOUPutRequest } from 'src/app/requests/attribution-copeou-put.request';
import { AttributionCOPEORPutRequest } from 'src/app/requests/attribution-copeor-put.request';
import { AttributionCOPEOCPutRequest } from 'src/app/requests/attribution-copeoc-put.request';
import { AttributionCOPEOAOPutRequest } from 'src/app/requests/attribution-copeoao-put.request';

@Injectable()
export class ServiceContractantService {

 
  constructor(private serviceContractantAccess: ServiceContractantAccess){}

  getAllServicesContractants(): Promise<ServiceContractant[]>{
    return this.serviceContractantAccess.getAllServicesContractants()
  }

  getServiceContractantByIdentifiant(id: String): Promise<ServiceContractant>{
    return this.serviceContractantAccess.getServiceContractantByIdentifiant(id)
  }

  getServiceContractantByEmail(email: String): Promise<ServiceContractant>{
    return this.serviceContractantAccess.getServiceContractantByEmail(email)
  }

  postServiceContractant(serviceContractant: ServiceContractantPostRequest): Promise<ServiceContractant>{
    return this.serviceContractantAccess.postServiceContractant(serviceContractant)
  }

  putServiceContractant(serviceContractant: ServiceContractantPutRequest, id: String): Promise<ServiceContractant>{
    return this.serviceContractantAccess.putServiceContractant(serviceContractant, id)
  }

  deleteServiceContractant(id: String):Promise<ServiceContractant>{
    return this.serviceContractantAccess.deleteServiceContractant(id)
  }


  postAttributionCCME(idSeviceContractant: string, attributionCCMEPostRequest: AttributionCCMEPostRequest): Promise<CommissionCompetente> {
    return this.serviceContractantAccess.postAttributionCCME(idSeviceContractant, attributionCCMEPostRequest);
  }

  postAttributionCMU(idSeviceContractant: string, attributionCMUPostRequest: AttributionCMUPostRequest): Promise<CommissionCompetente> {
    return this.serviceContractantAccess.postAttributionCMU(idSeviceContractant, attributionCMUPostRequest);
  }

  putAttributionCCME(idSeviceContractant: string, attributionCCMEPutRequest: AttributionCCMEPutRequest): Promise<CommissionCompetente> {
    return this.serviceContractantAccess.putAttributionCCME(idSeviceContractant, attributionCCMEPutRequest);
  }

  putAttributionCMU(idSeviceContractant: string, attributionCMUPutRequest: AttributionCMUPutRequest): Promise<CommissionCompetente> {
    return this.serviceContractantAccess.putAttributionCMU(idSeviceContractant, attributionCMUPutRequest);
  }


  deleteAttributionCMU(idSeviceContractant: string): Promise<CommissionCompetente> {
    return this.serviceContractantAccess.deleteAttributionCMU(idSeviceContractant);
  }

  deleteAttributionCCME(idSeviceContractant: string): Promise<CommissionCompetente> {
    return this.serviceContractantAccess.deleteAttributionCCME(idSeviceContractant);
  }



  postAttributionCOPEOU(idServiceContractant: string, attributionCOPEOUPostRequest: AttributionCOPEOUPostRequest): Promise<COPEO> {
    return this.serviceContractantAccess.postAttributionCOPEOU(idServiceContractant, attributionCOPEOUPostRequest);
  }
  postAttributionCOPEOR(idServiceContractant: string, attributionCOPEORPostRequest: AttributionCOPEORPostRequest): Promise<COPEO> {
    return this.serviceContractantAccess.postAttributionCOPEOR(idServiceContractant, attributionCOPEORPostRequest);
  }
  postAttributionCOPEOC(idServiceContractant: string, attributionCOPEOCPostRequest: AttributionCOPEOCPostRequest): Promise<COPEO> {
    return this.serviceContractantAccess.postAttributionCOPEOC(idServiceContractant, attributionCOPEOCPostRequest);
  }
  postAttributionCOPEOAO(idServiceContractant: string, attributionCOPEOAOPostRequest: AttributionCOPEOAOPostRequest): Promise<COPEO> {
    return this.serviceContractantAccess.postAttributionCOPEOAO(idServiceContractant, attributionCOPEOAOPostRequest);
  }

  putAttributionCOPEOU(idServiceContractant: string, attributionCOPEOUPutRequest: AttributionCOPEOUPutRequest): Promise<COPEO> {
    return this.serviceContractantAccess.putAttributionCOPEOU(idServiceContractant, attributionCOPEOUPutRequest);
  }
  putAttributionCOPEOR(idServiceContractant: string, attributionCOPEORPutRequest: AttributionCOPEORPutRequest): Promise<COPEO> {
    return this.serviceContractantAccess.putAttributionCOPEOR(idServiceContractant, attributionCOPEORPutRequest);
  }
  putAttributionCOPEOC(idServiceContractant: string, attributionCOPEOCPutRequest: AttributionCOPEOCPutRequest): Promise<COPEO> {
    return this.serviceContractantAccess.putAttributionCOPEOC(idServiceContractant, attributionCOPEOCPutRequest);
  }
  putAttributionCOPEOAO(idServiceContractant: string, attributionCOPEOAOPutRequest: AttributionCOPEOAOPutRequest): Promise<COPEO> {
    return this.serviceContractantAccess.putAttributionCOPEOAO(idServiceContractant, attributionCOPEOAOPutRequest);
  }


  deleteAttributionCOPEOU(idServiceContractant: string): Promise<COPEO> {
    return this.serviceContractantAccess.deleteAttributionCOPEOU(idServiceContractant);
  }
  deleteAttributionCOPEOR(idServiceContractant: string): Promise<COPEO> {
    return this.serviceContractantAccess.deleteAttributionCOPEOR(idServiceContractant);
  }
  deleteAttributionCOPEOC(idServiceContractant: string): Promise<COPEO> {
    return this.serviceContractantAccess.deleteAttributionCOPEOC(idServiceContractant);
  }
  deleteAttributionCOPEOAO(idServiceContractant: string): Promise<COPEO> {
    return this.serviceContractantAccess.deleteAttributionCOPEOAO(idServiceContractant);
  }

}



