

import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms'; 
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { UploadService } from 'src/app/upload.service';
import { map, startWith } from 'rxjs/operators';
import { DemandeEclaircissementService } from 'src/app/domaine/business-logic/demandes-eclaircissement.service';
import { ReponseEclaircissement } from 'src/app/models/reponse.eclaircissement.model';
import { RetraitCDC } from 'src/app/models/lancement/retrait-cdc.model';
import { ReponseEclaircissementPostRequest } from 'src/app/requests/reponse-eclaircissement-post.request';
import { ReponseEclaircissementPutRequest } from 'src/app/requests/reponse-eclaircissement-put.request';
 


@Component({
  selector: 'app-create-response-demande-eclaircissement',
  templateUrl: './create-response-demande-eclaircissement.component.html',
  styleUrls: ['./create-response-demande-eclaircissement.component.css']
})
export class CreateReponseDemandeEclaircissementComponent implements OnInit {
 
 
  constructor(
    public dialogRef: MatDialogRef<CreateReponseDemandeEclaircissementComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      rps: ReponseEclaircissement,
      id_dmd: string,
      rtr_cdc: RetraitCDC
    },
    private uploadService: UploadService,
    private demandeEclaircissementService: DemandeEclaircissementService
  ) {
 
  };




  reponseEclaircissementForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;
 
  candidatsNotifies: string[];

  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.rps.dateCreation);
    }

    this.candidatsNotifies = [];
    for(let i =0; i< this.data.rtr_cdc.candidats.length; i++){
      this.candidatsNotifies.push(this.data.rtr_cdc.candidats[i]._id);
    }


    this.reponseEclaircissementForm = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
      'dateReponse': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'pathReponse': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.reponseEclaircissementForm.patchValue(this.data.rps);
    }

  };



  //Data model to submit to the DB server
  reponseEclaircissementPostRequest: ReponseEclaircissementPostRequest;
  reponseEclaircissementPutRequest: ReponseEclaircissementPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.reponseEclaircissementForm);




    // save the data into the database
    let result:Promise<ReponseEclaircissement>;
    if (this.isCreate){
      this.reponseEclaircissementPostRequest = new ReponseEclaircissementPostRequest(
        this.reponseEclaircissementForm.value.dateReponse,
        this.reponseEclaircissementForm.value.pathReponse,
        this.candidatsNotifies
      ); 
      result = this.demandeEclaircissementService.postReponseEclaircissement(this.data.id_dmd, this.reponseEclaircissementPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.reponseEclaircissementPutRequest = new ReponseEclaircissementPutRequest(
        this.reponseEclaircissementForm.value.dateReponse,
        this.reponseEclaircissementForm.value.pathReponse,
        this.candidatsNotifies
    ); 
    result = this.demandeEclaircissementService.putReponseEclaircissement(this.data.id_dmd, this.reponseEclaircissementPutRequest);
  };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.demandeEclaircissementService.deleteReponseEclaircissement(this.data.id_dmd);
    };
    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });

  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.reponseEclaircissementForm.patchValue({'pathReponse' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}
