export class DemandeEclaircissementPutRequest {
  dateDemande:  Date;
  operateurEconomiqueId:string;
  dateNotificationSC:Date;
  numero: string;
  pathDemande: string;

  constructor(
    dateDemande:  Date,
    operateurEconomiqueId:string,
    dateNotificationSC:Date,
    numero: string,
    pathDemande: string
    ){
      this.operateurEconomiqueId= operateurEconomiqueId;
      this.dateDemande=dateDemande;
      this.dateNotificationSC=dateNotificationSC;
      this.numero=numero;
      this.pathDemande = pathDemande;
    }

}