import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { UploadService } from 'src/app/upload.service';
import { FormGroup, FormControl, AbstractControl, Validators } from '@angular/forms';
import { Observable } from 'rxjs';
import { MoyenNotification } from 'src/app/enums/moyen-notification.enum';
import { NotificationOperateurEconomique } from 'src/app/models/notification-operateur-economique.model';
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { NotificationOperateurEconomiqueService } from 'src/app/domaine/business-logic/notifications-operateurs-economiques.service';
import { OperateurEconomiqueService } from 'src/app/domaine/business-logic/operateurs-economiques.service';
import { startWith, map } from 'rxjs/operators';
import { NotificationOperateurEconomiquePostRequest } from 'src/app/requests/notification-operateur-economique-post.request';
import { NotificationOperateurEconomiquePutRequest } from 'src/app/requests/notification-operateur-economique-put.request';
import { Prolongation } from 'src/app/models/prolongation.model';



@Component({
  selector: 'app-create-notification-operateur-economique',
  templateUrl: './create-notification-operateur-economique.component.html',
  styleUrls: ['./create-notification-operateur-economique.component.css']
})
export class CreateNotificationOperateurEconomiqueComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<CreateNotificationOperateurEconomiqueComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      ntf_opr_ecn: NotificationOperateurEconomique, 
      id_lcm_cnrr: string,
      is_prlng : boolean,
      prlng: Prolongation   
    },
    private uploadService: UploadService,
    public notificationOperateurEconomiqueService: NotificationOperateurEconomiqueService,
    public operateurEconomiqueService: OperateurEconomiqueService
  ){
  }


  notificationForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

  loadingOE: boolean =false;
  //all the SC list
  optionsOE: OperateurEconomique[] = [];
  //the filtered SC list to show to the user
  filteredOptionsOE: Observable<OperateurEconomique[]>;

  private _filterOE(valueInput: string): OperateurEconomique[] {
    const filterValue = valueInput.toLowerCase();
    return this.optionsOE.filter(
      (oe : OperateurEconomique) => {
        let option:string = oe.denomination + " " + oe.formeJuridique + " " + oe.nif; 
        return option.toLowerCase().includes(filterValue);
      }
    );
  }

  getSelectedOE(_id : string) : OperateurEconomique{
    if (_id){
      return this.optionsOE.find(e => e._id === _id);
    } else {
      return null;
    }
  }

  getSelectedOEDenomination(): string{
    const result = this.getSelectedOE(this.notificationForm.value.operateurEconomique._id);
    if (result) {return result.denomination;}
    return "";
  };

  getSelectedOEFormeJuridique(): string{
    const result = this.getSelectedOE(this.notificationForm.value.operateurEconomique._id);
    if (result) {return result.formeJuridique;}
    return "";
  };

  getSelectedOENIF(): string{
    const result = this.getSelectedOE(this.notificationForm.value.operateurEconomique._id);
    if (result) {return result.nif;}
    return "";
  };

  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.ntf_opr_ecn.dateCreation);
    }




    this.notificationForm = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
      'operateurEconomique': new FormGroup({
        '_id' : new FormControl({value: null, disabled: false}, [Validators.required],[this.existsOE.bind(this), this.excludedOE.bind(this)]),
        'denomination' : new FormControl({value: null, disabled: true}, [Validators.required]),
        'formeJuridique' : new FormControl({value: null, disabled: true}, [Validators.required]),
        'nif' : new FormControl({value: null, disabled: true}, [Validators.required])
      }),
      'dateNotification': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'moyenNotification': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'pathJustificatif': new FormControl({value: null, disabled: false}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.notificationForm.patchValue(this.data.ntf_opr_ecn);
    }


    // load les servicesContractant for admin
    this.loadingOE = true;
    const result:Promise<OperateurEconomique[]> = this.operateurEconomiqueService.getAllOperateursEconomiques();
    result
    .then((value) =>{
      this.loadingOE = false;
      this.optionsOE = value;

      // recheck once the data is loaded
      const operateurEconomiqueForm : AbstractControl =  this.notificationForm.get("operateurEconomique");
      (<FormGroup>operateurEconomiqueForm).controls["_id"].updateValueAndValidity();

      // generate the filtered data
      this.filteredOptionsOE = this.notificationForm.get("operateurEconomique").get("_id").valueChanges
      .pipe(
        startWith(''),
        map( valueSC => this._filterOE(valueSC) )
      );

      return;
    })
    .catch(()=>{
      this.loadingOE = false ;
      return this.optionsOE = null;
    });

  };


  existsOE(control: FormControl): Promise<any> | Observable<any> {
    const _id = control.value;
    const result:OperateurEconomique = this.getSelectedOE(_id);
    return new Promise<any>(
      (resolve, reject) => {
          if (result != null) {
            resolve(null);
          } else {
            resolve({ "operateurEconomiqueInvalid": true });
          }
        }
    )
  }

  
  excludedOE(control: FormControl): Promise<any> | Observable<any> {
    const _id = control.value;
    const result:OperateurEconomique = this.getSelectedOE(_id);
    return new Promise<any>(
      (resolve, reject) => {
          if (result && result.estExclu) {
            resolve({ "operateurEconomiqueExcluded": true });
          } else {
            resolve(null);
          }
        }
    )
  }
  //MoyenNotification
  moyensNotification = Object.keys(MoyenNotification).slice(0);
  parseValueMoyenNotification(value : string) {
    return MoyenNotification[value].toString();
  }






  //Data model to submit to the DB server
  notificationPostRequest: NotificationOperateurEconomiquePostRequest;
  notificationPutRequest: NotificationOperateurEconomiquePutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.notificationForm);




    // save the data into the database
    let result:Promise<NotificationOperateurEconomique>;
    if (this.isCreate){
      this.notificationPostRequest = new NotificationOperateurEconomiquePostRequest(
        this.notificationForm.value.operateurEconomique._id,
        this.notificationForm.value.dateNotification,
        this.notificationForm.value.pathJustificatif,
        this.notificationForm.value.moyenNotification
      );
      if (this.data.is_prlng)
        result = this.notificationOperateurEconomiqueService.postNotificationOperateurEconomiqueProlongation(this.data.prlng._id, this.notificationPostRequest);
      else 
        result = this.notificationOperateurEconomiqueService.postNotificationOperateurEconomique(this.data.id_lcm_cnrr,this.notificationPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.notificationPutRequest = new NotificationOperateurEconomiquePutRequest(
        this.notificationForm.value.operateurEconomique._id,
        this.notificationForm.value.dateNotification,
        this.notificationForm.value.pathJustificatif,
        this.notificationForm.value.moyenNotification
      );   
      result = this.notificationOperateurEconomiqueService.putNotificationOperateurEconomique(this.data.ntf_opr_ecn._id, this.notificationPutRequest);
    };

    // delete le S/C data from the database
    if(this.isDelete){

      if (this.data.is_prlng)
        result = this.notificationOperateurEconomiqueService.deleteNotificationOperateurEconomiqueProlongation(this.data.prlng._id, this.data.ntf_opr_ecn._id);
      else
        result = this.notificationOperateurEconomiqueService.deleteNotificationOperateurEconomique(this.data.id_lcm_cnrr, this.data.ntf_opr_ecn._id);
    };
    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
        /*
        {
          _id: value._id , 
          serviceContractant: {
            _id: value.serviceContractant._id,
            identifiant: value.serviceContractant.identifiant,
            nomStructure: value.serviceContractant.nomStructure
          },
          intituleProjet : value.intituleProjet,
          codeProjet: value.codeProjet,
          montantProjet: value.montantProjet,
          dateCreationProjet: value.dateCreationProjet,
          yearPlanAction: value.yearPlanAction,
          pourLaStructureRattachement: value.pourLaStructureRattachement,
          sourcesFinancement: value.sourcesFinancement,
          siAutreSourceFinancement: value.siAutreSourceFinancement,
          budget: value.budget,
          isHorsPlan: value.isHorsPlan,
          pathJustificatifProjet: value.pathJustificatifProjet,
          sousProjets : value.sousProjets
        }
        */
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.notificationForm.patchValue({'pathJustificatif' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}
 

