import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';

import { DialogAction } from 'src/app/enums/dialog-action.enum'; 
import { CommissionCompetente } from 'src/app/models/commission-competente/commission-competente.model';
import { CommissionCompetenteService } from 'src/app/domaine/business-logic/commissions-competentes.service';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { TypeCommissionCompetente } from 'src/app/enums/type-commission-competente.enum';
import { AttributionCCMEPostRequest } from 'src/app/requests/attribution-ccme-post.request';
import { AttributionCCMEPutRequest } from 'src/app/requests/attribution-ccme-put.request';
import { AttributionCMUPostRequest } from 'src/app/requests/attribution-cmu-post.request';
import { AttributionCMUPutRequest } from 'src/app/requests/attribution-cmu-put.request';
import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { ServiceContractantService } from 'src/app/domaine/business-logic/services-contractants.service';

@Component({
  selector: 'app-create-attribution-commission-competente-service-contractant',
  templateUrl: './create-attribution-commission-competente-service-contractant.html',
  styleUrls: ['./create-attribution-commission-competente-service-contractant.css']
})

export class CreateAttributionCommissionCompetenteServiceContractant  implements OnInit {
  
    constructor(
      public dialogRef: MatDialogRef<CreateAttributionCommissionCompetenteServiceContractant>,
      private commissionCompetenteService: CommissionCompetenteService,
      private serviceContractantService: ServiceContractantService,
      @Inject(MAT_DIALOG_DATA) public data: {
        action: DialogAction, 
        sc: ServiceContractant, 
        typ_comm_comp: TypeCommissionCompetente
      }
    ) {
  
    };
  
    loadingCC: boolean = false;
    //all the SC list
    optionsCC: CommissionCompetente[] = [];
    //the filtered SC list to show to the user
    filteredOptionsCC: Observable<CommissionCompetente[]>;
  
  
    attributionCommissionCompetenteForm: FormGroup;
  
    isCreate : boolean;
    isModify : boolean;
    isDelete : boolean;

  
    // date creation to show (if create: today, if delete of modify: date creation )
    date ;
  
    
    ngOnInit() {


      this.isCreate = this.data.action === DialogAction.CREATE;
      this.isModify = this.data.action === DialogAction.MODIFY;
      this.isDelete = this.data.action === DialogAction.DELETE;

 

      this.attributionCommissionCompetenteForm = new FormGroup({
        'commissionCompetente': new FormGroup({
          '_id' : new FormControl({value: null, disabled: this.isDelete}, [Validators.required],[this.existsCC.bind(this), this.rightCC.bind(this)]),
          'designation' : new FormControl({value: null, disabled: true}, [Validators.required]),
          'typeCommission' : new FormControl({value: null, disabled: true}, [Validators.required])
        })
      });


      if (this.data.action === DialogAction.MODIFY ||
        this.data.action === DialogAction.DELETE ){
          console.log(this.data.sc);
          switch(this.data.typ_comm_comp){
            case TypeCommissionCompetente.CCME:
              this.attributionCommissionCompetenteForm.get("commissionCompetente").patchValue({_id: this.data.sc.CCME._id});
              break;
            case TypeCommissionCompetente.CMU:
              this.attributionCommissionCompetenteForm.get("commissionCompetente").patchValue({_id: this.data.sc.CMU._id});
              break;
            default:
              console.log("Check your code!");
          }


      }


      // load les commissions for admin
      this.loadingCC = true;
      const result:Promise<CommissionCompetente[]> = this.commissionCompetenteService.getAllCommissionsCompetentes();


      result
      .then((value) =>{
        this.loadingCC = false;
        this.optionsCC = value; 

        // recheck once the data is loaded
        const commissionCompetenteForm : AbstractControl =  this.attributionCommissionCompetenteForm.get("commissionCompetente");
        (<FormGroup>commissionCompetenteForm).controls["_id"].updateValueAndValidity();

        // generate the filtered data
        this.filteredOptionsCC = this.attributionCommissionCompetenteForm.get("commissionCompetente").get("_id").valueChanges
        .pipe(
          startWith(''),
          map( valueCC => this._filterCC(valueCC) )
        );

        return;
      })
      .catch(()=>{
        this.loadingCC = false ;
        return this.optionsCC = null;
      });

    };
  
  


    loadingData: boolean = false ;
    errorLoadingData: boolean = false ;
  
    onSubmit() {
      this.loadingData = true ;
      this.errorLoadingData = false ;
      console.log("Submitted", this.attributionCommissionCompetenteForm) ;
  
  
  
      
      // save the data into the database
      let result:Promise<CommissionCompetente>;
      if (this.isCreate){
        switch(this.data.typ_comm_comp){
          case TypeCommissionCompetente.CCME:
            let attributionCCMEPostRequest:AttributionCCMEPostRequest = new AttributionCCMEPostRequest(
              this.attributionCommissionCompetenteForm.value.commissionCompetente._id
            );
            result = this.serviceContractantService.postAttributionCCME(this.data.sc._id, attributionCCMEPostRequest);            
            break;
          case TypeCommissionCompetente.CMU:
            let attributionCMUPostRequest:AttributionCMUPostRequest = new AttributionCMUPostRequest(
              this.attributionCommissionCompetenteForm.value.commissionCompetente._id
            );
            result = this.serviceContractantService.postAttributionCMU(this.data.sc._id, attributionCMUPostRequest);             
            break;
          default:
            console.log("Check your code!");
        }
      };

      if (this.isModify){
        switch(this.data.typ_comm_comp){
          case TypeCommissionCompetente.CCME:
            let attributionCCMEPutRequest:AttributionCCMEPutRequest = new AttributionCCMEPutRequest(
              this.attributionCommissionCompetenteForm.value.commissionCompetente._id
            );
            result = this.serviceContractantService.putAttributionCCME(this.data.sc._id, attributionCCMEPutRequest);            
            break;
          case TypeCommissionCompetente.CMU:
            let attributionCMUPutRequest:AttributionCMUPutRequest = new AttributionCMUPutRequest(
              this.attributionCommissionCompetenteForm.value.commissionCompetente._id
            );
            result = this.serviceContractantService.putAttributionCMU(this.data.sc._id, attributionCMUPutRequest);             
            break;
          default:
            console.log("Check your code!");
        }
      };

  
      if (this.isDelete){
        switch(this.data.typ_comm_comp){
          case TypeCommissionCompetente.CCME:
            result = this.serviceContractantService.deleteAttributionCCME(this.data.sc._id);            
            break;
          case TypeCommissionCompetente.CMU:
            result = this.serviceContractantService.deleteAttributionCMU(this.data.sc._id);             
            break;
          default:
            console.log("Check your code!");
        }
      };


      result
      .then((value) => {
        console.log(value);
        this.loadingData = false;
        this.dialogRef.close(
          value 
        )
      })
      .catch(()=>{
        this.loadingData = false;
        return this.errorLoadingData = true;
      });
    


  
    }
  
  
  
    rightCC(control: FormControl): Promise<any> | Observable<any> {
      const _id = control.value;
      const result : CommissionCompetente = this.getSelectedCC(_id);
      if(!result || !this.data.typ_comm_comp){
        return new Promise<any>(
          (resolve, reject) => {
                resolve(null);
            }
        )
      }
      const typeCommissionSelected: TypeCommissionCompetente = result.typeCommission;
      return new Promise<any>(
        (resolve, reject) => {
            if (TypeCommissionCompetente[typeCommissionSelected] === this.data.typ_comm_comp) {
              resolve(null);
            } else {
              resolve({ "typeCommissionCompetenteInvalid": true });
            }
          }
      )
    }

    existsCC(control: FormControl): Promise<any> | Observable<any> {
      const _id = control.value;
      const result:CommissionCompetente = this.getSelectedCC(_id);
      return new Promise<any>(
        (resolve, reject) => {
            if (result != null) {
              resolve(null);
            } else {
              resolve({ "commissionCompetenteInvalide": true });
            }
          }
      )
    }

    getSelectedCC(_id : string) : CommissionCompetente{
      if (_id){
        return this.optionsCC.find(e => e._id === _id);
      } else {
        return null;
      }
    }

    getSelectedCCDesignation(): string{
      const result = this.getSelectedCC(this.attributionCommissionCompetenteForm.value.commissionCompetente._id);
      if (result) {return result.designation;}
      return "";
    };
  
    getSelectedCCTypeCommission(): string{
      const result = this.getSelectedCC(this.attributionCommissionCompetenteForm.value.commissionCompetente._id);
      if (result) {return result.typeCommission;}
      return "";
    };

  
    private _filterCC(valueInput: string): CommissionCompetente[] {
      const filterValue = valueInput.toLowerCase();
      return this.optionsCC.filter(
        (cc : CommissionCompetente) => {
          let option:string = cc.designation + " " + cc.typeCommission; 
          return option.toLowerCase().includes(filterValue);
        }
      );
    }

 
  }
  