import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable} from '@angular/core';
import { ProjetCDC } from 'src/app/models/projet-cdc.model';
import { ProjetCDCPostRequest } from 'src/app/requests/projet-cdc-post.request';
import { ProjetCDCPutRequest } from 'src/app/requests/projet-cdc-put.request';


@Injectable()
export class ProjetCDCAccess {

  constructor(private http: HttpClient){}


  async getProjetCDC(idProjetCDC: string): Promise<ProjetCDC> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<ProjetCDC>(
          `${apiEndpoint}/projets-cdc/${idProjetCDC}`,
          )
        .subscribe(
          projetCDC => { resolve(projetCDC) },
          error => reject(error)
        )

      });
  }


  async getAllProjetsCDC(): Promise<ProjetCDC[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<ProjetCDC[]>(
          `${apiEndpoint}/projets-cdc`,
          )
        .subscribe(
          projetsCDC => { resolve(projetsCDC) },
          error => reject(error)
        )

      });
  };



  async postProjetCDC(projetCDC: ProjetCDCPostRequest, idElaborationCDC: string): Promise<ProjetCDC> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<ProjetCDC>(
          `${apiEndpoint}/projets-cdc/${idElaborationCDC}`, 
          projetCDC, 
          httpOptions
          )
        .subscribe(
            projetCDC => { resolve(projetCDC) },
            error => reject(error)
        )
      }
    );
  };


  async putProjetCDC(projetCDC: ProjetCDCPutRequest, idProjetCDC: string): Promise<ProjetCDC> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<ProjetCDC>(
          `${apiEndpoint}/projets-cdc/${idProjetCDC}`, 
          projetCDC, 
          httpOptions
          )
        .subscribe(
            projetCDC => { resolve(projetCDC) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteProjetCDC(idElaborationCDC: string, idProjetCDC: string): Promise<ProjetCDC> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<ProjetCDC>(
          `${apiEndpoint}/projets-cdc/${idElaborationCDC}/${idProjetCDC}`
          )
        .subscribe(
            projetCDC => { resolve(projetCDC) },
            error => reject(error)
        )
      }
    );
  }
  
};