import { Injectable } from '@angular/core'; 
import { NotificationOperateurEconomique } from 'src/app/models/notification-operateur-economique.model';
import { NotificationOperateurEconomiqueAccess } from '../data-access/notification-operateur-economique.access';
import { NotificationOperateurEconomiquePutRequest } from 'src/app/requests/notification-operateur-economique-put.request';
import { NotificationOperateurEconomiquePostRequest } from 'src/app/requests/notification-operateur-economique-post.request';

@Injectable()
export class NotificationOperateurEconomiqueService {

  constructor(private notificationOperateurEconomiqueAccess: NotificationOperateurEconomiqueAccess){}
  
  postNotificationOperateurEconomiqueProlongation(idProlongation: string, notification: NotificationOperateurEconomiquePostRequest): Promise<NotificationOperateurEconomique> {
    return this.notificationOperateurEconomiqueAccess.postNotificationOperateurEconomiqueProlongation(idProlongation, notification);
  }

  postNotificationOperateurEconomique(idLancementConcurrence: string, notification: NotificationOperateurEconomiquePostRequest): Promise<NotificationOperateurEconomique> {
    return this.notificationOperateurEconomiqueAccess.postNotificationOperateurEconomique(idLancementConcurrence, notification);
  }

  putNotificationOperateurEconomique(idNotification: string, notification: NotificationOperateurEconomiquePutRequest): Promise<NotificationOperateurEconomique> {
    return this.notificationOperateurEconomiqueAccess.putNotificationOperateurEconomique(idNotification, notification);
  }

  deleteNotificationOperateurEconomique(idLancementConcurrence: string, idNotification: string):Promise<NotificationOperateurEconomique> {
    return this.notificationOperateurEconomiqueAccess.deleteNotificationOperateurEconomique(idLancementConcurrence, idNotification);
  }

  deleteNotificationOperateurEconomiqueProlongation(idProlongation: string, idNotification: string):Promise<NotificationOperateurEconomique> {
    return this.notificationOperateurEconomiqueAccess.deleteNotificationOperateurEconomiqueProlongation(idProlongation, idNotification);
  }

}



