export class Avenant {
    num: string
    nature: string
    objet: string
    montant: number // +/- XXX dinars
    delai: number // +/- XX jours
    negociation: string
    
    getTauxSelonMontant(montantInitial){
        return "-10"
    }

    constructor(
        num: string,
        nature: string,
        objet: string,
        montant: number,
        delai: number,
        negociation: string
    ) {
        this.num = num,
        this.nature= nature,
        this.objet = objet,
        this.montant = montant,
        this.delai = delai,
        this.negociation = negociation
    }
    
    static getDummy(): Avenant {
        return new Avenant(
            "1",
            "prestation complémentaire",
            "modifier clause 1",
            -500,
            -15,
            "negociation"
        )
    }
}