import { Component, Input, ViewChild } from '@angular/core';
import { MatDialog, MatTable } from '@angular/material';
import { CreateDemandeClarificationComponent } from 'src/app/dialogs/create-demande-clarification/create-demande-clarification.component';
import { ClarificationOffre } from 'src/app/models/clarification.offre.model';
import { CreatePassageComiteTechniqueComponent } from 'src/app/dialogs/create-passage-comite-technique/create-passage-comite-technique.component';
import { PassageComiteTechnique } from 'src/app/models/passage.comite.technique.model';
import { CreateResultatCopeoEvaluationOffresComponent } from 'src/app/dialogs/create-resultat-copeo-evaluation-offres/create-resultat-copeo-evaluation-offres.component';
import { Lancement } from 'src/app/models/lancement/lancement.model';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { Projet } from 'src/app/models/repertoire/projet.model';


@Component({
  selector: 'app-evaluation-offres',
  templateUrl: './evaluation_offres.component.html',
  styleUrls: ['./evaluation_offres.component.css']
})

export class EvaluationOffresComponent {
  
  @ViewChild("tableClarificationsOffres", {static: true}) tableClarificationsOffres: MatTable<ClarificationOffre>;
  @ViewChild("tablePassagesComitesTechniques", {static: true}) tablePassagesComitesTechniques: MatTable<PassageComiteTechnique>;

  @Input() lancement : Lancement;
  @Input() elaborationCDC : ElaborationCDC;
  @Input() sousProjet : SousProjet;
  @Input() projet : Projet;
  
  dataSourcePassagesComitesTechniques: PassageComiteTechnique [] = []
  displayedColumnsPassagesComitesTechniques: string[] = [
    "comiteTechnique",
    "dateSaisie",    
    "demandePath",
    "reponse"
    ];

  dataSourceClarificationsOffres: ClarificationOffre [] = []
  displayedColumnsClarificationsOffres: string[] = [
    "soumissionnaire",
    "demande",
    "dateNotification",
    "moyenNotification",
    "reponse"
    ];
    

    
  constructor(public dialog: MatDialog) { }


  ngOnInit() {

  }

  openDialogCreateReponseComplementOffre(){

  }
 
  openDialogCreateDemandeClarification(){
    const dialogRef = this.dialog.open(CreateDemandeClarificationComponent, {
      width: '800px',
      data: "resultat commission ouverture plis"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
    });    
    this.dataSourceClarificationsOffres.push(ClarificationOffre.getDummy())
    this.tableClarificationsOffres.renderRows();    
  }

  openDialogCreatePassageComiteTechnique(){
    const dialogRef = this.dialog.open(CreatePassageComiteTechniqueComponent, {
      width: '800px',
      data: "ajouter passage CT"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
    });    
    this.dataSourcePassagesComitesTechniques.push(PassageComiteTechnique.getDummy())
    this.tablePassagesComitesTechniques.renderRows(); 
  }

  openDialogCreateResultatCOPEOEvalutionOffres():void{
    const dialogRef = this.dialog.open(CreateResultatCopeoEvaluationOffresComponent, {
      width: '800px',
      data: "passage commission ouverture plis"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
  }


}
