import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { UploadService } from 'src/app/upload.service';
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { OperateurEconomiqueService } from 'src/app/domaine/business-logic/operateurs-economiques.service';
import { OperateurEconomiquePostRequest } from 'src/app/requests/operateur-economique-post.request';
import { OperateurEconomiquePutRequest } from 'src/app/requests/operateur-economique-put.request';
import { FormeJuridique } from 'src/app/enums/forme.juridique.enum';
import { fileUploadImageUrl } from 'src/app/config';
import { Observable } from 'rxjs';


@Component({
  selector: 'app-create-operateur-economique',
  templateUrl: './create-operateur-economique.component.html',
  styleUrls: ['./create-operateur-economique.component.css']
})
export class CreateOperateurEconomiqueComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<CreateOperateurEconomiqueComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, oper_econ: OperateurEconomique},
    private uploadService: UploadService,
    public operateurEconomiqueService: OperateurEconomiqueService
  ) {

  };




  operateurEconomiqueForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;
  isConfirm: boolean;


  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;
    this.isConfirm = this.data.action === DialogAction.CONFIRM;

    //set the date of creation
    if (this.isCreate) {
      this.date = new FormControl(new Date());
    } else {
      this.date = new FormControl(this.data.oper_econ.dateCreation);
    }




    this.operateurEconomiqueForm = new FormGroup({
      'dateCreationOperateurEconomique': new FormControl({value: null, disabled: true}, [Validators.required]),
      'denomination': new FormControl({value: null, disabled: this.isDelete || this.isConfirm}, [Validators.required]),
      'nif': new FormControl({value: null, disabled: this.isDelete || this.isConfirm}, [Validators.required], this.nifExists.bind(this)),
      'formeJuridique': new FormControl({value: null, disabled: this.isDelete || this.isConfirm}, [Validators.required]),
      'estExclu': new FormControl({value: false, disabled: this.isDelete || this.isConfirm}, [Validators.required]),
      'pathLogo': new FormControl({value: "default-company-logo.png", disabled: false}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ||
      this.data.action === DialogAction.CONFIRM 
      ){
      this.operateurEconomiqueForm.patchValue(this.data.oper_econ);
    }

  }



  //FormeJuridique
  formesJuridiques = Object.keys(FormeJuridique).slice(0);
  parseValueFormeJuridique(value: string){
    return FormeJuridique[value].toString();
  }



  //Data model to submit to the DB server
  operateurEconomiquePostRequest: OperateurEconomiquePostRequest;
  operateurEconomiquePutRequest: OperateurEconomiquePutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.operateurEconomiqueForm);




    // save the data into the database
    let result:Promise<OperateurEconomique>;
    if (this.isCreate){

      this.operateurEconomiquePostRequest = new OperateurEconomiquePostRequest(
        this.operateurEconomiqueForm.value.denomination,
        this.operateurEconomiqueForm.value.nif,
        this.operateurEconomiqueForm.value.formeJuridique,
        this.operateurEconomiqueForm.value.estExclu,
        this.operateurEconomiqueForm.value.pathLogo
      ); 

      result = this.operateurEconomiqueService.postOperateurEconomique(this.operateurEconomiquePostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.operateurEconomiquePutRequest = new OperateurEconomiquePutRequest(
        this.operateurEconomiqueForm.value.denomination,
        this.operateurEconomiqueForm.value.nif,
        this.operateurEconomiqueForm.value.formeJuridique,
        this.operateurEconomiqueForm.value.estExclu,
        this.operateurEconomiqueForm.value.pathLogo
      );      
      result = this.operateurEconomiqueService.putOperateurEconomique(this.operateurEconomiquePutRequest, this.data.oper_econ._id);
    };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.operateurEconomiqueService.deleteOperateurEconomique(this.data.oper_econ._id);
    };

    if(this.isConfirm){
      result = this.operateurEconomiqueService.putConfirmOperateurEconomique(this.data.oper_econ._id);
    };

    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value 
        /*
        {
          _id: value._id , 
          intituleProjet : value.intituleProjet,
          codeProjet: value.codeProjet,
          montantProjet: value.montantProjet,
          dateCreationProjet: value.dateCreationProjet,
          yearPlanAction: value.yearPlanAction,
          pourLaStructureRattachement: value.pourLaStructureRattachement,
          sourcesFinancement: value.sourcesFinancement,
          siAutreSourceFinancement: value.siAutreSourceFinancement,
          budget: value.budget,
          isHorsPlan: value.isHorsPlan,
          pathJustificatifProjet: value.pathJustificatifProjet,
          sousProjets : value.sousProjets
        }*/
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingImage = false;
  onlyPngAllowed = false;
  failedUploadImage = false;

  onFileChange(event) {
    this.uploadingImage = true;
    this.failedUploadImage = false;
    this.onlyPngAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'image/png'){
      this.onlyPngAllowed = true;
      this.uploadingImage = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.uploadImage(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.operateurEconomiqueForm.patchValue({'pathLogo' : val.filename});
        }
        this.uploadingImage = false;
      }).catch((err =>{
        this.failedUploadImage = true;
        this.uploadingImage = false;
      }));
    }
    
  }


  getLogoPath(filename:string): string {
    if (filename=="default-company-logo.png") return "assets/img/default-company-logo.png";
    else return `${fileUploadImageUrl}/sendMePNG/${filename}`;
  }






  nifExists(control: FormControl): Promise<any> | Observable<any> {
    const nif = control.value;

    // if action is modify or delete, and identifiant not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY||this.data.action === DialogAction.DELETE) && nif === this.data.oper_econ.nif)
    return new Promise<any>((resolve, reject) =>  resolve(null))

    const result:Promise<OperateurEconomique> = this.operateurEconomiqueService.getOperateurEconomiqueByNIF(nif);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(sc => {
          console.log("sc", sc)
          if (sc != null) {
            resolve({ "nifExist": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }




}
