import { PassageCommissionMarches } from './passage-commission/passage.commission.marches.model';

export class AdditifCDC {
    _id: string;
    pathAdditifCDC: string;
    passagesCommissionMarches: PassageCommissionMarches[];

    constructor(
        pathAdditifCDC: string
    ){
        this.pathAdditifCDC = pathAdditifCDC;
        this.passagesCommissionMarches = [];
    }

    static getDummy(): AdditifCDC {
        return new AdditifCDC(
            "N: "+Math.random().toString()
        )
    }
}