import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { ElaborationCDCPostRequest } from 'src/app/requests/elaboration-cdc-post.request';
import { ElaborationCDCPutRequest } from 'src/app/requests/elaboration-cdc-put.request';


@Injectable()
export class ElaborationCDCAccess {

  constructor(private http: HttpClient){}


  async getElaborationCDC(idElaborationCDC: string): Promise<ElaborationCDC> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<ElaborationCDC>(
          `${apiEndpoint}/elaborations-cdc/${idElaborationCDC}`,
          )
        .subscribe(
          elaborationCDC => { resolve(elaborationCDC) },
          error => reject(error)
        )

      });
  }


  async getAllElaborationsCDC(): Promise<ElaborationCDC[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<ElaborationCDC[]>(
          `${apiEndpoint}/elaborations-cdc`,
          )
        .subscribe(
          elaborationsCDC => { resolve(elaborationsCDC) },
          error => reject(error)
        )

      });
  };



  async postElaborationCDC(elaborationCDC: ElaborationCDCPostRequest, idSousProjet: string): Promise<ElaborationCDC> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<ElaborationCDC>(
          `${apiEndpoint}/elaborations-cdc/${idSousProjet}`, 
          elaborationCDC, 
          httpOptions
          )
        .subscribe(
            elaborationCDC => { resolve(elaborationCDC) },
            error => reject(error)
        )
      }
    );
  };


  async putElaborationCDC(elaborationCDC: ElaborationCDCPutRequest, idSousProjet: string): Promise<ElaborationCDC> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<ElaborationCDC>(
          `${apiEndpoint}/elaborations-cdc/${idSousProjet}`, 
          elaborationCDC, 
          httpOptions
          )
        .subscribe(
            elaborationCDC => { resolve(elaborationCDC) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteElaborationCDC(idSousProjet: string, idElaborationCDC: string): Promise<ElaborationCDC> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<ElaborationCDC>(
          `${apiEndpoint}/elaborations-cdc/${idSousProjet}/${idElaborationCDC}`
          )
        .subscribe(
            elaborationCDC => { resolve(elaborationCDC) },
            error => reject(error)
        )
      }
    );
  }
  
};