import { Injectable } from '@angular/core';
import { CalendarEntry } from 'src/app/models/calendar.entry.model';
import { CalendarEntryAccess } from '../data-access/calendar-entry.access';
import { CalendarEntryPostRequest } from 'src/app/requests/calendar-entry-post.request';
import { CalendarEntryPutRequest } from 'src/app/requests/calendar-entry-put.request';

@Injectable()
export class CalendarEntryService {

  constructor(private calendarEntryAccess: CalendarEntryAccess){}

  getAllCalendarEntries(): Promise<CalendarEntry[]>{
    return this.calendarEntryAccess.getAllCalendarEntries();
  }

  getProchainJourOuvrable(date: Date):Promise<CalendarEntry[]>{
    return this.calendarEntryAccess.getProchainJourOuvrable(date);
  }
  postCalendarEntry(calendarEntry: CalendarEntryPostRequest): Promise<CalendarEntry>{
    return this.calendarEntryAccess.postCalendarEntry(calendarEntry);
  }

  putCalendarEntry(calendarEntry: CalendarEntryPutRequest, id: String): Promise<CalendarEntry>{
    return this.calendarEntryAccess.putCalendarEntry(calendarEntry, id);
  }

  deleteCalendarEntry(id: String):Promise<CalendarEntry>{
    return this.calendarEntryAccess.deleteCalendarEntry(id);
  }

}



