  async postNotificationCOPEO(idCOPEO: string, notificationCOPEOPostRequest: NotificationCOPEOPostRequest): Promise<NotificationCOPEO> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<NotificationCOPEO>(
          `${apiEndpoint}/notifications-copeos/${idCOPEO}`, 
          notificationCOPEOPostRequest, 
          httpOptions
          )
        .subscribe(
            notif => { resolve(notif) },
            error => reject(error)
        )
      }
    );
  };

