import { Component, Input, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { CreateElaborationCDCComponent } from 'src/app/dialogs/create-elaboration-cdc/create-elaboration-cdc.component';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { Projet } from 'src/app/models/repertoire/projet.model';


@Component({
  selector: 'app-passation',
  templateUrl: './passation.component.html',
  styleUrls: ['./passation.component.css']
})

export class PassationComponent  implements OnInit {

  @Input() projet: Projet;
  @Input() idSousProjet: string;
  sousProjet: SousProjet;
  
  constructor(public dialog: MatDialog) {
    console.log("constructor Passation ran");

   }
  ngOnInit(): void {
    this.sousProjet = this.projet.sousProjets.find(e => e._id === this.idSousProjet);
    console.log("Niveau", this.projet.serviceContractant.niveau);
  }



  
  openDialogCreateElaborerMarche(): void {

    const dialogRef = this.dialog.open(CreateElaborationCDCComponent, {
      width: '800px',
      data: {action: DialogAction.CREATE, elbCDC: null, sousPrj: this.sousProjet, prj: this.projet}
    });

    dialogRef.afterClosed().subscribe(result => {
      if (this.sousProjet && result) {
        this.sousProjet.elaborationsCDC.push(result);
      }
    });
  }



}
