import { Injectable } from '@angular/core';
import { CanalAffichage } from 'src/app/models/canal-affichage.model';
import { CanalAffichagePostRequest } from 'src/app/requests/canal-affichage-post.request';
import { CanalAffichagePutRequest } from 'src/app/requests/canal-affichage-put.request';
import { CanalAffichageAccess } from '../data-access/canal-affichage.access';

@Injectable()
export class CanalAffichageService {

  constructor(private canalAffichageAccess: CanalAffichageAccess){}

  postCanalAffichageProlongation(idProlongation: string, canalAffichage: CanalAffichagePostRequest): Promise<CanalAffichage> {
    return this.canalAffichageAccess.postCanalAffichageProlongation(idProlongation, canalAffichage);
  }

  postCanalAffichage(idLancementConcurrence: string, canalAffichage: CanalAffichagePostRequest): Promise<CanalAffichage> {
    return this.canalAffichageAccess.postCanalAffichage(idLancementConcurrence, canalAffichage);
  }

  putCanalAffichage(idCanalAffichage: string, canalAffichagePutRequest: CanalAffichagePutRequest): Promise<CanalAffichage> {
    return this.canalAffichageAccess.putCanalAffichage(idCanalAffichage, canalAffichagePutRequest);
  }

  deleteCanalAffichage(idLancementConcurrence: String, idCanalAffichage: string):Promise<CanalAffichage> {
    return this.canalAffichageAccess.deleteCanalAffichage(idLancementConcurrence, idCanalAffichage);
  }

  deleteCanalAffichageProlongation(idProlongation: String, idCanalAffichage: string):Promise<CanalAffichage> {
    return this.canalAffichageAccess.deleteCanalAffichageProlongation(idProlongation, idCanalAffichage);
  }
}



