import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { UploadService } from 'src/app/upload.service';

import { CanalAffichage } from 'src/app/models/canal-affichage.model';
import { TypeCanalAffichage } from 'src/app/enums/type-canal-affichage.enum';
import { CanalAffichagePostRequest } from 'src/app/requests/canal-affichage-post.request';
import { CanalAffichagePutRequest } from 'src/app/requests/canal-affichage-put.request';
import { CanalAffichageService } from 'src/app/domaine/business-logic/canaux-affichage.service';
import { Prolongation } from 'src/app/models/prolongation.model';


@Component({
  selector: 'app-create-canal-affichage',
  templateUrl: './create-canal-affichage.component.html',
  styleUrls: ['./create-canal-affichage.component.css']
})

export class CreateCanalAffichageComponent implements OnInit {





  constructor(
    public dialogRef: MatDialogRef<CreateCanalAffichageComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, 
      cnl_aff: CanalAffichage
      id_lcm_cnrr: string
      is_prlng : boolean
      prlng : Prolongation      
    },
    private uploadService: UploadService,
    public canalAffichageService: CanalAffichageService
  ) {



  };




  canalAffichageForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;



  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.cnl_aff.dateCreation);
    }




    this.canalAffichageForm = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
      'dateParution':new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'denomination': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'type': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'pathJustificatif': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.canalAffichageForm.patchValue(this.data.cnl_aff);
    }



  };





  //Type
  typesCanauxAffichage = Object.keys(TypeCanalAffichage).slice(0);
  parseValueTypeCanalAffichage(value : string) {
    return TypeCanalAffichage[value].toString();
  }







  //Data model to submit to the DB server
  canalAffichagePostRequest: CanalAffichagePostRequest;
  canalAffichagePutRequest: CanalAffichagePutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.canalAffichageForm);




    // save the data into the database
    let result:Promise<CanalAffichage>;
    if (this.isCreate){
      this.canalAffichagePostRequest = new CanalAffichagePostRequest(
        this.canalAffichageForm.value.dateParution,
        this.canalAffichageForm.value.denomination,
        this.canalAffichageForm.value.type,
        this.canalAffichageForm.value.pathJustificatif
      );
      if (this.data.is_prlng)
        result = this.canalAffichageService.postCanalAffichageProlongation(this.data.prlng._id, this.canalAffichagePostRequest);
      else 
        result = this.canalAffichageService.postCanalAffichage(this.data.id_lcm_cnrr, this.canalAffichagePostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.canalAffichagePutRequest = new CanalAffichagePutRequest(
        this.canalAffichageForm.value.dateParution,
        this.canalAffichageForm.value.denomination,
        this.canalAffichageForm.value.type,
        this.canalAffichageForm.value.pathJustificatif
      );      
      result = this.canalAffichageService.putCanalAffichage(this.data.cnl_aff._id, this.canalAffichagePutRequest);
    };

    // delete le S/C data from the database
    if(this.isDelete){

      if (this.data.is_prlng)
        result = this.canalAffichageService.deleteCanalAffichageProlongation(this.data.prlng._id, this.data.cnl_aff._id);
      else
        result = this.canalAffichageService.deleteCanalAffichage(this.data.id_lcm_cnrr, this.data.cnl_aff._id);
    };

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value/*
        {
          _id: value._id , 
          serviceContractant: {
            _id: value.serviceContractant._id,
            identifiant: value.serviceContractant.identifiant,
            nomStructure: value.serviceContractant.nomStructure
          },
          intituleProjet : value.intituleProjet,
          codeProjet: value.codeProjet,
          montantProjet: value.montantProjet,
          dateCreationProjet: value.dateCreationProjet,
          yearPlanAction: value.yearPlanAction,
          pourLaStructureRattachement: value.pourLaStructureRattachement,
          sourcesFinancement: value.sourcesFinancement,
          siAutreSourceFinancement: value.siAutreSourceFinancement,
          budget: value.budget,
          isHorsPlan: value.isHorsPlan,
          pathJustificatifProjet: value.pathJustificatifProjet,
          sousProjets : value.sousProjets
        }
        */
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.canalAffichageForm.patchValue({'pathJustificatif' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}




