import { Component, OnInit, Inject } from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { NaturePrestation } from 'src/app/enums/nature.prestation.enum';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { SousProjetService } from 'src/app/domaine/business-logic/sous-projets.service';
import { SousProjetPostRequest } from 'src/app/requests/sous-projet-post.request';
import { SousProjetPutRequest } from 'src/app/requests/sous-projet-put.request';


@Component({
  selector: 'app-create-sous-projet',
  templateUrl: './create-sous-projet.component.html',
  styleUrls: ['./create-sous-projet.component.css']
})
export class CreateSousProjetComponent implements OnInit {
 

  //NaturePrestation
  naturePrestation = Object.keys(NaturePrestation).slice(0)
  parseValueNaturePrestation(value : string) {
    return NaturePrestation[value].toString();
  }
  


  constructor(public dialogRef: MatDialogRef<CreateSousProjetComponent>,
    private sousProjetService: SousProjetService,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, prj: Projet, sousPrj: SousProjet}
    ) { }


  sousProjetForm: FormGroup;
  
  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    } else {
      this.date = new FormControl(this.data.sousPrj.dateCreationSousProjet);
    }

    this.sousProjetForm = new FormGroup({
      'dateCreationSousProjet':new FormControl({value: null, disabled: true}, [Validators.required]),
      'intituleSousProjet': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'estimationAdministrative': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'naturePrestation': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });


    if(this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.sousProjetForm.patchValue(this.data.sousPrj);
    }
  }





  
  //Data model to submit to the DB server
  sousProjetPostRequest: SousProjetPostRequest;
  sousProjetPutRequest: SousProjetPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.sousProjetForm);




    // save the data into the database
    let result:Promise<SousProjet>;
    if (this.isCreate){
      this.sousProjetPostRequest = new SousProjetPostRequest(
        this.sousProjetForm.value.intituleSousProjet,
        this.sousProjetForm.value.naturePrestation,
        this.sousProjetForm.value.estimationAdministrative
      );      
      result = this.sousProjetService.postSousProjet(this.sousProjetPostRequest, this.data.prj._id);
    };

    // update the data in the database
    if(this.isModify){
      this.sousProjetPutRequest = new SousProjetPutRequest(
        this.sousProjetForm.value.intituleSousProjet,
        this.sousProjetForm.value.naturePrestation,
        this.sousProjetForm.value.estimationAdministrative
      );      
      result = this.sousProjetService.putSousProjet(this.sousProjetPutRequest, this.data.sousPrj._id);
    };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.sousProjetService.deleteSousProjet(this.data.prj._id, this.data.sousPrj._id);
    };

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        {
          _id: value._id ,
          dateCreationSousProjet: value.dateCreationSousProjet,
          intituleSousProjet : value.intituleSousProjet,
          estimationAdministrative: value.estimationAdministrative,
          naturePrestation: value.naturePrestation
        }
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }
}
