export class SousTraitant {
    sousTraitant: string
    objetSoustraitance: string
    dateDebutIntervention: Date
    dateFinIntervention: Date 
    declarationSoustraitantPath: string 
    tauxIntervention: number 

    constructor (    
        sousTraitant: string,
        objetSoustraitance: string,
        dateDebutIntervention: Date,
        dateFinIntervention: Date ,
        declarationSoustraitantPath: string,
        tauxIntervention: number 
    ){
        this.sousTraitant= sousTraitant,
        this.objetSoustraitance= objetSoustraitance,
        this.dateDebutIntervention= dateDebutIntervention,
        this.dateFinIntervention= dateFinIntervention ,
        this.declarationSoustraitantPath= declarationSoustraitantPath ,
        this.tauxIntervention= tauxIntervention 
    }
    static getDummy(): SousTraitant {
        return new SousTraitant(
            "sousTraitant",
            "objetSoustraitance",
            new Date(),
            new Date(),
            "declarationSoustraitantPath",
            30
        )
      }
}