import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { ServiceContractantAccess } from '../data-access/service-contractant.access';
import { Injectable } from '@angular/core';
import { Candidat } from 'src/app/models/candidat.model';
import { CandidatAccess } from '../data-access/candidat.access';
import { CandidatPostRequest } from 'src/app/requests/candidat-post.request';
import { CandidatPutRequest } from 'src/app/requests/candidat-put.request';

@Injectable()
export class CandidatService {

  constructor(private candidatAccess: CandidatAccess){}

  getAllCandidats(): Promise<Candidat[]>{
    return this.candidatAccess.getAllCandidats();
  }

  getCandidatByNumero(idRetraitCDC: string, numero: string): Promise<Candidat>{
    return this.candidatAccess.getCandidatByNumero(idRetraitCDC, numero);
  }

  getCandidatByOperEconId(idRetraitCDC: string, idOperateurEconomique: string): Promise<Candidat>{
    return this.candidatAccess.getCandidatByOperEconId(idRetraitCDC, idOperateurEconomique);
  }
  
  postCandidat(idRetraitCDC: string, candidatPostRequest: CandidatPostRequest): Promise<Candidat>{
    return this.candidatAccess.postCandidat(idRetraitCDC, candidatPostRequest);
  }
  
  putCandidat(idRetraitCDC: string, idCandidat: string, candidatPutRequest: CandidatPutRequest): Promise<Candidat>{
    return this.candidatAccess.putCandidat(idRetraitCDC, idCandidat, candidatPutRequest);
  }
  
  deleteCandidat(idRetraitCDC: string, idCandidat: string):Promise<Candidat>{
    return this.candidatAccess.deleteCandidat(idRetraitCDC, idCandidat);
  }
  
}



