import { Component, OnInit } from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

export interface DialogData {
  animal: string;
  name: string;
}

@Component({
  selector: 'app-create-avenant',
  templateUrl: './create-avenant.component.html',
  styleUrls: ['./create-avenant.component.css']
})
export class CreateAvenantComponent implements OnInit {
  disabled : boolean = false;
  intituleOperation : string;// "Intitulé de l'opération"
  intituleProjet : string;// "Intitulé du projet"
  checkedIntituleProjetDifferent : boolean = false;
  
  constructor(
    public dialogRef: MatDialogRef<CreateAvenantComponent>) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngOnInit() {
  }

  disableIt(){
    this.disabled = !this.disabled;
  }

  onEditIntituleOperation(event: Event){
    this.intituleOperation = (<HTMLInputElement>event.target).value;
    if(!this.checkedIntituleProjetDifferent){
      this.intituleProjet = this.intituleOperation;
    }
  }

}
