export class ReponseEclaircissement {
    dateCreation: Date 
    dateReponse: Date
    reponsePath: string
    candidatsNotifies: string[]

    constructor(
        dateReponse: Date,
        reponsePath: string,
        candidatsNotifies: string[]
    ){
        this.dateCreation = new Date();
        this.dateReponse = dateReponse;
        this.reponsePath = reponsePath;
        this.candidatsNotifies = candidatsNotifies;
    }

    static getDummy(){
        return new ReponseEclaircissement(
            new Date(),
            "reponsePath",
            ["test"]
        );
    }
}