import { Component, OnInit, Inject } from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { NaturePrestation } from 'src/app/enums/nature.prestation.enum';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { UploadService } from 'src/app/upload.service';
import { ModificationCOPEO } from 'src/app/models/copeo/modification-copeo.model';
import { COPEO } from 'src/app/models/copeo/copeo.model';
import { ModificationCOPEOPostRequest } from 'src/app/requests/modification-copeo-post.request';
import { ModificationCOPEOPutRequest } from 'src/app/requests/modification-copeo-put.request';
import { COPEOService } from 'src/app/domaine/business-logic/copeos.service';


@Component({
  selector: 'app-create-modification-copeo',
  templateUrl: './create-modification-copeo.component.html',
  styleUrls: ['./create-modification-copeo.component.css']
})
export class CreateModificationCOPEOComponent implements OnInit {
 

  //NaturePrestation
  naturePrestation = Object.keys(NaturePrestation).slice(0)
  parseValueNaturePrestation(value : string) {
    return NaturePrestation[value].toString();
  }
  


  constructor(public dialogRef: MatDialogRef<CreateModificationCOPEOComponent>,
    private copeoService: COPEOService,
    private uploadService: UploadService,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      copeo: COPEO,
       modif_copeo: ModificationCOPEO}
    ) { }


  modificationCOPEOForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;
 

  // date creation to show (if create: today, if delete of modify: date creation )
  date;


  displayedColumnsMembres: string[] = ['role', 'nom', 'prenom', 'qualite', 'matricule', 'email', 'telephone', 'penta'];

 
  
  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    } else {
      this.date = new FormControl(this.data.modif_copeo.dateCreation);
    }

    this.modificationCOPEOForm = new FormGroup({
      'dateCreation':new FormControl({value: null, disabled: true}, [Validators.required]),
      'numModification': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'numDecision': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'pathJustificatifDecision': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });


    if(this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.modificationCOPEOForm.patchValue(this.data.modif_copeo);
    }
  
  }

  

  
  //Data model to submit to the DB server
  modificationCOPEOPostRequest: ModificationCOPEOPostRequest;
  modificationCOPEOPutRequest: ModificationCOPEOPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.modificationCOPEOForm);




    // save the data into the database
    let result:Promise<ModificationCOPEO>;
    if (this.isCreate){
      this.modificationCOPEOPostRequest = new ModificationCOPEOPostRequest(
        this.modificationCOPEOForm.value.numModification,
        this.modificationCOPEOForm.value.numDecision,
        this.modificationCOPEOForm.value.pathJustificatifDecision
      );      
      result = this.copeoService.postModificationCOPEO(this.data.copeo._id, this.modificationCOPEOPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.modificationCOPEOPutRequest = new ModificationCOPEOPutRequest(
        this.modificationCOPEOForm.value.numModification,
        this.modificationCOPEOForm.value.numDecision,
        this.modificationCOPEOForm.value.pathJustificatifDecision
      );      
      result = this.copeoService.putModificationCOPEO(this.data.modif_copeo._id, this.modificationCOPEOPutRequest);
    };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.copeoService.deleteModificationCOPEO(this.data.copeo._id, this.data.modif_copeo._id);
    };

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });

  }




  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.modificationCOPEOForm.patchValue({'pathJustificatifDecision' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }  

}
