import { Component, OnInit, Inject } from '@angular/core';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { Observable } from 'rxjs';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { RepresentantOperateurEconomique } from 'src/app/models/representant.operateur.economique.model';
import { RepresentantOperateurEconomiqueService } from 'src/app/domaine/business-logic/representants-operateurs-economiques.service';
import { Civilite } from 'src/app/enums/civilite.enum';
import { RepresentantOperateurEconomiquePostRequest } from 'src/app/requests/representant-operateur-economique-post.request';
import { RepresentantOperateurEconomiquePutRequest } from 'src/app/requests/representant-operateur-economique-put.request';



@Component({
  selector: 'app-create-representant-operateur-economique',
  templateUrl: './create-representant-operateur-economique.component.html',
  styleUrls: ['./create-representant-operateur-economique.component.css']
})
export class CreateRepresentantOperateurEconomiqueComponent implements OnInit {

  INFO_TEL = "Forme: 023232323 ou +21323232323. Si plusieurs numéros alors séparez-les par des virgules ',' Ex: 023102030,021102030";
  PHONENUMBER_PATTERN: string = "^[+]*[0-9]+(,[+]*[0-9]+)*$"

  constructor(public dialogRef: MatDialogRef<CreateRepresentantOperateurEconomiqueComponent>,
    private representantOperateurEconomiqueService: RepresentantOperateurEconomiqueService,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      rprst: RepresentantOperateurEconomique, 
      id_oper_econ: string,
      id_cnd: string}
    ) { }


  representantOperateurEconomiqueForm: FormGroup
  
  isCreate : boolean
  isModify: boolean
  isDelete: boolean
  isView: boolean 

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE
    this.isModify = this.data.action === DialogAction.MODIFY
    this.isDelete = this.data.action === DialogAction.DELETE
    this.isView = this.data.action === DialogAction.VIEW

    this.representantOperateurEconomiqueForm = new FormGroup({
      'nom': new FormControl({value: null, disabled: this.isDelete || this.isView}, [Validators.required]),
      'prenom': new FormControl({value: null, disabled: this.isDelete || this.isView}, [Validators.required]),
      'civilite': new FormControl({value: null, disabled: this.isDelete || this.isView}, [Validators.required]),
      'qualite': new FormControl({value: null, disabled: this.isDelete || this.isView}, [Validators.required]),
      'email': new FormControl({value: null, disabled: this.isDelete || this.isView}, [Validators.required, Validators.email]/*, [this.emailExists.bind(this)]*/),
      'telephone': new FormControl({value: null, disabled: this.isDelete || this.isView}, [Validators.required, Validators.pattern(this.PHONENUMBER_PATTERN)]),
      'portable': new FormControl({value: null, disabled: this.isDelete || this.isView}, [Validators.required, Validators.pattern(this.PHONENUMBER_PATTERN)]),
      'fax': new FormControl({value: null, disabled: this.isDelete || this.isView}, [Validators.required, Validators.pattern(this.PHONENUMBER_PATTERN)]),
      'nationalite': new FormControl({value: null, disabled: this.isDelete || this.isView}, [Validators.required])
    })


    //this.serviceContractantForm.setValue
    if(this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ||
      this.data.action === DialogAction.VIEW){
      this.representantOperateurEconomiqueForm.patchValue(this.data.rprst)
      this.representantOperateurEconomiqueForm.patchValue({
          "telephone": this.data.rprst.telephone.toString(),
          "portable": this.data.rprst.portable.toString(),
          "fax": this.data.rprst.fax.toString()
        })
    }
  }

  emailExists(control: FormControl): Promise<any> | Observable<any> {

    const email = control.value;

    // if action is modify od delete, and email not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE || this.data.action === DialogAction.VIEW) && 
    email === this.data.rprst.email)
    return new Promise<any>((resolve, reject) =>  resolve(null))

    const result:Promise<RepresentantOperateurEconomique> = this.representantOperateurEconomiqueService.getRepresentantByEmail(email);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(rpr => {
          if (rpr != null) {
            resolve({ "emailExist": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }


  //Civilite
  civilites = Object.keys(Civilite).slice(0)
  parseValueCivilite(value: string) {
    return Civilite[value].toString();
  }



  loadingData: boolean= false
  errorLoadingData: boolean = false

  onSubmit() {
    this.loadingData = true
    this.errorLoadingData = false
    console.log("Submitted", this.representantOperateurEconomiqueForm);


    const telephone: string[] = this.representantOperateurEconomiqueForm.value.telephone.split(",");
    const portable: string[] = this.representantOperateurEconomiqueForm.value.portable.split(",");
    const fax: string[] = this.representantOperateurEconomiqueForm.value.fax.split(",");





    // save the data into the database
    let result:Promise<RepresentantOperateurEconomique>;
    if (this.isCreate){
      let representantOperateurEconomiquePostRequest = new RepresentantOperateurEconomiquePostRequest(
        this.representantOperateurEconomiqueForm.value.nom,
        this.representantOperateurEconomiqueForm.value.prenom,
        this.representantOperateurEconomiqueForm.value.civilite,
        this.representantOperateurEconomiqueForm.value.qualite,
        this.representantOperateurEconomiqueForm.value.email,
        telephone,
        portable,
        fax,
        this.representantOperateurEconomiqueForm.value.nationalite
      );
      if(this.data.id_cnd==null)
        result = this.representantOperateurEconomiqueService.postRepresentant(this.data.id_oper_econ, representantOperateurEconomiquePostRequest);
      else 
        result = this.representantOperateurEconomiqueService.postRepresentantBySC(this.data.id_cnd, this.data.id_oper_econ, representantOperateurEconomiquePostRequest);      
    }

    // update the data in the database
    if(this.isModify){
      let representantOperateurEconomiquePutRequest = new RepresentantOperateurEconomiquePutRequest(
        this.representantOperateurEconomiqueForm.value.nom,
        this.representantOperateurEconomiqueForm.value.prenom,
        this.representantOperateurEconomiqueForm.value.civilite,
        this.representantOperateurEconomiqueForm.value.qualite,
        this.representantOperateurEconomiqueForm.value.email,
        telephone,
        portable,
        fax,
        this.representantOperateurEconomiqueForm.value.nationalite
      );      
      result = this.representantOperateurEconomiqueService.putRepresentant(representantOperateurEconomiquePutRequest, this.data.rprst._id);
    }

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.representantOperateurEconomiqueService.deleteRepresentant(this.data.id_oper_econ, this.data.rprst._id);
    }    

    result
    .then((value) =>{
      this.loadingData = false
      this.dialogRef.close(
        value
        /*
        {
          _id: value._id , 
          ...this.serviceContractant
        }
        */
      )
    })
    .catch(()=>{
      this.loadingData = false 
      return this.errorLoadingData = true
    })



  }







}
