import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms'; 
import { DialogAction } from 'src/app/enums/dialog-action.enum'; 
import { UploadService } from 'src/app/upload.service';  
import { RetraitCDCPutRequest } from 'src/app/requests/retrait-cdc-put.request';
import { RetraitCDC } from 'src/app/models/lancement/retrait-cdc.model';
import { RetraitCDCService } from 'src/app/domaine/business-logic/retraits-cdc.service';


@Component({
  selector: 'app-create-liste-retraits',
  templateUrl: './create-liste-retraits.component.html',
  styleUrls: ['./create-liste-retraits.component.css']
})
export class CreateListeRetraitsComponent implements OnInit {





  constructor(
    public dialogRef: MatDialogRef<CreateListeRetraitsComponent>,
    private retraitCDCService: RetraitCDCService,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      rtr_cdc: RetraitCDC},
    private uploadService: UploadService
  ) {



  };




  retraitCDCForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;




  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;



    this.retraitCDCForm = new FormGroup({
      'pathListeRetraits': new FormControl({value: null, disabled: false}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.retraitCDCForm.patchValue(this.data.rtr_cdc);
    }


  };






  //Data model to submit to the DB server
  retraitCDCPutRequest: RetraitCDCPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.retraitCDCForm);




    // save the data into the database
    let result:Promise<RetraitCDC>;
    if (this.isCreate || this.isModify){
      this.retraitCDCPutRequest = new RetraitCDCPutRequest(
        this.retraitCDCForm.value.pathListeRetraits
      );      
      result = this.retraitCDCService.putRetraitCDC(this.retraitCDCPutRequest, this.data.rtr_cdc._id);
    };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.retraitCDCService.deleteListeRetraitsCDC(this.data.rtr_cdc._id);
    };
    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.retraitCDCForm.patchValue({'pathListeRetraits' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}
