export class SousProjetPostRequest {

  public intituleSousProjet: string;
  public naturePrestation: string;
  public estimationAdministrative: number

  constructor(
    intituleSousProjet: string,
    naturePrestation: string,
    estimationAdministrative: number
    ){
      this.intituleSousProjet = intituleSousProjet;
      this.naturePrestation = naturePrestation;
      this.estimationAdministrative = estimationAdministrative;
    }

}