import { Injectable } from '@angular/core';
import { ElaborationCDCAccess } from '../data-access/elaboration-cdc.access';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { ElaborationCDCPostRequest } from 'src/app/requests/elaboration-cdc-post.request';
import { ElaborationCDCPutRequest } from 'src/app/requests/elaboration-cdc-put.request';

@Injectable()
export class ElaborationCDCService {

  constructor(private elaborationCDCAccess: ElaborationCDCAccess){}

  getElaborationCDC(idElaborationCDC: string): Promise<ElaborationCDC> {
    return this.elaborationCDCAccess.getElaborationCDC(idElaborationCDC);
  }

  getAllElaborationsCDC(): Promise<ElaborationCDC[]> {
    return this.elaborationCDCAccess.getAllElaborationsCDC();
  }

  postElaborationCDC(elaborationCDC: ElaborationCDCPostRequest, idSousProjet: string): Promise<ElaborationCDC> {
    return this.elaborationCDCAccess.postElaborationCDC(elaborationCDC, idSousProjet);
  }

  putElaborationCDC(elaborationCDC: ElaborationCDCPutRequest, idElaborationCDC: string): Promise<ElaborationCDC> {
    return this.elaborationCDCAccess.putElaborationCDC(elaborationCDC, idElaborationCDC);
  }

  deleteElaborationCDC(idSousProjet: string, idElaborationCDC:string):Promise<ElaborationCDC> {
    return this.elaborationCDCAccess.deleteElaborationCDC(idSousProjet, idElaborationCDC);
  }
}



