import { Injectable } from '@angular/core';
import { DemandeEclaircissement } from 'src/app/models/demande.eclaircissement.model';
import { DemandeEclaircissementAccess } from '../data-access/demande-eclaircissement.access';
import { DemandeEclaircissementPostRequest } from 'src/app/requests/demande-eclaircissement-post.request';
import { DemandeEclaircissementPutRequest } from 'src/app/requests/demande-eclaircissement-put.request';
import { RepresentantOperateurEconomique } from 'src/app/models/representant.operateur.economique.model';
import { RepresentantOperateurEconomiquePostRequest } from 'src/app/requests/representant-operateur-economique-post.request';
import { RepresentantOperateurEconomiquePutRequest } from 'src/app/requests/representant-operateur-economique-put.request';
import { ReponseEclaircissement } from 'src/app/models/reponse.eclaircissement.model';
import { ReponseEclaircissementPutRequest } from 'src/app/requests/reponse-eclaircissement-put.request';
import { ReponseEclaircissementPostRequest } from 'src/app/requests/reponse-eclaircissement-post.request';

@Injectable()
export class DemandeEclaircissementService {

  constructor(private demandeEclaircissementAccess: DemandeEclaircissementAccess){}



  getDemandeEclaircissementByNumero(idRetraitCDC: string, numero: string): Promise<DemandeEclaircissement>{
    return this.demandeEclaircissementAccess.getDemandeEclaircissementByNumero(idRetraitCDC, numero);
  }

  postDemandeEclaircissement(idRetraitCDC: string, demandeEclaircissementPostRequest: DemandeEclaircissementPostRequest): Promise<DemandeEclaircissement>{
    return this.demandeEclaircissementAccess.postDemandeEclaircissement(idRetraitCDC, demandeEclaircissementPostRequest);
  }


  getAllDemandesEclaircissement(): Promise<DemandeEclaircissement[]>{
    return this.demandeEclaircissementAccess.getAllDemandesEclaircissement();
  }

  
  putDemandeEclaircissement(idRetraitCDC: string, idDemandeEclaircissement: string, demandeEclaircissementPutRequest: DemandeEclaircissementPutRequest): Promise<DemandeEclaircissement>{
    return this.demandeEclaircissementAccess.putDemandeEclaircissement(idRetraitCDC, idDemandeEclaircissement, demandeEclaircissementPutRequest);
  }
  
  deleteDemandeEclaircissement(idRetraitCDC: string, idDemandeEclaircissement: string):Promise<DemandeEclaircissement>{
    return this.demandeEclaircissementAccess.deleteDemandeEclaircissement(idRetraitCDC, idDemandeEclaircissement);
  }
  
  postDemandeur(idDemandeEclaircissement: string, representant: RepresentantOperateurEconomiquePostRequest): Promise<RepresentantOperateurEconomique>{
    return this.demandeEclaircissementAccess.postDemandeur(idDemandeEclaircissement, representant);
  }

  putDemandeur(idDemandeEclaircissement: string, representant: RepresentantOperateurEconomiquePutRequest): Promise<RepresentantOperateurEconomique>{
    return this.demandeEclaircissementAccess.putDemandeur(idDemandeEclaircissement, representant);
  }

  deleteDemandeur(idDemandeEclaircissement: string): Promise<RepresentantOperateurEconomique>{
    return this.demandeEclaircissementAccess.deleteDemandeur(idDemandeEclaircissement);
  }


  postReponseEclaircissement(idDemandeEclaircissement: string, reponse: ReponseEclaircissementPostRequest): Promise<ReponseEclaircissement>{
    return this.demandeEclaircissementAccess.postReponseEclaircissement(idDemandeEclaircissement, reponse);
  }
  putReponseEclaircissement(idDemandeEclaircissement: string, reponse: ReponseEclaircissementPutRequest): Promise<ReponseEclaircissement>{
    return this.demandeEclaircissementAccess.putReponseEclaircissement(idDemandeEclaircissement, reponse);
  }
  deleteReponseEclaircissement(idDemandeEclaircissement: string): Promise<ReponseEclaircissement>{
    return this.demandeEclaircissementAccess.deleteReponseEclaircissement(idDemandeEclaircissement);
  }
  
}



