export class ODS {
    numeroODS: string
    type: string //arret, reprise, augmentation(prolongation), dimunition, autre?
    objet: string
    jours: number
    dateEtablissement: Date
    dateEffet: Date
    ODSPath:string

    constructor(
        numeroODS: string,
        type: string,
        objet: string,
        jours: number,
        dateEtablissement: Date,
        dateEffet: Date,
        ODSPath:string
    ) {
        this.numeroODS = numeroODS,
            this.type = type,
            this.objet = objet,
            this.jours = jours,
            this.dateEtablissement = dateEtablissement,
            this.dateEffet = dateEffet,
            this.ODSPath  = ODSPath
    }

    static getDummy(): ODS {
        return new ODS(
            "numeroODS",
            "type",
            "objet",
            15,
            new Date(),
            new Date(),
            "ODS Path"
        )
    }
}