import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable} from '@angular/core'; 
import { AdditifCDC } from 'src/app/models/additif-cdc.model';
import { AdditifCDCPostRequest } from 'src/app/requests/additif-cdc-post.request';
import { AdditifCDCPutRequest } from 'src/app/requests/additif-cdc-put.request';


@Injectable()
export class AdditifCDCAccess {

  constructor(private http: HttpClient){}

  getAdditifCDC(idAdditifCDC: string): Promise<AdditifCDC> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<AdditifCDC>(
          `${apiEndpoint}/additifs-cdc/${idAdditifCDC}`,
          )
        .subscribe(
          additifCDC => { resolve(additifCDC) },
          error => reject(error)
        )

      });
  }



 
  async getAllAdditifsCDC(): Promise<AdditifCDC[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<AdditifCDC[]>(
          `${apiEndpoint}/additifs-cdc`,
          )
        .subscribe(
          additifsCDC => { resolve(additifsCDC) },
          error => reject(error)
        )

      });
  };



  async postAdditifCDC(additifCDC: AdditifCDCPostRequest, idElaborationCDC: string): Promise<AdditifCDC> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<AdditifCDC>(
          `${apiEndpoint}/additifs-cdc/${idElaborationCDC}`, 
          additifCDC, 
          httpOptions
          )
        .subscribe(
            additifCDC => { resolve(additifCDC) },
            error => reject(error)
        )
      }
    );
  };


  async putAdditifCDC(additifCDC: AdditifCDCPutRequest, idAdditifCDC: string): Promise<AdditifCDC> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<AdditifCDC>(
          `${apiEndpoint}/additifs-cdc/${idAdditifCDC}`, 
          additifCDC, 
          httpOptions
          )
        .subscribe(
            additifCDC => { resolve(additifCDC) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteAdditifCDC(idElaborationCDC: string, idAdditifCDC: string): Promise<AdditifCDC> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<AdditifCDC>(
          `${apiEndpoint}/additifs-cdc/${idElaborationCDC}/${idAdditifCDC}`
          )
        .subscribe(
            additifCDC => { resolve(additifCDC) },
            error => reject(error)
        )
      }
    );
  }
  
};