import { Civilite } from 'src/app/enums/civilite.enum';
import { Component, OnInit, Inject } from '@angular/core';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { Observable } from 'rxjs';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { Utilisateur } from 'src/app/models/utilisateur.model';
import { UtilisateurService } from 'src/app/domaine/business-logic/utilisateurs.service';
import { CalendarEntry } from 'src/app/models/calendar.entry.model';
import { CalendarEntryService } from 'src/app/domaine/business-logic/calendar-entries.service';
import { CalendarEntryPostRequest } from 'src/app/requests/calendar-entry-post.request';
import { CalendarEntryPutRequest } from 'src/app/requests/calendar-entry-put.request';

@Component({
  selector: 'app-create-calendar-entry',
  templateUrl: './create-calendar-entry.component.html',
  styleUrls: ['./create-calendar-entry.component.css']
})
export class CreateCalendarEntryComponent implements OnInit {


  constructor(public dialogRef: MatDialogRef<CreateCalendarEntryComponent>,
    private calendarEntryService: CalendarEntryService,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, clnd_entry: CalendarEntry}
    ) { }


  calendarEntryForm: FormGroup;
  
  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

  ngOnInit() {

    console.log("Dealing with:", this.data.clnd_entry);

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    this.calendarEntryForm = new FormGroup({
      'date': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'event': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });

  
    // fill the form with the previous data
    if(this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.calendarEntryForm.patchValue(this.data.clnd_entry);
    }

  }


  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.calendarEntryForm);



    // save the data into the database
    let result:Promise<CalendarEntry>;
    if (this.isCreate){
      let calendarEntryPostRequest = new CalendarEntryPostRequest(
        this.calendarEntryForm.value.date,
        this.calendarEntryForm.value.event
      );
      
      result = this.calendarEntryService.postCalendarEntry(calendarEntryPostRequest);
    }

    // update the data in the database
    if(this.isModify){
      let calendarEntryPutRequest = new CalendarEntryPutRequest(
        this.calendarEntryForm.value.date,
        this.calendarEntryForm.value.event
      );
      result = this.calendarEntryService.putCalendarEntry(calendarEntryPutRequest, this.data.clnd_entry._id);
    }

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.calendarEntryService.deleteCalendarEntry(this.data.clnd_entry._id);
    };


    result
    .then((value) =>{
      this.loadingData = false
      this.dialogRef.close( 
        value
      )
    })
    .catch(()=>{
      this.loadingData = false 
      return this.errorLoadingData = true
    })



  }








}

