export class PassageCommissionCompetente {

    dateDepotSecretariatCommission: Date
    datePassageCommission: Date
    decision: string
    dateLeveeReserves: Date
    PVPath: string
    dateDecision: Date
    numero: string
    decisionPath: string

    constructor(
        dateDepotSecretariatCommission: Date,
        datePassageCommission: Date,
        decision: string,
        dateLeveeReserves: Date,
        PVPath: string,
        dateDecision: Date,
        numero: string,
        decisionPath: string
    ){
        this.dateDepotSecretariatCommission = dateDepotSecretariatCommission
        this.datePassageCommission = datePassageCommission
        this.decision = decision
        this.dateLeveeReserves = dateLeveeReserves
        this.PVPath = PVPath
        this.dateDecision = dateDecision
        this.numero = numero
        this.decisionPath = decisionPath
    }

    static getDummy(): PassageCommissionCompetente {
        return new PassageCommissionCompetente(
            new Date(),
            new Date(),
            "decision",
            new Date(),
            "pathPV",
            new Date(),            
            "numero",
            "decisionPath"
        )
    }
}