import { OperateurEconomique } from './operateur.economique.model'
import { RepresentantOperateurEconomique } from './representant.operateur.economique.model';

export class Candidat {
    _id:string;
    dateCreation: Date;
    operateurEconomique: OperateurEconomique;
    representant: RepresentantOperateurEconomique;
    dateRetrait: Date;
    numero: string;
    /*
    denominationEntreprise: string
    nom: string
    prenom:string
    qualite:string
    email:string 
    portable:string[]
    telephone:string[]
    fax:string[]
    dateEtHeureRetrait:Date
    */
    constructor (
      dateCreation: Date,
      operateurEconomique: OperateurEconomique,
      representant: RepresentantOperateurEconomique,
      dateRetrait: Date,
      numero: string
    ){
        this.dateCreation = dateCreation;
        this.operateurEconomique=operateurEconomique;
        this.representant=representant;
        this.dateRetrait=dateRetrait;
        this.numero=numero;
    }
    static getDummy(): Candidat {
        return new Candidat(
          new Date(),
          OperateurEconomique.getDummy(),
          RepresentantOperateurEconomique.getDummy(), 
          new Date(),
          "Test"
        )
      }
}