import { Injectable } from '@angular/core';
import { ProjetCDC } from 'src/app/models/projet-cdc.model';
import { ProjetCDCPostRequest } from 'src/app/requests/projet-cdc-post.request';
import { ProjetCDCPutRequest } from 'src/app/requests/projet-cdc-put.request';
import { ProjetCDCAccess } from '../data-access/projet-cdc.access';

@Injectable()
export class ProjetCDCService {

  constructor(private projetCDCAccess: ProjetCDCAccess){}

  getProjetCDC(idProjetCDC: string): Promise<ProjetCDC> {
    return this.projetCDCAccess.getProjetCDC(idProjetCDC);
  }

  getAllProjetsCDC(): Promise<ProjetCDC[]> {
    return this.projetCDCAccess.getAllProjetsCDC();
  }

  postProjetCDC(projetCDC: ProjetCDCPostRequest, idElaborationCDC: string): Promise<ProjetCDC> {
    return this.projetCDCAccess.postProjetCDC(projetCDC, idElaborationCDC);
  }

  putProjetCDC(projetCDC: ProjetCDCPutRequest, idProjetCDC: string): Promise<ProjetCDC> {
    return this.projetCDCAccess.putProjetCDC(projetCDC, idProjetCDC);
  }

  deleteProjetCDC(idElaborationCDC: string, idProjetCDC:string):Promise<ProjetCDC> {
    return this.projetCDCAccess.deleteProjetCDC(idElaborationCDC, idProjetCDC);
  }
}



