import { Component } from '@angular/core';
import { CreateClotureComponent } from 'src/app/dialogs/create-cloture/create-cloture.component';
import { CreateReceptionComponent } from 'src/app/dialogs/create-reception/create-reception.component';
import { MatDialog } from '@angular/material';


@Component({
  selector: 'app-cloture',
  templateUrl: './cloture.component.html',
  styleUrls: ['./cloture.component.css']
})

export class ClotureComponent {

  constructor(public dialog: MatDialog) {
    console.log("constructor cloture ran");
   }


  openDialogCreateReception(): void {

    const dialogRef = this.dialog.open(CreateReceptionComponent, {
      width: '800px',
      data: "reception"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
    })

  }


  openDialogCreateCloture(): void {

    const dialogRef = this.dialog.open(CreateClotureComponent, {
      width: '800px',
      data: "cloture"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
    })

  }


}
