import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable} from '@angular/core';
import { ProlongationPostRequest } from 'src/app/requests/prolongation-post.request';
import { Prolongation } from 'src/app/models/prolongation.model';
import { ProlongationPutRequest } from 'src/app/requests/prolongation-put.request';


@Injectable()
export class ProlongationAccess {

  constructor(private http: HttpClient){}

  


  


  async postProlongation(idLancementConcurrence: string, prolongation: ProlongationPostRequest): Promise<Prolongation> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<Prolongation>(
          `${apiEndpoint}/prolongations/${idLancementConcurrence}`, 
          prolongation, 
          httpOptions
          )
        .subscribe(
          prolongation => { resolve(prolongation) },
            error => reject(error)
        )
      }
    );
  };


  async putProlongation(idProlongation: string, prolongationPutRequest: ProlongationPutRequest): Promise<Prolongation> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the put query
        this.http.put<Prolongation>(
          `${apiEndpoint}/prolongations/${idProlongation}`, 
          prolongationPutRequest, 
          httpOptions
          )
        .subscribe(
          prolongation => { resolve(prolongation) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteProlongation(idLancementConcurrence: String, idProlongation: string):Promise<Prolongation> {

    return new Promise(
      (resolve, reject) => {
        
        //send the delete query
        this.http.delete<Prolongation>(
          `${apiEndpoint}/prolongations/${idLancementConcurrence}/${idProlongation}`
          )
        .subscribe(
          prolongation => { resolve(prolongation) },
            error => reject(error)
        )
      }
    );
  }
  
};