import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';


@Injectable()
export class ProjetAccess {

  constructor(private http: HttpClient){}


  async getAllProjets(): Promise<Projet[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Projet[]>(
          `${apiEndpoint}/projets`,
          )
        .subscribe(
          projets => { resolve(projets) },
          error => reject(error)
        )

      });
  };

  async getProjet(idProjet: string): Promise<Projet> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Projet>(
          `${apiEndpoint}/projets/${idProjet}`,
          )
        .subscribe(
          projet => { resolve(projet) },
          error => reject(error)
        )

    });
  }


  async getProjetByCode(codeProjet: String): Promise<Projet>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Projet[]>(
          `${apiEndpoint}/projets/codes/${codeProjet}`,
        )
        .subscribe(
          projet => {
            if(projet.length > 0)
              resolve(projet[0]);
            else 
              resolve(null);
          },
          error => reject(error)
        )

      });

  };



  async postProjet(projet: ProjetPostRequest): Promise<Projet> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<Projet>(
          `${apiEndpoint}/projets`, 
          projet, 
          httpOptions
          )
        .subscribe(
            projet => { resolve(projet) },
            error => reject(error)
        )
      }
    );
  };


  async putProjet(projet: ProjetPutRequest, id: String): Promise<Projet> {
    console.log("The id:", id)
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<Projet>(
          `${apiEndpoint}/projets/${id}`, 
          projet, 
          httpOptions
          )
        .subscribe(
            projet => { resolve(projet) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteProjet(id: String): Promise<Projet> {
    console.log("The id:", id)

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<Projet>(
          `${apiEndpoint}/projets/${id}`
          )
        .subscribe(
            projet => { resolve(projet) },
            error => reject(error)
        )
      }
    );
  }
  
};