import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { UploadService } from 'src/app/upload.service';
import { Siteweb } from 'src/app/models/siteweb.model';
import { SitewebService } from 'src/app/domaine/business-logic/sitewebs.service';
import { SitewebPutRequest } from 'src/app/requests/siteweb-put.request';
import { SitewebPostRequest } from 'src/app/requests/siteweb-post.request';
import { Prolongation } from 'src/app/models/prolongation.model';



@Component({
  selector: 'app-create-siteweb',
  templateUrl: './create-siteweb.component.html',
  styleUrls: ['./create-siteweb.component.css']
})

export class CreateSitewebComponent implements OnInit {





  constructor(
    public dialogRef: MatDialogRef<CreateSitewebComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, 
      stwb: Siteweb
      id_lcm_cnrr: string
      is_prlng : boolean
      prlng : Prolongation
    },
    private uploadService: UploadService,
    public sitewebService: SitewebService
  ) {



  };




  sitewebForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;



  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.stwb.dateCreation);
    }




    this.sitewebForm = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
      'dateParution':new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'denomination': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'lien': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'pathJustificatif': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.sitewebForm.patchValue(this.data.stwb);
    }



  };









  //Data model to submit to the DB server
  sitewebPostRequest: SitewebPostRequest;
  sitewebPutRequest: SitewebPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.sitewebForm);




    // save the data into the database
    let result:Promise<Siteweb>;
    if (this.isCreate){
      this.sitewebPostRequest = new SitewebPostRequest(
        this.sitewebForm.value.dateParution,
        this.sitewebForm.value.denomination,
        this.sitewebForm.value.lien,
        this.sitewebForm.value.pathJustificatif
      ); 

      if (this.data.is_prlng)
        result = this.sitewebService.postSitewebProlongation(this.data.prlng._id, this.sitewebPostRequest);
      else 
        result = this.sitewebService.postSiteweb(this.data.id_lcm_cnrr, this.sitewebPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.sitewebPutRequest = new SitewebPutRequest(
        this.sitewebForm.value.dateParution,
        this.sitewebForm.value.denomination,
        this.sitewebForm.value.lien,
        this.sitewebForm.value.pathJustificatif
      );      
      result = this.sitewebService.putSiteweb(this.data.stwb._id, this.sitewebPutRequest);
    };

    // delete le S/C data from the database
    if(this.isDelete){


      if (this.data.is_prlng)
        result = this.sitewebService.deleteSitewebProlongation(this.data.prlng._id, this.data.stwb._id);
      else
        result = this.sitewebService.deleteSiteweb(this.data.id_lcm_cnrr, this.data.stwb._id);
    };

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value/*
        {
          _id: value._id , 
          serviceContractant: {
            _id: value.serviceContractant._id,
            identifiant: value.serviceContractant.identifiant,
            nomStructure: value.serviceContractant.nomStructure
          },
          intituleProjet : value.intituleProjet,
          codeProjet: value.codeProjet,
          montantProjet: value.montantProjet,
          dateCreationProjet: value.dateCreationProjet,
          yearPlanAction: value.yearPlanAction,
          pourLaStructureRattachement: value.pourLaStructureRattachement,
          sourcesFinancement: value.sourcesFinancement,
          siAutreSourceFinancement: value.siAutreSourceFinancement,
          budget: value.budget,
          isHorsPlan: value.isHorsPlan,
          pathJustificatifProjet: value.pathJustificatifProjet,
          sousProjets : value.sousProjets
        }
        */
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.sitewebForm.patchValue({'pathJustificatif' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}




