export class OrganePresse {
    _id: string;
    dateCreationOrganePresse: Date;
    langue:string;
    denomination: string;
    dateParution: Date;
    pathOrganePresse: string;

    constructor (
        langue:string,
        denomination: string,
        dateParution: Date,
        pathOrganePresse: string 
    ){
        this.langue = langue
        this.denomination=denomination
        this.dateParution = dateParution
        this.pathOrganePresse = pathOrganePresse
    }

    static getDummy():OrganePresse {
        return new OrganePresse(
            "fr",
            "NomJournal",
            new Date(),
            "pathJustificatif"
        )
    }
}