import { RepresentantOperateurEconomique } from './representant.operateur.economique.model';
import { FormeJuridique } from '../enums/forme.juridique.enum';

export class OperateurEconomique {
    _id: string; 

    dateCreation: Date;
    pathLogo: string;
    denomination: string;
    nif: string;
    formeJuridique: string;
    estExclu: boolean;
    confirmed: boolean;

    representants: RepresentantOperateurEconomique[];

    constructor (
        dateCreation: Date,
        denomination: string,
        nif: string,
        pathLogo: string,
        formeJuridique: string,
        estExclu: boolean,
        confirmed: boolean
    ){
        this.dateCreation = dateCreation;
        this.denomination=denomination;
        this.nif = nif;
        this.pathLogo = pathLogo;
        this.estExclu = estExclu;
        this.formeJuridique = formeJuridique;
        this.representants = [];
        this.confirmed = confirmed;
    }

    static getDummy():OperateurEconomique {
        return new OperateurEconomique(
            new Date(),
            "Algerie Poste",
            "NIF",
            "pathLogo",
            FormeJuridique.EPE,
            false,
            false
        )
    }
}