import { Component, ViewChild, Input } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { Candidat } from 'src/app/models/candidat.model';
import { DemandeEclaircissement } from 'src/app/models/demande.eclaircissement.model';
import { CreateCandidatComponent } from 'src/app/dialogs/create-candidat/create-candidat.component';
import { CreateReponseDemandeEclaircissementComponent } from 'src/app/dialogs/create-response-demande-eclaircissement/create-response-demande-eclaircissement.component';
import { CreateDemandeEclaircissementComponent } from 'src/app/dialogs/create-demande-eclaircissement/create-demande-eclaircissement.component';
import { Lancement } from 'src/app/models/lancement/lancement.model';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { CreateRepresentantOperateurEconomiqueComponent } from 'src/app/dialogs/create-representant-operateur-economique/create-representant-operateur-economique.component';
import { RepresentantOperateurEconomique } from 'src/app/models/representant.operateur.economique.model';
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { RepresentantOperateurEconomiqueAccess } from 'src/app/domaine/data-access/representant-operateur-economique.access';
import { CreateListeRetraitsComponent } from 'src/app/dialogs/create-liste-retraits/create-liste-retraits.component';
import { fileUploadUrl } from 'src/app/config';
