import { Component, OnInit } from '@angular/core';
import * as Highcharts from 'highcharts';
import HC_exporting from 'highcharts/modules/exporting';
HC_exporting(Highcharts);

@Component({ 
  selector: 'app-stacked-chart',
  templateUrl: './stacked-chart.component.html',
  styleUrls: ['./stacked-chart.component.css']
})
export class StackedChartComponent implements OnInit {

  constructor() { }

  Highcharts: typeof Highcharts = Highcharts;

  chartOptions: Highcharts.Options = { 

    chart: {
        type: 'area'
    },
    title: {
        text: 'Historique et estimation des projets par region'
    },
    subtitle: {
        text: 'Source: Algérie poste (DCM)'
    },
    xAxis: {
        categories: ['2015', '2016', '2017', '2018', '2019', '2020', '2021'],
        tickmarkPlacement: 'on'
    },
    yAxis: {
        title: {
            text: 'Millions (DZD)'
        }
    },
    tooltip: {
        split: true,
        valueSuffix: ' millions'
    },
    credits: {
      enabled: false
    },
    plotOptions: {
        area: {
            stacking: 'normal',
            lineColor: '#666666',
            lineWidth: 1,
            marker: {
                lineWidth: 1,
                lineColor: '#666666'
            }
        }
    },
    series: [{
        name: 'DCM',
        data: [502, 635, 809, 947, 1402, 3634, 5268],
        type: 'line'
    }, {
        name: 'DISR',
        data: [106, 107, 111, 133, 221, 767, 1766],
        type: 'line'
    }, {
        name: 'DDR',
        data: [163, 203, 276, 408, 547, 729, 628],
        type: 'line'
    }, {
        name: 'DFP',
        data: [18, 31, 54, 156, 339, 818, 1201],
        type: 'line'
    }, {
        name: 'DIPB',
        data: [2, 2, 2, 6, 13, 30, 46],
        type: 'line'
    }],
    
    exporting:{
        enabled: true  
    }      
   };


  
  ngOnInit() {
  }

}
