export class MiseEnDemeure {
    numMiseEnDemeure: string
    objet: string
    dateNotif: Date 
    moyenNotif: string 
    delaiPriseEnCharge: number //jours 
    miseEnDemeurePath: string 
    resultat: string 

    constructor(
        numMiseEnDemeure: string,
        objet: string,
        dateNotif: Date ,
        moyenNotif: string ,
        delaiPriseEnCharge: number ,//jours 
        miseEnDemeurePath: string ,
        resultat: string 
    ) {
        this.numMiseEnDemeure = numMiseEnDemeure
        this.objet = objet
        this.dateNotif = dateNotif 
        this.moyenNotif = moyenNotif 
        this.delaiPriseEnCharge = delaiPriseEnCharge
        this.miseEnDemeurePath = miseEnDemeurePath 
        this.resultat = resultat
    }

    static getDummy(): MiseEnDemeure {
        return new MiseEnDemeure(
            "numMiseEnDemeure",
            "objet",
            new Date(),
            "moyenNotif",
            50,
            "miseEnDemeurePath",
            "resultat"
        )
    }
}