import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { SousProjetPutRequest } from 'src/app/requests/sous-projet-put.request';
import { SousProjetPostRequest } from 'src/app/requests/sous-projet-post.request';


@Injectable()
export class SousProjetAccess {

  constructor(private http: HttpClient){}


  async getSousProjet(idSousProjet: string): Promise<SousProjet> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<SousProjet>(
          `${apiEndpoint}/sous-projets/${idSousProjet}`,
          )
        .subscribe(
          sousProjet => { resolve(sousProjet) },
          error => reject(error)
        )

      });
  }


  async getAllSousProjets(): Promise<SousProjet[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<SousProjet[]>(
          `${apiEndpoint}/sous-projets`,
          )
        .subscribe(
          sousProjets => { resolve(sousProjets) },
          error => reject(error)
        )

      });
  };


  async getAllSousProjetsOfProjet(id : string): Promise<SousProjet[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<SousProjet[]>(
          `${apiEndpoint}/sous-projets/${id}`,
          )
        .subscribe(
          sousProjets => { resolve(sousProjets) },
          error => reject(error)
        )

      });
  };





  async postSousProjet(sousProjet: SousProjetPostRequest, idProjet: string): Promise<SousProjet> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<SousProjet>(
          `${apiEndpoint}/sous-projets/${idProjet}`, 
          sousProjet, 
          httpOptions
          )
        .subscribe(
            sousProjet => { resolve(sousProjet) },
            error => reject(error)
        )
      }
    );
  };


  async putSousProjet(sousProjet: SousProjetPutRequest, idSousProjet: string): Promise<SousProjet> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<SousProjet>(
          `${apiEndpoint}/sous-projets/${idSousProjet}`, 
          sousProjet, 
          httpOptions
          )
        .subscribe(
            sousProjet => { resolve(sousProjet) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteSousProjet(idProjet: string, idSousProjet: string): Promise<SousProjet> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<SousProjet>(
          `${apiEndpoint}/sous-projets/${idProjet}/${idSousProjet}`
          )
        .subscribe(
            sousProjet => { resolve(sousProjet) },
            error => reject(error)
        )
      }
    );
  }
  
};