import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { Lancement } from 'src/app/models/lancement/lancement.model';
import { LancementPostRequest } from 'src/app/requests/lancement-post.request';
import { LancementPutRequest } from 'src/app/requests/lancement-put.request';


@Injectable()
export class LancementAccess {

  constructor(private http: HttpClient){}


  async getAllLancements(): Promise<Lancement[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Lancement[]>(
          `${apiEndpoint}/lancements`,
          )
        .subscribe(
          lancements => { resolve(lancements) },
          error => reject(error)
        )

      });
  };

  async getLancement(idLancement: string): Promise<Lancement> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Lancement>(
          `${apiEndpoint}/lancements/${idLancement}`,
          )
        .subscribe(
          lancement => { resolve(lancement) },
          error => reject(error)
        )

    });
  }



  async postLancement(idElaborationCDC: string, lancementPostRequest: LancementPostRequest): Promise<Lancement> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<Lancement>(
          `${apiEndpoint}/lancements/${idElaborationCDC}`, 
          lancementPostRequest, 
          httpOptions
          )
        .subscribe(
            lancement => { resolve(lancement) },
            error => reject(error)
        )
      }
    );
  };


  async putLancement(idLancement: string, lancementPutRequest: LancementPutRequest): Promise<Lancement> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<Lancement>(
          `${apiEndpoint}/lancements/${idLancement}`, 
          lancementPutRequest, 
          httpOptions
          )
        .subscribe(
            lancement => { resolve(lancement) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteLancement(idElaborationCDC: string, idLancement: string):Promise<Lancement> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<Lancement>(
          `${apiEndpoint}/lancements/${idElaborationCDC}/${idLancement}`
          )
        .subscribe(
            lancement => { resolve(lancement) },
            error => reject(error)
        )
      }
    );
  }
  
};