import { Civilite } from '../enums/civilite.enum';

export class RepresentantOperateurEconomique {
    _id: string; 
    dateCreation: Date;

    nom: string;
    prenom: string;
    civilite: Civilite;
    qualite: string;
    email: string;
    telephone: string[];
    portable: string[];
    fax: string[];
    nationalite: string; 

    
    constructor (
        dateCreation: Date,
        nom: string,
        prenom: string,
        civilite: Civilite,
        qualite: string,
        email: string,
        telephone: string[],
        portable: string[],
        fax: string[],
        nationalite: string
    ){
        this.dateCreation = dateCreation
        this.nom = nom,
        this.prenom = prenom,
        this.civilite = civilite,
        this.qualite = qualite,
        this.email = email,
        this.telephone = telephone,
        this.portable = portable,
        this.fax = fax,
        this.nationalite = nationalite
    }

    static getDummy():RepresentantOperateurEconomique {
        return new RepresentantOperateurEconomique(
            new Date(),
            "ali",
            "bouderbala",
            Civilite.M,
            "Ig",
            "test@test.fr",
            ["025055555","025055555"],
            ["025055555","025055555"],
            ["025055555","025055555"],
            "Algerie"
        )
    }
}