import { Component, ViewChild } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { Recours } from 'src/app/models/recours.model';
import { AnnulationAttribution } from 'src/app/models/annulation.attribution.model';
import { DesistementAttribution } from 'src/app/models/desistement.attribution.model';
import { DeclarationAttribution } from 'src/app/models/declaration.attribution.model';
import { CreateRecoursComponent } from 'src/app/dialogs/create-recours/create-recours.component';
import { CreateDeclarationAttributionProvisoireComponent } from 'src/app/dialogs/create-declaration-attribution-provisoire/create-declaration-attribution-provisoire.component';


@Component({
  selector: 'app-attribution',
  templateUrl: './attribution.component.html',
  styleUrls: ['./attribution.component.css']
})

export class AttributionComponent {

  @ViewChild("tableRecours", {static: true}) tableRecours: MatTable<Recours>;

  
  annulationsAttributionsProvisoires: AnnulationAttribution = null;
  desistementsAttributionsProvisoires: DesistementAttribution = null;

  attributionsProvisoires: DeclarationAttribution[] = []

  dataSourceRecours : Recours [] = [];
  displayedColumnsRecours: string[] = [
    'soumissionnaire',
    'objetRecours',
    'type',
    'dateDeSaisie',
    'dateLimite',
    'dateRecours',
    'dateReponse',
    'recoursPath',
    'reponsePath',
    'datePassageCommission',
    'decisionCommission',
    'PVPath',
    'decisionPath'
    ];


  constructor(public dialog: MatDialog) { }


  ngOnInit() {

  }


  openDialogCreateRecours(): void {
    const dialogRef = this.dialog.open(CreateRecoursComponent, {
      width: '800px',
      data: "nouveau recours"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    this.dataSourceRecours.push(Recours.getDummy())
    this.tableRecours.renderRows();
  }


  openDialogCreateDeclarationAttribution():void{

    const dialogRef = this.dialog.open(CreateDeclarationAttributionProvisoireComponent, {
      width: '800px',
      data: "declaration attribution provisoire"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    })

    this.attributionsProvisoires.push(DeclarationAttribution.getDummy())
  }


  openDialogCreateAnnulerAttribution():void{
    /*
    const dialogRef = this.dialog.open(CreateAnnulerDeclarationAttributionProvisoireComponent, {
      width: '800px',
      data: "annuler la declaration d'attribution provisoire"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    })
    */
    this.annulationsAttributionsProvisoires = AnnulationAttribution.getDummy()
  }

  openDialogCreateDesistementAttribution():void{
    this.desistementsAttributionsProvisoires = DesistementAttribution.getDummy()
  }


}
