import { Component, OnInit, Inject } from '@angular/core';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { Niveau } from 'src/app/enums/niveau.enum';
import { Wilaya } from 'src/app/enums/wilaya.enum';
import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { Observable } from 'rxjs';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ServiceContractantService } from 'src/app/domaine/business-logic/services-contractants.service';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { ServiceContractantPostRequest } from 'src/app/requests/service-contractant-post.request';
import { ServiceContractantPutRequest } from 'src/app/requests/service-contractant-put.request';



@Component({
  selector: 'app-create-service-contractant',
  templateUrl: './create-service-contractant.component.html',
  styleUrls: ['./create-service-contractant.component.css']
})
export class CreateServiceContractantComponent implements OnInit {

  INFO_TEL = "Forme: 023232323 ou +21323232323. Si plusieurs numéros alors séparez-les par des virgules ',' Ex: 023102030,021102030";
  PHONENUMBER_PATTERN: string = "^[+]*[0-9]+(,[+]*[0-9]+)*$"

  constructor(public dialogRef: MatDialogRef<CreateServiceContractantComponent>,
    private serviceContractantService: ServiceContractantService,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, sc: ServiceContractant}
    ) { }


  serviceContractantForm: FormGroup
  
  isCreate : boolean
  isModify: boolean
  isDelete: boolean

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE
    this.isModify = this.data.action === DialogAction.MODIFY
    this.isDelete = this.data.action === DialogAction.DELETE

    this.serviceContractantForm = new FormGroup({
      'identifiant': new FormControl({value: null, disabled: this.isDelete}, [Validators.required], this.identifiantExists.bind(this)),
      'nomStructure': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'email': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.email], [this.emailExists.bind(this)]),
      'telephone': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.pattern(this.PHONENUMBER_PATTERN)]),
      'fax': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.pattern(this.PHONENUMBER_PATTERN)]),
      'adresse': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.minLength(3)]),
      'wilaya': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'codePostal': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.pattern("[0-9][0-9][0-9][0-9][0-9]")]),
      'niveau': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    })

    /*
    this.serviceContractantForm.valueChanges.subscribe(
      (value)=>{console.log(value)}
    )


    this.serviceContractantForm.statusChanges.subscribe(
      (value) => { console.log(value) }
    )
    */

    //this.serviceContractantForm.setValue
    if(this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.serviceContractantForm.patchValue(this.data.sc)
      this.serviceContractantForm.patchValue({"telephone": this.data.sc.telephone.toString(),
        "fax": this.data.sc.fax.toString()})
    }
  }

  emailExists(control: FormControl): Promise<any> | Observable<any> {

    const email = control.value;

    // if action is modify od delete, and email not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && email === this.data.sc.email)
    return new Promise<any>((resolve, reject) =>  resolve(null))

    const result:Promise<ServiceContractant> = this.serviceContractantService.getServiceContractantByEmail(email);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(sc => {
          console.log("sc", sc)
          if (sc != null) {
            resolve({ "emailExist": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }

  identifiantExists(control: FormControl): Promise<any> | Observable<any> {
    const id = control.value;

    // if action is modify or delete, and identifiant not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY||this.data.action === DialogAction.DELETE) && id === this.data.sc.identifiant)
    return new Promise<any>((resolve, reject) =>  resolve(null))

    const result:Promise<ServiceContractant> = this.serviceContractantService.getServiceContractantByIdentifiant(id);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(sc => {
          console.log("sc", sc)
          if (sc != null) {
            resolve({ "identifiantExist": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }

  //Niveau 
  niveaux = Object.keys(Niveau).slice(0)
  parseValueNiveau(value: string) {
    return Niveau[value].toString();
  }

  //Wilaya
  wilayas = Object.keys(Wilaya).slice(0)
  parseValueWilaya(value: string) {
    return Wilaya[value].toString();
  }


  loadingData: boolean= false
  errorLoadingData: boolean = false

  serviceContractantPostRequest : ServiceContractantPostRequest;
  serviceContractantPutRequest : ServiceContractantPutRequest;
  onSubmit() {
    this.loadingData = true
    this.errorLoadingData = false
    console.log("Submitted", this.serviceContractantForm);







    // save the data into the database
    let result:Promise<ServiceContractant>;
    if (this.isCreate){
      const telephone: string[] = this.serviceContractantForm.value.telephone.split(",")
      const fax: string[] = this.serviceContractantForm.value.fax.split(",")
      this.serviceContractantPostRequest = new ServiceContractantPostRequest(
        this.serviceContractantForm.value.identifiant,
        this.serviceContractantForm.value.nomStructure,
        this.serviceContractantForm.value.email,
        telephone,
        fax,
        this.serviceContractantForm.value.adresse,
        this.serviceContractantForm.value.wilaya,
        this.serviceContractantForm.value.codePostal,
        this.serviceContractantForm.value.niveau
      ) ;    
      result = this.serviceContractantService.postServiceContractant(this.serviceContractantPostRequest);
    }

    // update the data in the database
    if(this.isModify){
      const telephone: string[] = this.serviceContractantForm.value.telephone.split(",");
      const fax: string[] = this.serviceContractantForm.value.fax.split(",");
      this.serviceContractantPutRequest = new ServiceContractantPutRequest(
        this.serviceContractantForm.value.identifiant,
        this.serviceContractantForm.value.nomStructure,
        this.serviceContractantForm.value.email,
        telephone,
        fax,
        this.serviceContractantForm.value.adresse,
        this.serviceContractantForm.value.wilaya,
        this.serviceContractantForm.value.codePostal,
        this.serviceContractantForm.value.niveau
      ) ;          
      result = this.serviceContractantService.putServiceContractant(this.serviceContractantPutRequest, this.data.sc._id);
    }

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.serviceContractantService.deleteServiceContractant(this.data.sc._id);
    }    
    console.log("Delete", result)

    result
    .then((value) =>{
      this.loadingData = false
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false 
      return this.errorLoadingData = true
    })



  }


  //Data model
  serviceContractant: ServiceContractant






}
