import { Injectable } from '@angular/core';
import { RepresentantOperateurEconomiqueAccess } from '../data-access/representant-operateur-economique.access';
import { RepresentantOperateurEconomique } from 'src/app/models/representant.operateur.economique.model';
import { RepresentantOperateurEconomiquePostRequest } from 'src/app/requests/representant-operateur-economique-post.request';
import { RepresentantOperateurEconomiquePutRequest } from 'src/app/requests/representant-operateur-economique-put.request';

@Injectable()
export class RepresentantOperateurEconomiqueService {

  constructor(private representantOperateurEconomiqueAccess: RepresentantOperateurEconomiqueAccess){}

  getAllRepresentants(): Promise<RepresentantOperateurEconomique[]>{
    return this.representantOperateurEconomiqueAccess.getAllRepresentants();
  }

  getRepresentantById(id: String): Promise<RepresentantOperateurEconomique>{
    return this.representantOperateurEconomiqueAccess.getRepresentantById(id);
  }

  getRepresentantByEmail(email: String): Promise<RepresentantOperateurEconomique>{
    return this.representantOperateurEconomiqueAccess.getRepresentantByEmail(email);
  }

  postRepresentant(idOperateurEconomique: string, representant: RepresentantOperateurEconomiquePostRequest): Promise<RepresentantOperateurEconomique>{
    return this.representantOperateurEconomiqueAccess.postRepresentant(idOperateurEconomique, representant);
  }

  postRepresentantBySC(idCandidat: string, idOperateurEconomique: string, representant: RepresentantOperateurEconomiquePostRequest): Promise<RepresentantOperateurEconomique>{
    return this.representantOperateurEconomiqueAccess.postRepresentantBySC(idCandidat, idOperateurEconomique, representant);
  }

  putRepresentant(representant: RepresentantOperateurEconomiquePutRequest, idRepresentant: String): Promise<RepresentantOperateurEconomique>{
    return this.representantOperateurEconomiqueAccess.putRepresentant(representant, idRepresentant);
  }

  deleteRepresentant(idOperateurEconomique:string, idRepresentant:string):Promise<RepresentantOperateurEconomique>{
    return this.representantOperateurEconomiqueAccess.deleteRepresentant(idOperateurEconomique, idRepresentant);
  }
}



