  async postNotificationOperateurEconomiqueProlongation(idProlongation: string, notification: NotificationOperateurEconomiquePostRequest): Promise<NotificationOperateurEconomique> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<NotificationOperateurEconomique>(
          `${apiEndpoint}/notifications-operateurs-economiques/prolongations/${idProlongation}`, 
          notification, 
          httpOptions
          )
        .subscribe(
            notif => { resolve(notif) },
            error => reject(error)
        )
      }
    );
  };

