import { Wilaya } from '../enums/wilaya.enum'
import { Niveau } from '../enums/niveau.enum'
import { CommissionCompetente } from './commission-competente/commission-competente.model'
import { COPEO } from './copeo/copeo.model'
import { Comite } from './comite/comite.model'

export class ServiceContractant {
    public _id: string
    public identifiant: string
    public nomStructure: string
    public email: string
    public telephone: string[]
    public fax: string[]
    public adresse: string
    public wilaya: Wilaya
    public codePostal: string
    public niveau: Niveau

    public CCME: CommissionCompetente;
    public CMU: CommissionCompetente;
    public COPEO_C: COPEO;
    public COPEO_AO: COPEO;
    public COPEO_U: COPEO;
    public COPEO_R: COPEO;
    public comiteNegociation: Comite;
    public comiteTechnique: Comite;

    constructor(
        identifiant: string,
        nomStructure: string,
        email: string,
        telephone: string[],
        fax: string[],
        adresse: string,
        wilaya: Wilaya,
        codePostal: string,
        niveau: Niveau
    ){
        this.identifiant=identifiant
        this.nomStructure=nomStructure
        this.email=email
        this.telephone=telephone
        this.fax=fax
        this.adresse=adresse
        this.wilaya=wilaya
        this.codePostal=codePostal
        this.niveau = niveau

        this.CCME= null;
        this.CMU= null;
        this.COPEO_C= null;
        this.COPEO_AO= null;
        this.COPEO_U= null;
        this.COPEO_R= null;
        this.comiteNegociation= null;
        this.comiteTechnique= null;
    }

    static getDummy(): ServiceContractant {
        return new ServiceContractant(
            "DFC",
            "Direction Finances et comptabilite (DFC)",
            "email@test.com",
            ["023232323","024242424"],
            ["023232323","024242424"],
            "Rue 344 Alger Centre",
            Wilaya.Alger,
            "16000",
            Niveau.CENTRAL
        )
    }
    
  }