import { Component, ViewChild, Input } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { Lancement } from 'src/app/models/lancement/lancement.model';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { CreateLancementComponent } from 'src/app/dialogs/create-lancement/create-lancement.component';
import { DialogAction } from 'src/app/enums/dialog-action.enum';


@Component({
  selector: 'app-lancement',
  templateUrl: './lancement.component.html',
  styleUrls: ['./lancement.component.css']
})

export class LancementComponent {


  constructor(public dialog: MatDialog) { }

  @Input() lancement : Lancement;
  @Input() elaborationCDC : ElaborationCDC;
  @Input() sousProjet : SousProjet;
  @Input() projet: Projet;



  openDialogDeleteLancement(): void {
    const dialogRef = this.dialog.open(CreateLancementComponent, {
      width: '800px',
      data: {action: DialogAction.DELETE, elb_cdc: this.elaborationCDC, lncm: this.lancement}
    });

    dialogRef.afterClosed().subscribe(result => {
        console.log("After delete depot result:", result);
        if (this.elaborationCDC && result) {
          let index = this.elaborationCDC.lancements.findIndex(e => e._id == result._id);
          this.elaborationCDC.lancements.splice(index, 1);
          console.log("Cool updating", this.elaborationCDC.lancements);
        }

    });

  }


  openDialogModifyLancement(): void {
    const dialogRef = this.dialog.open(CreateLancementComponent, {
      width: '800px',
      data: {action: DialogAction.MODIFY, elb_cdc: this.elaborationCDC, lncm: this.lancement}
    });

    dialogRef.afterClosed().subscribe(result => {
        console.log("After modify result:", result);

        if (this.lancement && result) {
          this.lancement.lancementConcurrence.dureePreparationOffres = result.dureePreparationOffres;
          this.lancement.lancementConcurrence.dureeValiditeOffres = result.dureeValiditeOffres;
        }

    });

  }






}
