export class SitewebPutRequest {
  dateParution: Date;
  denomination: string;
  lien: string;
  pathJustificatif: string;

  constructor(
    dateParution: Date,
    denomination: string,
    lien: string,
    pathJustificatif: string
    ){

      this.dateParution = dateParution;
      this.denomination = denomination;
      this.lien = lien;
      this.pathJustificatif = pathJustificatif;

    }

}