import { Component, ViewChild } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { ODS } from 'src/app/models/ods.model';
import { SousTraitant } from 'src/app/models/sous.traitant.model';
import { Facture } from 'src/app/models/facture.model';
import { Litige } from 'src/app/models/litige.model';
import { MiseEnDemeure } from 'src/app/models/mise.en.demeure.model';
import { RallongeFinanciere } from 'src/app/models/rallonge.financiere.model';
import { Avenant } from 'src/app/models/avenant.model';
import { CreateDemandeRallongeFinanciereComponent } from 'src/app/dialogs/create-demande-rallonge-financiere/create-demande-rallonge-financiere.component';
import { CreateODSComponent } from 'src/app/dialogs/create-ods/create-ods.component';
import { CreateSousTraitantComponent } from 'src/app/dialogs/create-sous-traitant/create-sous-traitant.component';
import { CreateMiseEnDemeureComponent } from 'src/app/dialogs/create-mise-en-demeure/create-mise-en-demeure.component';
import { CreateFactureComponent } from 'src/app/dialogs/create-facture/create-facture.component';
import { CreateResiliationComponent } from 'src/app/dialogs/create-resiliation/create-resiliation.component';
import { CreateAvenantComponent } from 'src/app/dialogs/create-avenant/create-avenant.component';
import { CreateLitigeComponent } from 'src/app/dialogs/create-litige/create-litige.component';


@Component({
  selector: 'app-execution',
  templateUrl: './execution.component.html',
  styleUrls: ['./execution.component.css']
})

export class ExecutionComponent {
 

  @ViewChild("tableODS", {static: true}) tableODS: MatTable<ODS>;
  @ViewChild("tableSousTraitants", {static: true}) tableSousTraitants: MatTable<SousTraitant>;
  @ViewChild("tableFactures", {static: true}) tableFactures: MatTable<Facture>;
  @ViewChild("tableLitiges", {static: true}) tableLitiges: MatTable<Litige>;
  @ViewChild("tableMisesEnDemeure", {static: true}) tableMisesEnDemeure: MatTable<MiseEnDemeure>;
  @ViewChild("tableRallongesFinancieresExecution", {static: true}) tableRallongesFinancieresExecution: MatTable<RallongeFinanciere>;
  @ViewChild("tableAvenants", {static: true}) tableAvenants: MatTable<Avenant>;
  

  dataSourceMisesEnDemeure: MiseEnDemeure [] = []
  displayedColumnsMisesEnDemeure: string[] = [
    "numMiseEnDemeure",
    "objet",
    "dateNotif",
    "moyenNotif",
    "delaiPriseEnCharge",
    "miseEnDemeurePath",
    "resultat"
  ];

  
  dataSourceAvenants: Avenant [] = []
  displayedColumnsAvenants: string[] = [
    "num",
    "nature",
    "objet",
    "montant",
    "taux",
    "delai",
    "negociation"
  ];

  dataSourceFactures: Facture [] = []
  displayedColumnsFactures: string[] = [
    "hasPenaliteRetard",
    "numFacture",
    "dateReception",
    "montant",
    "facturePath"
  ];

  dataSourceSousTraitants: SousTraitant [] = []
  displayedColumnsSousTraitants: string[] = [
    "sousTraitant",
    "objetSoustraitance",
    "dateDebutIntervention",
    "dateFinIntervention", 
    "declarationSoustraitantPath", 
    "tauxIntervention"
  ];

  dataSourceODS: ODS [] = []
  displayedColumnsODS: string[] = [
    "numeroODS",
    "type",
    "objet",
    "jours",
    "dateEtablissement",
    "dateEffet",
    "ODSPath"
  ];

  dataSourceLitiges : Litige [] = [];
  displayedColumnsLitige: string[] = [
  'introduitPar',
  'partieAdverse',
  'comment',
  'dateDeSaisie',
  'dateLimite',
  'dateLitige',
  'dateReponse',
  'litigePath',
  'reponsePath',
  'datePassageCommission',
  'decisionCommission',
  'PVPath',
  'decisionPath'
  ];
 

  demandeRallongeExecutionChecked:boolean
  dataSourceRallongesFinancieresExecution: RallongeFinanciere [] = []
  displayedColumnsRallongesFinancieres: string[] = [
    "serviceContractant",
    "dateDemande",
    "montantDemande",
    "demandePath",
    "reponse"
  ];


  actualisationPrixChecked : boolean
  revisionPrixChecked : boolean
  cautionSoumissionChecked : boolean
  cautionBonneExecutionChecked : boolean
  cautionGarantieChecked : boolean
  garantieAvanceApprovisionnementChecked : boolean
  garantieAvanceForfaitaireChecked : boolean

  constructor(public dialog: MatDialog) { 
    console.log("constructor exec ran");
  }


  ngOnInit() {
    this.demandeRallongeExecutionChecked = false
    this.actualisationPrixChecked = false
    this.revisionPrixChecked  = false
    this.cautionSoumissionChecked  = false
    this.cautionBonneExecutionChecked  = false
    this.cautionGarantieChecked  = false
    this.garantieAvanceApprovisionnementChecked  = false
    this.garantieAvanceForfaitaireChecked  = false
  }


  openDialogCreateLitige(): void {
    const dialogRef = this.dialog.open(CreateLitigeComponent, {
      width: '800px',
      data: "nouveauLitige"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    this.dataSourceLitiges.push(Litige.getDummy())
    this.tableLitiges.renderRows();

  }

            

  openDialogCreateDemandeRallongeFinanciereExecution():void{
    const dialogRef = this.dialog.open(CreateDemandeRallongeFinanciereComponent, {
      width: '800px',
      data: "passage commission ouverture plis"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    this.dataSourceRallongesFinancieresExecution.push(RallongeFinanciere.getDummy())
    //console.log(this.recours);
    this.tableRallongesFinancieresExecution.renderRows(); 
  }

  openDialogCreateODS():void{

    const dialogRef = this.dialog.open(CreateODSComponent, {
      width: '800px',
      data: "ajouter ODS"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    })

    this.dataSourceODS.push(ODS.getDummy())
    this.tableODS.renderRows()
  }


  openDialogCreateSousTraitant():void{

    const dialogRef = this.dialog.open(CreateSousTraitantComponent, {
      width: '800px',
      data: "ajouter ODS"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    })

    this.dataSourceSousTraitants.push(SousTraitant.getDummy())
    this.tableSousTraitants.renderRows()
  }

  openDialogCreateMiseEnDemeure():void{

    const dialogRef = this.dialog.open(CreateMiseEnDemeureComponent, {
      width: '800px',
      data: "ajouter ODS"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    })

    this.dataSourceMisesEnDemeure.push(MiseEnDemeure.getDummy())
    this.tableMisesEnDemeure.renderRows()
  }

  openDialogCreateFacture():void{

    const dialogRef = this.dialog.open(CreateFactureComponent, {
      width: '800px',
      data: "ajouter ODS"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    })

    this.dataSourceFactures.push(Facture.getDummy())
    this.tableFactures.renderRows()
  }


  openDialogCreateResiliation():void{

    const dialogRef = this.dialog.open(CreateResiliationComponent, {
      width: '800px',
      data: "Resilitaion"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    })

  }

  openDialogCreateAvenant():void{

    const dialogRef = this.dialog.open(CreateAvenantComponent, {
      width: '800px',
      data: "ajouter Avenant"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    })

    this.dataSourceAvenants.push(Avenant.getDummy())
    this.tableAvenants.renderRows()

  }

}
