import { Injectable } from '@angular/core';
import { PassageCommissionMarchesAccess } from '../data-access/passage-commission-marches.access';
import { PassageCommissionMarches } from 'src/app/models/passage-commission/passage.commission.marches.model';
import { ProjetEnAttentePassageCommissionMarches } from 'src/app/models/passage-commission/projet.en.attente.passage.commission.marches.model';
import { DepotProjetCDCSecretariatCommissionCompetenteCDCPostRequest } from 'src/app/requests/depot-projet-cdc-secretariat-commission-competente-CDC-post.request';
import { DepotAdditifCDCSecretariatCommissionCompetenteCDCPostRequest } from 'src/app/requests/depot-additif-cdc-secretariat-commission-competente-CDC-post.request';

@Injectable()
export class PassageCommissionMarchesService {

  constructor(private passageCommissionMarchesAccess: PassageCommissionMarchesAccess){}

  getProjetEnAttenteByCommissionId(idCommissionCompetente: string): Promise<ProjetEnAttentePassageCommissionMarches[]> {
    return this.passageCommissionMarchesAccess.getProjetEnAttenteByCommissionId(idCommissionCompetente);
  }

  getPassagesCommissionMarchesByCommissionId(idCommissionCompetente: string): Promise<PassageCommissionMarches[]> {
    return this.passageCommissionMarchesAccess.getPassagesCommissionMarchesByCommissionId(idCommissionCompetente);
  }

  postDepotProjetCDCPassageCommissionMarches(projetCDCId: string, depotProjetCDCSecretariatCommissionMarches: DepotProjetCDCSecretariatCommissionCompetenteCDCPostRequest): Promise<PassageCommissionMarches> {
    return this.passageCommissionMarchesAccess.postDepotProjetCDCPassageCommissionMarches(projetCDCId, depotProjetCDCSecretariatCommissionMarches);
  }

  postDepotAdditifCDCPassageCommissionMarches(additifCDCId: string, depotAdditifCDCSecretariatCommissionMarches: DepotAdditifCDCSecretariatCommissionCompetenteCDCPostRequest): Promise<PassageCommissionMarches> {
    return this.passageCommissionMarchesAccess.postDepotAdditifCDCPassageCommissionMarches(additifCDCId, depotAdditifCDCSecretariatCommissionMarches);
  }

}



