import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { CreateUserComponent } from '../dialogs/create-user/create-user.component';
import { Utilisateur } from '../models/utilisateur.model';
import { DialogAction } from '../enums/dialog-action.enum';
import { UtilisateurService } from '../domaine/business-logic/utilisateurs.service';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {


  utilisateurs: Utilisateur[]
  loadingData = false

  constructor(public dialog: MatDialog, public serviceUtilisateur: UtilisateurService) {
    this.utilisateurs = []
  }

  ngOnInit() {
    this.loadingData = true
    const result:Promise<Utilisateur[]> = this.serviceUtilisateur.getAllUtilisateurs();
    result
    .then((value) =>{
      this.loadingData = false
      return this.utilisateurs = value
    })
    .catch(()=>{
      this.loadingData = false 
      return this.utilisateurs = null
    })
  }
  
  openDialogCreateUtilisateur(): void {

    const dialogRef = this.dialog.open(CreateUserComponent, 
      {
      width: '800px',
      data: {action: DialogAction.CREATE, user: null}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      console.log("Return result", result);
      if(this.utilisateurs && result) {
        this.utilisateurs.push(result)
      }
    });

    
  }

  openDialogUpdateUtilisateur(utilisateur: Utilisateur): void {
    
    console.log("utilisateur", utilisateur)

    const dialogRef = this.dialog.open(CreateUserComponent, 
      {
      width: '800px',
      data: {action: DialogAction.MODIFY, user: utilisateur}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      
      if(this.utilisateurs && result) {
        let user = this.utilisateurs.find(e => e._id === result._id)
        //const index = this.servicesContractants.indexOf(sc);
        //this.servicesContractants.splice(index,1)
        for(var k in user) user[k]=result[k];
      }
      
    });

    
  }

  openDialogDeleteUtilisateur(utilisateur: Utilisateur): void {
    
    console.log("utilisateur:openDialogDeleteUtilisateur", utilisateur)

    const dialogRef = this.dialog.open(CreateUserComponent, 
      {
      width: '800px',
      data: {action: DialogAction.DELETE, user: utilisateur}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      
      if(this.utilisateurs && result) {
        let user = this.utilisateurs.find(e => e._id === result._id);
        const index = this.utilisateurs.indexOf(user);
        this.utilisateurs.splice(index,1);
      }
      
    });

    
  }  



}
