import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { RepresentantOperateurEconomique } from 'src/app/models/representant.operateur.economique.model';
import { RepresentantOperateurEconomiquePostRequest } from 'src/app/requests/representant-operateur-economique-post.request';
import { RepresentantOperateurEconomiquePutRequest } from 'src/app/requests/representant-operateur-economique-put.request';


@Injectable()
export class RepresentantOperateurEconomiqueAccess {

  constructor(private http: HttpClient){}


  async getAllRepresentants(): Promise<RepresentantOperateurEconomique[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<RepresentantOperateurEconomique[]>(
          `${apiEndpoint}/representants-operateurs-economiques`,
          )
        .subscribe(
          representants => { resolve(representants) },
          error => reject(error)
        )

      });
  }


  




  async getRepresentantById(id: String): Promise<RepresentantOperateurEconomique>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<RepresentantOperateurEconomique>(
          `${apiEndpoint}/representants-operateurs-economiques/${id}`,
        )
        .subscribe(
          representant => {resolve(representant)},
          error => reject(error)
        )

      });

  }



  
  


  async getRepresentantByEmail(email: String): Promise<RepresentantOperateurEconomique>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<RepresentantOperateurEconomique[]>(
          `${apiEndpoint}/representants-operateurs-economiques/emails/${email}`,
        )
        .subscribe(
          representant => {
            if(representant.length > 0)
              resolve(representant[0])
            else 
              resolve(null)
          },
          error => reject(error)
        )

      });

  }



  



  async postRepresentant(idOperateurEconomique: string, representant: RepresentantOperateurEconomiquePostRequest): Promise<RepresentantOperateurEconomique> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<RepresentantOperateurEconomique>(
          `${apiEndpoint}/representants-operateurs-economiques/${idOperateurEconomique}`, 
          representant, 
          httpOptions
          )
        .subscribe(
            rprst => { resolve(rprst) },
            error => reject(error)
        )
      }
    );
  }


  async postRepresentantBySC(idCandidat: string, idOperateurEconomique: string, representant: RepresentantOperateurEconomiquePostRequest): Promise<RepresentantOperateurEconomique> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<RepresentantOperateurEconomique>(
          `${apiEndpoint}/representants-operateurs-economiques/${idCandidat}/${idOperateurEconomique}`, 
          representant, 
          httpOptions
          )
        .subscribe(
            rprst => { resolve(rprst) },
            error => reject(error)
        )
      }
    );
  }

  

  async putRepresentant(representant: RepresentantOperateurEconomiquePutRequest, idRepresentant: String): Promise<RepresentantOperateurEconomique> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<RepresentantOperateurEconomique>(
          `${apiEndpoint}/representants-operateurs-economiques/${idRepresentant}`, 
          representant, 
          httpOptions
          )
        .subscribe(
            rprst => { resolve(rprst) },
            error => reject(error)
        )
      }
    );
  }
  



  async deleteRepresentant(idOperateurEconomique:string, idRepresentant:string): Promise<RepresentantOperateurEconomique> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<RepresentantOperateurEconomique>(
          `${apiEndpoint}/representants-operateurs-economiques/${idOperateurEconomique}/${idRepresentant}`
          )
        .subscribe(
            rprst => { resolve(rprst) },
            error => reject(error)
        )
      }
    );
  }
  
} 