import { Injectable } from '@angular/core';
import { ProjetCDC } from 'src/app/models/projet-cdc.model';
import { ProjetCDCPostRequest } from 'src/app/requests/projet-cdc-post.request';
import { ProjetCDCPutRequest } from 'src/app/requests/projet-cdc-put.request';
import { ProjetCDCAccess } from '../data-access/projet-cdc.access';
