import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Utilisateur } from 'src/app/models/utilisateur.model';
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { OperateurEconomiquePostRequest } from 'src/app/requests/operateur-economique-post.request';
import { OperateurEconomiquePutRequest } from 'src/app/requests/operateur-economique-put.request';


@Injectable()
export class OperateurEconomiqueAccess {

  constructor(private http: HttpClient){}


  async getAllOperateursEconomiques(): Promise<OperateurEconomique[]>{
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<OperateurEconomique[]>(
          `${apiEndpoint}/operateurs-economiques`,
          )
        .subscribe(
          operateurs_economiques => { resolve(operateurs_economiques) },
          error => reject(error)
        )

      });
  }

  async getAllOperateursEconomiquesWithRepresentants(): Promise<OperateurEconomique[]>{
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<OperateurEconomique[]>(
          `${apiEndpoint}/operateurs-economiques/representants/`,
          )
        .subscribe(
          operateurs_economiques => { resolve(operateurs_economiques) },
          error => reject(error)
        )

      });
  }




  async getOperateurEconomiqueByNIF(nif: String): Promise<OperateurEconomique>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<OperateurEconomique[]>(
          `${apiEndpoint}/operateurs-economiques/identifiants/${nif}`,
        )
        .subscribe(
          operateur_economique => {
            if(operateur_economique.length > 0)
              resolve(operateur_economique[0])
            else 
              resolve(null)
          },
          error => reject(error)
        )

      });

  }


  async getOperateurEconomiqueById(id: String): Promise<OperateurEconomique>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<OperateurEconomique>(
          `${apiEndpoint}/operateurs-economiques/${id}`,
        )
        .subscribe(
          operateur_economique => {resolve(operateur_economique)},
          error => reject(error)
        )

      });

  }




  
  async postOperateurEconomique(operateurEconomique: OperateurEconomiquePostRequest): Promise<OperateurEconomique>{
       
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<OperateurEconomique>(
          `${apiEndpoint}/operateurs-economiques`, 
          operateurEconomique, 
          httpOptions
          )
        .subscribe(
          operateur_economique => { resolve(operateur_economique) },
            error => reject(error)
        )
      }
    );
  }







  async putOperateurEconomique(operateurEconomique: OperateurEconomiquePutRequest, idOperateurEconomique: String): Promise<OperateurEconomique>{
       
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<OperateurEconomique>(
          `${apiEndpoint}/operateurs-economiques/${idOperateurEconomique}`, 
          operateurEconomique, 
          httpOptions
          )
        .subscribe(
          operateur_economique => { resolve(operateur_economique) },
            error => reject(error)
        )
      }
    );
  }
  

  async putConfirmOperateurEconomique(idOperateurEconomique: String): Promise<OperateurEconomique>{
       
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<OperateurEconomique>(
          `${apiEndpoint}/operateurs-economiques/confirm/${idOperateurEconomique}`, 
          null, 
          httpOptions
          )
        .subscribe(
          operateur_economique => { resolve(operateur_economique) },
            error => reject(error)
        )
      }
    );
  }




  async deleteOperateurEconomique(idOperateurEconomique: String):Promise<OperateurEconomique>{

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<OperateurEconomique>(
          `${apiEndpoint}/operateurs-economiques/${idOperateurEconomique}`
          )
        .subscribe(
          operateur_economique => { resolve(operateur_economique) },
            error => reject(error)
        )
      }
    );
  }
  
} 