import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { DialogAction } from 'src/app/enums/dialog-action.enum'; 
import { NotificationCOPEOService } from 'src/app/domaine/business-logic/notifications-copeos.service';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { Lancement } from 'src/app/models/lancement/lancement.model';
import { NotificationCOPEO } from 'src/app/models/copeo/notification-copeo.model';
import { COPEO } from 'src/app/models/copeo/copeo.model';
import { NotificationCOPEOPostRequest } from 'src/app/requests/notification-copeo-post.request';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';



@Component({
  selector: 'app-create-notification-copeo',
  templateUrl: './create-notification-copeo.component.html',
  styleUrls: ['./create-notification-copeo.component.css']
})
export class CreateNotificationCOPEOComponent implements OnInit {


  constructor(
    public dialogRef: MatDialogRef<CreateNotificationCOPEOComponent>,
    private notificationCOPEOService: NotificationCOPEOService,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      prj: Projet,
      sous_prj: SousProjet,
      elb_cdc: ElaborationCDC,
      lncm: Lancement,
      id_copeo: string
    } 
  ) {
  };



  notificationCOPEOFrom: FormGroup;
  isCreate : boolean;

  // date creation to show (if create: today, if delete of modify: date creation )
  date;
  dateLancement;
  dateElaborationCDC;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
      this.dateLancement = new FormControl(this.data.lncm.dateCreationLancement);
      this.dateElaborationCDC = new FormControl(this.data.elb_cdc.dateCreationElaborationCDC);
    }

        
    this.notificationCOPEOFrom = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
      'intituleProjet': new FormControl({value: this.data.prj.intituleProjet, disabled: true}, [Validators.required]),
      'intituleSousProjet': new FormControl({value: this.data.sous_prj.intituleSousProjet, disabled: true}, [Validators.required]),
      'dateCreationElaborationCDC': new FormControl({value: null, disabled: true}, [Validators.required]),
      'dateCreationLancement': new FormControl({value: null, disabled: true}, [Validators.required]),
      'copeo': new FormControl({value: this.data.id_copeo, disabled: true}, [Validators.required])
    });

  };




  //Data model to submit to the DB server
  notificationCOPEOPostRequest: NotificationCOPEOPostRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.notificationCOPEOFrom);




    // save the data into the database
    let result:Promise<NotificationCOPEO>;
    if (this.isCreate){
      this.notificationCOPEOPostRequest = new NotificationCOPEOPostRequest(
        this.data.prj._id,
        this.data.sous_prj._id,
        this.data.elb_cdc._id,
        this.data.lncm._id
      );      
      result = this.notificationCOPEOService.postNotificationCOPEO(this.data.id_copeo, this.notificationCOPEOPostRequest);
    };


    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value 
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }

 


  
}
