import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Prolongation } from 'src/app/models/prolongation.model';
import { ProlongationService } from 'src/app/domaine/business-logic/prolongations.service';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ProlongationPostRequest } from 'src/app/requests/prolongation-post.request';
import { ProlongationPutRequest } from 'src/app/requests/prolongation-put.request';

export interface DialogData {
  animal: string;
  name: string;
}


@Component({
  selector: 'app-create-prolongation',
  templateUrl: './create-prolongation.component.html',
  styleUrls: ['./create-prolongation.component.css']
})
export class CreateProlongationComponent implements OnInit {


  constructor(
    public dialogRef: MatDialogRef<CreateProlongationComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, 
      prlng: Prolongation
      id_lcm_cnrr: string
    },
    public prolongationService: ProlongationService
  ) {
  };




  prolongationForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;



  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.prlng.dateCreation);
    }




    this.prolongationForm = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
      'nombreJours': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.prolongationForm.patchValue(this.data.prlng);
    }



  };




  //Data model to submit to the DB server
  prolongationPostRequest: ProlongationPostRequest;
  prolongationPutRequest: ProlongationPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.prolongationForm);




    // save the data into the database
    let result:Promise<Prolongation>;
    if (this.isCreate){
      this.prolongationPostRequest = new ProlongationPostRequest(
        this.prolongationForm.value.nombreJours
      );      
      result = this.prolongationService.postProlongation(this.data.id_lcm_cnrr, this.prolongationPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.prolongationPutRequest = new ProlongationPutRequest(
        this.prolongationForm.value.nombreJours
      );      
      result = this.prolongationService.putProlongation(this.data.prlng._id, this.prolongationPutRequest);
    };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.prolongationService.deleteProlongation(this.data.id_lcm_cnrr, this.data.prlng._id);
    };

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });


  }


  
}




