import { Component, OnInit, Inject, Type } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ModePassation } from 'src/app/enums/mode.passation.enum';
import { TypeProcedure } from 'src/app/enums/type.procedure.enum';
import { FormeMarche } from 'src/app/enums/forme.marche.enum';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CaractereMarche } from 'src/app/enums/caractere.marche.enum';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { ElaborationCDCPostRequest } from 'src/app/requests/elaboration-cdc-post.request';
import { ElaborationCDCPutRequest } from 'src/app/requests/elaboration-cdc-put.request';
import { ElaborationCDCService } from 'src/app/domaine/business-logic/elaborations-cdc.service';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { NaturePrestation } from 'src/app/enums/nature.prestation.enum';
import { Niveau } from 'src/app/enums/niveau.enum';


@Component({
  selector: 'app-create-elaboration-cdc',
  templateUrl: './create-elaboration-cdc.component.html',
  styleUrls: ['./create-elaboration-cdc.component.css']
})
export class CreateElaborationCDCComponent implements OnInit {
  disabled: boolean = false;
  intituleOperation: string; // "Intitulé de l'opération"
  checkedIntituleProjetDifferent: boolean = false;

  //current date
  date = new FormControl(new Date());

  //ModesPassation
  modesPassation = Object.keys(ModePassation).slice(0)
  parseValueModePassation(value : string) {
    return ModePassation[value].toString();
  }

  //TypesProcedure
  typesProcedure = Object.keys(TypeProcedure).slice(0)
  parseValueTypeProcedure(value : string) {
    return TypeProcedure[value].toString();
  }

  //FormesMarches
  formesMarche = Object.keys(FormeMarche).slice(0)
  parseValueFormeMarche(value : string) {
    return FormeMarche[value].toString();
  }

  //caracteresMarches
  caracteresMarche = Object.keys(CaractereMarche).slice(0);
  parseValueCaractereMarche(value : string) {
    return CaractereMarche[value].toString();
  }

  

  constructor(
    public dialogRef: MatDialogRef<CreateElaborationCDCComponent>,
    private elaborationCDCService: ElaborationCDCService,
    @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, elbCDC: ElaborationCDC, sousPrj: SousProjet, prj: Projet}
    ) {}
    

  createAdditif(): void {
    this.dialogRef.close({
      intituleOperation: "this.name"
    });
  }

  onNoClick(): void {
    this.dialogRef.close({
      intituleOperation: "this.name"
    });
  }

  elaborationCDCForm: FormGroup;
  
  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

  ngOnInit() {
    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    } else {
      this.date = new FormControl(this.data.elbCDC.dateCreationElaborationCDC);
    }

    this.elaborationCDCForm = new FormGroup({
      'dateCreationElaborationCDC':new FormControl({value: null, disabled: true}, [Validators.required]),
      'typeProcedure': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, this.isAuthorizedTypeProcedure.bind(this)]),
      'modePassation': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'formeMarche': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'caractereMarche': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });


    if(this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.elaborationCDCForm.patchValue(this.data.elbCDC);
    }    
  }

  
  
  isAuthorizedTypeProcedure(control: FormControl): any {      
    const value = control.value;
    if (!value) {
      return null;
    }
    if( TypeProcedure[value] !== this.getAuthorizesTypeProcedure()){
      return { "wrongTypeProcedure": true };
    } 
    return null;
  }

  getAuthorizesTypeProcedure(): TypeProcedure{
    switch(NaturePrestation[this.data.sousPrj.naturePrestation]){
      case NaturePrestation.TRAVAUX:
      case NaturePrestation.FOURNITURES:
      switch(Niveau[this.data.prj.serviceContractant.niveau]){
        case Niveau.CENTRAL:
          if(this.data.sousPrj.estimationAdministrative>=20000000) return TypeProcedure.PF;
          else if(this.data.sousPrj.estimationAdministrative<=2000000) return TypeProcedure.PABC;
          else return TypeProcedure.PAC;
          break;
        case Niveau.REGIONAL:
        case Niveau.UNITE: 
        if(this.data.sousPrj.estimationAdministrative>=12000000) return TypeProcedure.PF;
        else if(this.data.sousPrj.estimationAdministrative<=1000000) return TypeProcedure.PABC;
        else return TypeProcedure.PAC;
          break
      }

      break;
      case NaturePrestation.ETUDES:
      case NaturePrestation.SERVICES: 
      switch(Niveau[this.data.prj.serviceContractant.niveau]){
        case Niveau.CENTRAL:
          if(this.data.sousPrj.estimationAdministrative>=12000000) return TypeProcedure.PF;
          else if(this.data.sousPrj.estimationAdministrative<=1000000) return TypeProcedure.PABC;
          else return TypeProcedure.PAC;
          break;
        case Niveau.REGIONAL:
        case Niveau.UNITE: 
          if(this.data.sousPrj.estimationAdministrative>=6000000) return TypeProcedure.PF;
          else if(this.data.sousPrj.estimationAdministrative<=500000) return TypeProcedure.PABC;
          else return TypeProcedure.PAC;
          break
      }      
      break;      
    }
      
  }


  //Data model to submit to the DB server
  elaborationCDCPostRequest: ElaborationCDCPostRequest;
  elaborationCDCPutRequest: ElaborationCDCPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.elaborationCDCForm);




    // save the data into the database
    let result:Promise<ElaborationCDC>;
    if (this.isCreate){
      this.elaborationCDCPostRequest = new ElaborationCDCPostRequest(
        this.elaborationCDCForm.value.typeProcedure,
        this.elaborationCDCForm.value.modePassation,
        this.elaborationCDCForm.value.formeMarche,
        this.elaborationCDCForm.value.caractereMarche
      );      
      result = this.elaborationCDCService.postElaborationCDC(this.elaborationCDCPostRequest, this.data.sousPrj._id);
    };

    // update the data in the database
    if(this.isModify){
      this.elaborationCDCPutRequest = new ElaborationCDCPutRequest(
        this.elaborationCDCForm.value.typeProcedure,
        this.elaborationCDCForm.value.modePassation,
        this.elaborationCDCForm.value.formeMarche,
        this.elaborationCDCForm.value.caractereMarche
      );      
      result = this.elaborationCDCService.putElaborationCDC(this.elaborationCDCPutRequest, this.data.elbCDC._id);
    };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.elaborationCDCService.deleteElaborationCDC(this.data.sousPrj._id, this.data.elbCDC._id);
    };

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }





}
