import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { Projet } from '../models/repertoire/projet.model';
import { SousProjet } from '../models/repertoire/sous-projet.model';
import { Filter } from '../models/repertoire/filter.model';
import { CreateSousProjetComponent } from '../dialogs/create-sous-projet/create-sous-projet.component';
import { ProjetService } from '../domaine/business-logic/projets.service';
import { CreateProjetComponent } from '../dialogs/create-projet/create-projet.component';
import { DialogAction } from '../enums/dialog-action.enum';
import { UploadService } from '../upload.service';
import { fileUploadUrl } from '../config';
import { CreateCommissionCompetenteComponent } from '../dialogs/create-commission-competente/create-commission-competente.component';
import { CommissionCompetente } from '../models/commission-competente/commission-competente.model';
import { CommissionCompetenteService } from '../domaine/business-logic/commissions-competentes.service';
import { CreateModificationCommissionCompetenteComponent } from '../dialogs/create-modification-commission-competente/create-modification-commission-competente.component';
import { ModificationCommissionCompetente } from '../models/commission-competente/modification-commission-competente.model';


@Component({
  selector: 'app-commissions',
  templateUrl: './commissions.component.html',
  styleUrls: ['./commissions.component.css']
})

export class CommissionsComponent implements OnInit {


  commissionsCompetentes: CommissionCompetente[];
  loadingData = false;
  displayedColumnsMembres: string[] = ['role', 'nom', 'prenom', 'qualite', 'matricule', 'email', 'telephone', 'penta', 'action'];
  constructor(
    public dialog: MatDialog, 
    public serviceCommissionCompetente: CommissionCompetenteService,
    private uploadService: UploadService
  ) {
    this.commissionsCompetentes = [];
  };

  ngOnInit() {
    this.loadingData = true;
    const result:Promise<CommissionCompetente[]> = this.serviceCommissionCompetente.getAllCommissionsCompetentes();
    result
    .then((value) =>{
      this.loadingData = false;
      return this.commissionsCompetentes = value;
    })
    .catch(()=>{
      this.loadingData = false;
      return this.commissionsCompetentes = null;
    })
  }




  openDialogCreateCommissionCompetente(): void {
    const dialogRef = this.dialog.open(CreateCommissionCompetenteComponent, 
      {
      width: '800px',
      data: {action: DialogAction.CREATE, comm_compt: null}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      console.log("Return result", result);
      if(this.commissionsCompetentes && result) {
        this.commissionsCompetentes.push(result);
      }
    });
  }


  openDialogUpdateCommissionCompetente(commissionCompetente:CommissionCompetente): void {
    const dialogRef = this.dialog.open(CreateCommissionCompetenteComponent, 
      {
      width: '800px',
      data: {action: DialogAction.MODIFY, comm_compt: commissionCompetente}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      if(this.commissionsCompetentes && result) {
        let comm_compt = this.commissionsCompetentes.find(e => e._id === result._id);
        for(var k in result){
           comm_compt[k]=result[k];
        }
      }
    });
  };

  openDialogDeleteCommissionCompetente(commissionCompetente:CommissionCompetente): void {
    const dialogRef = this.dialog.open(CreateCommissionCompetenteComponent, 
      {
      width: '800px',
      data: {action: DialogAction.DELETE, comm_compt: commissionCompetente}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      if(this.commissionsCompetentes && result) {
        let sc = this.commissionsCompetentes.find(e => e._id === result._id);
        const index = this.commissionsCompetentes.indexOf(sc);
        this.commissionsCompetentes.splice(index,1)
      }
    });    
  };






  openDialogCreateModificationCommissionCompetente(commissionCompetente: CommissionCompetente): void {

    const dialogRef = this.dialog.open(CreateModificationCommissionCompetenteComponent, {
      width: '800px',
      data: {action: DialogAction.CREATE, comm_compt: commissionCompetente, modif_comm_compt: null}
    });

    dialogRef.afterClosed().subscribe(result => {
      if (commissionCompetente && result) {
        commissionCompetente.modificationsCommissionCompetente.push(result);
      }
    });
  }



  openDialogDeleteModificationCommissionCompetente(commissionCompetente: CommissionCompetente, modificationCommissionCompetente: ModificationCommissionCompetente): void{
    console.log("prj:", commissionCompetente);
    console.log("sousPrj", modificationCommissionCompetente);
    const dialogRef = this.dialog.open(CreateModificationCommissionCompetenteComponent, {
      width: '800px',
      data: {action: DialogAction.DELETE, comm_compt: commissionCompetente, modif_comm_compt: modificationCommissionCompetente}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(commissionCompetente && result) {
        let modif = commissionCompetente.modificationsCommissionCompetente.find(e => e._id === result._id);
        const index = commissionCompetente.modificationsCommissionCompetente.indexOf(modif);
        commissionCompetente.modificationsCommissionCompetente.splice(index,1);
      }
    });    
  };

  openDialogUpdateModificationCommissionCompetente(commissionCompetente:CommissionCompetente, modificationCommissionCompetente:ModificationCommissionCompetente): void{
    const dialogRef = this.dialog.open(CreateModificationCommissionCompetenteComponent, {
      width: '800px',
      data: {action: DialogAction.MODIFY, comm_compt: commissionCompetente, modif_comm_compt: modificationCommissionCompetente}
    });

    dialogRef.afterClosed().subscribe(result => {
      if(Projet && result) {
        let modif = commissionCompetente.modificationsCommissionCompetente.find(e => e._id === result._id);
        for(var k in result) modif[k]=result[k];
      }
    });  
  };
/*
  downloadJustificatif(filename : string) : void {
    this.uploadService.get(filename);
  }
  */
  getPathFile(filename:string){
    return `${fileUploadUrl}/sendMePDF/${filename}`;
  }


}
