import { Injectable } from '@angular/core';
import { ProjetAccess } from '../data-access/projet.access';
import { Projet } from 'src/app/models/repertoire/projet.model';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';

@Injectable()
export class ProjetService {

  constructor(private projetAccess: ProjetAccess){}

  getAllProjets(): Promise<Projet[]> {
    return this.projetAccess.getAllProjets();
  }

  getProjet(idProjet: string): Promise<Projet>{
    return this.projetAccess.getProjet(idProjet);
  }

  getProjetByCode(codeProjet: String): Promise<Projet> {
    return this.projetAccess.getProjetByCode(codeProjet);
  }

  postProjet(projet: ProjetPostRequest): Promise<Projet> {
    return this.projetAccess.postProjet(projet);
  }

  putProjet(projet: ProjetPutRequest, id: String): Promise<Projet> {
    return this.projetAccess.putProjet(projet, id);
  }

  deleteProjet(id: String):Promise<Projet> {
    return this.projetAccess.deleteProjet(id);
  }
}



