import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';

export interface DialogData {
  animal: string;
  name: string;
}

export interface PeriodicElement {
  position:number;
  name: string;
}

const ELEMENT_DATA: PeriodicElement[] = [
  {position: 1, name:'mobilis'},
  {position: 2, name:'sntf'},
  {position: 3, name:'cosider'},
];


@Component({
  selector: 'app-create-demande-clarification',
  templateUrl: './create-demande-clarification.component.html',
  styleUrls: ['./create-demande-clarification.component.css']
})
export class CreateDemandeClarificationComponent implements OnInit {

  
  disabled: boolean = false;
  intituleOperation: string; // "Intitulé de l'opération"
  checkedIntituleProjetDifferent: boolean = false;

  nonCandidatChecked: boolean = false

  constructor(
    public dialogRef: MatDialogRef<CreateDemandeClarificationComponent>,
    @Inject(MAT_DIALOG_DATA) public serviceContractant: string) {
    console.log(serviceContractant)
  }

  createAdditif(): void {
    this.dialogRef.close({
      intituleOperation: "this.name"
    });
  }

  onNoClick(): void {
    this.dialogRef.close({
      intituleOperation: "this.name"
    });
  }

  ngOnInit() {
  }



  displayedColumns: string[] = ['select', 'position', 'name'];
  dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
  selection = new SelectionModel<PeriodicElement>(true, []);

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: PeriodicElement): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.position + 1}`;
  }
}
