import { Injectable } from '@angular/core';
import { ProjetAccess } from '../data-access/projet.access';
import { ProjetPostRequest } from 'src/app/requests/projet-post.request';
import { ProjetPutRequest } from 'src/app/requests/projet-put.request';
import { DeliberationCOPEO } from 'src/app/models/copeo/deliberation-copeo.model';
import { DeliberationCOPEOAccess } from '../data-access/deliberation-copeo.access';
import { DeliberationCOPEOPostRequest } from 'src/app/requests/deliberation-copeo-post.request';
import { DeliberationCOPEOPutRequest } from 'src/app/requests/deliberation-copeo-put.request';

@Injectable()
export class DeliberationCOPEOService {

  constructor(private deliberationCOPEOAccess: DeliberationCOPEOAccess){}

  getAllDeliberations(): Promise<DeliberationCOPEO[]> {
    return this.deliberationCOPEOAccess.getAllDeliberations();
  }

  getDeliberationCOPEO(idDeliberationCOPEO: string): Promise<DeliberationCOPEO>{
    return this.deliberationCOPEOAccess.getDeliberationCOPEO(idDeliberationCOPEO);
  }
 

  postDeliberationCOPEO(idNotificationCOPEO: string, deliberationCOPEOPostRequest: DeliberationCOPEOPostRequest): Promise<DeliberationCOPEO> {
    return this.deliberationCOPEOAccess.postDeliberationCOPEO(idNotificationCOPEO, deliberationCOPEOPostRequest);
  }

  putDeliberationCOPEO(idDeliberationCOPEO: String, deliberationCOPEOPutRequest: DeliberationCOPEOPutRequest): Promise<DeliberationCOPEO> {
    return this.deliberationCOPEOAccess.putDeliberationCOPEO(idDeliberationCOPEO, deliberationCOPEOPutRequest);
  }

  deleteDeliberationCOPEO(idNotificationCOPEO: string, idDeliberationCOPEO: String): Promise<DeliberationCOPEO> {
    return this.deliberationCOPEOAccess.deleteDeliberationCOPEO(idNotificationCOPEO, idDeliberationCOPEO);
  }
}



