import { Component, Input, ViewChild } from '@angular/core';
import { MatTable, MatDialog } from '@angular/material';
import { RallongeFinanciere } from 'src/app/models/rallonge.financiere.model';
import { CreateRecoursComponent } from 'src/app/dialogs/create-recours/create-recours.component';
import { Recours } from 'src/app/models/recours.model';
import { CreatePassageCopeoOuverturePlisComponent } from 'src/app/dialogs/create-passage-copeo-ouverture-plis/create-passage-copeo-ouverture-plis.component';
import { CreatePassageCopeoEvaluationOffresComponent } from 'src/app/dialogs/create-passage-copeo-evaluation-offres/create-passage-copeo-evaluation-offres.component';
import { CreateDemandeRallongeFinanciereComponent } from 'src/app/dialogs/create-demande-rallonge-financiere/create-demande-rallonge-financiere.component';
import { CreateReponseRallongeFinanciereComponent } from 'src/app/dialogs/create-reponse-rallonge-financiere/create-reponse-rallonge-financiere.component';
import { Lancement } from 'src/app/models/lancement/lancement.model';
import { ElaborationCDC } from 'src/app/models/elaboration.cdc.model';
import { SousProjet } from 'src/app/models/repertoire/sous-projet.model';
import { Projet } from 'src/app/models/repertoire/projet.model';


@Component({
  selector: 'app-choix-partenaire',
  templateUrl: './choix_partenaire.component.html',
  styleUrls: ['./choix_partenaire.component.css']
})

export class ChoixPartenaireComponent {
  
  @ViewChild("tableRallongesFinancieres", {static: true}) tableRallongesFinancieres: MatTable<RallongeFinanciere>;

  @Input() lancement : Lancement;
  @Input() elaborationCDC : ElaborationCDC;
  @Input() sousProjet : SousProjet;
  @Input() projet : Projet;
  
  dataSourceRallongesFinancieres: RallongeFinanciere [] = []
  displayedColumnsRallongesFinancieres: string[] = [
    "serviceContractant",
    "dateDemande",
    "montantDemande",
    "demandePath",
    "reponse"
  ];


        

  constructor(public dialog: MatDialog) { }


  demandeRallongeChecked: boolean
  ngOnInit() {
    this.demandeRallongeChecked = false


  }

          


  openDialogCreateRecours(): void {
    const dialogRef = this.dialog.open(CreateRecoursComponent, {
      width: '800px',
      data: "nouveauLitige"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

    //TODO, si auccune offre ocnfomre/insuff finance
    // recours est possible 
    
    /*
    this.recours.push(Recours.getDummy())
    this.tableRecours.renderRows();
    */
  }
    


  openDialogSelectPassageCOPEOOuverturePlis(): void {
    const dialogRef = this.dialog.open(CreatePassageCopeoOuverturePlisComponent, {
      width: '800px',
      data: "passage commission ouverture plis"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
  }
 

  openDialogSelectPassageCOPEOEvaluationOffres(): void {
    const dialogRef = this.dialog.open(CreatePassageCopeoEvaluationOffresComponent, {
      width: '800px',
      data: "passage commission ouverture plis"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });
  }
 
  openDialogCreateDemandeRallongeFinanciere():void{
    const dialogRef = this.dialog.open(CreateDemandeRallongeFinanciereComponent, {
      width: '800px',
      data: "passage commission ouverture plis"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
    });

    this.dataSourceRallongesFinancieres.push(RallongeFinanciere.getDummy())
    this.tableRallongesFinancieres.renderRows(); 
  }

 
  openDialogCreateReponseRallongeFinanciere():void {
    const dialogRef = this.dialog.open(CreateReponseRallongeFinanciereComponent, {
      width: '800px',
      data: "reponse rallonge financiere"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });    
  }




}
