import { Injectable } from '@angular/core';
import { UtilisateurAccess } from '../data-access/utilisateur.access';
import { Utilisateur } from 'src/app/models/utilisateur.model';
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { OperateurEconomiqueAccess } from '../data-access/operateur-economique.access';
import { OperateurEconomiquePostRequest } from 'src/app/requests/operateur-economique-post.request';
import { OperateurEconomiquePutRequest } from 'src/app/requests/operateur-economique-put.request';

@Injectable()
export class OperateurEconomiqueService {

  constructor(private operateurEconomiqueAccess: OperateurEconomiqueAccess){}

  
  getAllOperateursEconomiques(): Promise<OperateurEconomique[]>{
    return this.operateurEconomiqueAccess.getAllOperateursEconomiques();
  }
 


  getAllOperateursEconomiquesWithRepresentants(): Promise<OperateurEconomique[]>{
    return this.operateurEconomiqueAccess.getAllOperateursEconomiquesWithRepresentants();
  }
 
  getOperateurEconomiqueByNIF(nif: String): Promise<OperateurEconomique>{
    return this.operateurEconomiqueAccess.getOperateurEconomiqueByNIF(nif);
  }

  getOperateurEconomiqueById(idOperateurEconomique: String): Promise<OperateurEconomique>{
    return this.operateurEconomiqueAccess.getOperateurEconomiqueById(idOperateurEconomique);
  }

  postOperateurEconomique(operateurEconomique: OperateurEconomiquePostRequest): Promise<OperateurEconomique>{
    return this.operateurEconomiqueAccess.postOperateurEconomique(operateurEconomique);
  }

  putOperateurEconomique(operateurEconomique: OperateurEconomiquePutRequest, idOperateurEconomique: String): Promise<OperateurEconomique>{
    return this.operateurEconomiqueAccess.putOperateurEconomique(operateurEconomique, idOperateurEconomique);
  }

  deleteOperateurEconomique(idOperateurEconomique: String):Promise<OperateurEconomique>{
    return this.operateurEconomiqueAccess.deleteOperateurEconomique(idOperateurEconomique);
  }

  putConfirmOperateurEconomique(idOperateurEconomique: String):Promise<OperateurEconomique>{
    return this.operateurEconomiqueAccess.putConfirmOperateurEconomique(idOperateurEconomique);
  }


}



