import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatTable } from '@angular/material';
import { DialogAction } from '../enums/dialog-action.enum';
import { CalendarEntry } from '../models/calendar.entry.model';
import { CalendarEntryService } from '../domaine/business-logic/calendar-entries.service';
import { CreateCalendarEntryComponent } from '../dialogs/create-calendar-entry/create-calendar-entry.component';

@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.css']
})



export class CalendarComponent implements OnInit {
  @ViewChild("tableCalendarEntries", {static: true}) tableCalendarEntries: MatTable<CalendarEntry>;

  displayedColumns: string[] = ['date', 'event', 'action'];
  calendarEntries: CalendarEntry[];
  loadingData = false;

  constructor(public dialog: MatDialog, public calendarEntryService: CalendarEntryService) {
    this.calendarEntries = [];
  }

  ngOnInit() {
    this.loadingData = true;
    const result:Promise<CalendarEntry[]> = this.calendarEntryService.getAllCalendarEntries();
    result
    .then((value) =>{
      this.loadingData = false;
      return this.calendarEntries = value;
    })
    .catch(()=>{
      this.loadingData = false ;
      return this.calendarEntries = null;
    })
  }
  
  openDialogCreateCalendarEntry(): void {

    const dialogRef = this.dialog.open(CreateCalendarEntryComponent, 
      {
      width: '800px',
      data: {action: DialogAction.CREATE, clnd_entry: null}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      console.log("Return result", result);
      if(this.calendarEntries && result) {
        this.calendarEntries.push(result);
        this.tableCalendarEntries.renderRows();
      }
    });

    
  }

  openDialogUpdateCalendarEntry(calendarEntry: CalendarEntry): void {


    const dialogRef = this.dialog.open(CreateCalendarEntryComponent, 
      {
      width: '800px',
      data: {action: DialogAction.MODIFY, clnd_entry: calendarEntry}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      
      if(this.calendarEntries && result) {
        let elm = this.calendarEntries.find(e => e._id === result._id)
        //const index = this.servicesContractants.indexOf(sc);
        //this.servicesContractants.splice(index,1)
        for(var k in elm) elm[k]=result[k];
        this.tableCalendarEntries.renderRows();
      }
      
    });

    
  }

  openDialogDeleteCalendarEntry(calendarEntry: CalendarEntry): void {

    const dialogRef = this.dialog.open(CreateCalendarEntryComponent, 
      {
      width: '800px',
      data: {action: DialogAction.DELETE, clnd_entry: calendarEntry}
      }
    );

    dialogRef.afterClosed().subscribe(result => {
      
      if(this.calendarEntries && result) {
        let elm = this.calendarEntries.find(e => e._id === result._id)
        const index = this.calendarEntries.indexOf(elm);
        this.calendarEntries.splice(index,1);
        this.tableCalendarEntries.renderRows();
      }
      
    });

    
  }

}
