import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable} from '@angular/core';
import { OrganePresse } from 'src/app/models/organe.presse.model';
import { CanalAffichagePostRequest } from 'src/app/requests/canal-affichage-post.request';
import { CanalAffichage } from 'src/app/models/canal-affichage.model';
import { CanalAffichagePutRequest } from 'src/app/requests/canal-affichage-put.request';


@Injectable()
export class CanalAffichageAccess {

  constructor(private http: HttpClient){}

  async postCanalAffichageProlongation(idProlongation: string, canalAffichage: CanalAffichagePostRequest): Promise<CanalAffichage> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<CanalAffichage>(
          `${apiEndpoint}/canaux-affichage/prolongations/${idProlongation}`, 
          canalAffichage, 
          httpOptions
          )
        .subscribe(
          canalAffichage => { resolve(canalAffichage) },
            error => reject(error)
        )
      }
    );
  };



  async postCanalAffichage(idLancementConcurrence: string, canalAffichage: CanalAffichagePostRequest): Promise<CanalAffichage> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<CanalAffichage>(
          `${apiEndpoint}/canaux-affichage/${idLancementConcurrence}`, 
          canalAffichage, 
          httpOptions
          )
        .subscribe(
          canalAffichage => { resolve(canalAffichage) },
            error => reject(error)
        )
      }
    );
  };


  async putCanalAffichage(idCanalAffichage: string, canalAffichagePutRequest: CanalAffichagePutRequest): Promise<CanalAffichage> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the put query
        this.http.put<CanalAffichage>(
          `${apiEndpoint}/canaux-affichage/${idCanalAffichage}`, 
          canalAffichagePutRequest, 
          httpOptions
          )
        .subscribe(
          canalAffichage => { resolve(canalAffichage) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteCanalAffichage(idLancementConcurrence: String, idCanalAffichage: string):Promise<CanalAffichage> {

    return new Promise(
      (resolve, reject) => {
        
        //send the delete query
        this.http.delete<CanalAffichage>(
          `${apiEndpoint}/canaux-affichage/${idLancementConcurrence}/${idCanalAffichage}`
          )
        .subscribe(
          canalAffichage => { resolve(canalAffichage) },
            error => reject(error)
        )
      }
    );
  }
  
  async deleteCanalAffichageProlongation(idProlongation: String, idCanalAffichage: string):Promise<CanalAffichage> {

    return new Promise(
      (resolve, reject) => {
        
        //send the delete query
        this.http.delete<CanalAffichage>(
          `${apiEndpoint}/canaux-affichage/${idProlongation}/${idCanalAffichage}`
          )
        .subscribe(
          canalAffichage => { resolve(canalAffichage) },
            error => reject(error)
        )
      }
    );
  }
  

};