import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { UploadService } from 'src/app/upload.service';
import { ServiceContractantService } from 'src/app/domaine/business-logic/services-contractants.service';
import { CommissionCompetente } from 'src/app/models/commission-competente/commission-competente.model';
import { TypeCommissionCompetente } from 'src/app/enums/type-commission-competente.enum';
import { RoleMembreCommissionCompetente } from 'src/app/enums/role-membre-commission-competente.enum';
import { MembreCommissionCompetente } from 'src/app/models/commission-competente/membre-commission-competente.model';
import { CommissionCompetentePostRequest } from 'src/app/requests/commission-competente-post.request';
import { CommissionCompetenteService } from 'src/app/domaine/business-logic/commissions-competentes.service';
import { CommissionCompetentePutRequest } from 'src/app/requests/commission-competente-put.request';

@Component({
  selector: 'app-create-commission-competente',
  templateUrl: './create-commission-competente.component.html',
  styleUrls: ['./create-commission-competente.component.css']
})
export class CreateCommissionCompetenteComponent implements OnInit {
  
    constructor(
      public dialogRef: MatDialogRef<CreateCommissionCompetenteComponent>,
      private commissionCompetenteService: CommissionCompetenteService,
      @Inject(MAT_DIALOG_DATA) public data: {action: DialogAction, comm_compt: CommissionCompetente},
      private uploadService: UploadService,
      public serviceContractantService: ServiceContractantService
    ) {
  
    };
  
  
  
  
    commissionCompetenteForm: FormGroup;
    membreCommissionCompetenteForm: FormGroup;
  
    isCreate : boolean;
    isModify: boolean;
    isDelete: boolean;
  
  
    // date creation to show (if create: today, if delete of modify: date creation )
    date;
  
    membresCommissionCompetente : MembreCommissionCompetente[] = [];
    displayedColumnsMembres: string[] = ['role', 'nom', 'prenom', 'qualite', 'matricule', 'email', 'telephone', 'penta'];

    isAjouterMembre : boolean = false;
    toggleAjouterMembre(){
      this.isAjouterMembre = !this.isAjouterMembre;
    }
    
    ngOnInit() {


      this.isCreate = this.data.action === DialogAction.CREATE;
      this.isModify = this.data.action === DialogAction.MODIFY;
      this.isDelete = this.data.action === DialogAction.DELETE;
  
      //set the date of creation
      if (this.isCreate){
        this.date = new FormControl(new Date());
        this.membresCommissionCompetente = [];
      }else{
        this.date = new FormControl(this.data.comm_compt.dateCreationCommissionCompetente);
        this.membresCommissionCompetente = [...this.data.comm_compt.membresCommissionCompetente];
      }
  console.log(this.data.comm_compt);
  
      this.commissionCompetenteForm = new FormGroup({
        'dateCreationCommissionCompetente': new FormControl({value: null, disabled: true}, [Validators.required]),
        'typeCommission': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'membresCommissionCompetente': new FormControl({value: 0, disabled: false},[this.checkNombreMembres.bind(this)]),
        'numero': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'designation': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'pathJustificatifDecision': new FormControl({value: null, disabled: false}, [Validators.required])
      });

  
      if (this.data.action === DialogAction.MODIFY ||
        this.data.action === DialogAction.DELETE ){
        this.commissionCompetenteForm.patchValue(this.data.comm_compt);
        this.commissionCompetenteForm.patchValue({"membresCommissionCompetente" : this.membresCommissionCompetente.length});
      }
  

      this.membreCommissionCompetenteForm = new FormGroup({
        'role': new FormControl({value: null, disabled: false}, [Validators.required]),
        'nom': new FormControl({value: null, disabled: false}, [Validators.required]),
        'prenom': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'qualite': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'matricule': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'email': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.email]),
        'telephone': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
        'penta': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
      });

    };

  
    //Type Commissions competentes
    typesCommissionsCompetentes = Object.keys(TypeCommissionCompetente).slice(0);
    parseValueTypeCommissionCompetente(value : string) {
      return TypeCommissionCompetente[value].toString();
    }
  
    //Roles des membres
    rolesMembresCommissionCompetente = Object.keys(RoleMembreCommissionCompetente).slice(0);
    parseValueRoleMembreCommissionCompetente(value : string) {
      return RoleMembreCommissionCompetente[value].toString();
    }
  


    checkNombreMembres(control: FormControl): any {      
      const value = control.value;
      console.log("value", value);
      if (!value || parseInt(value) == 0) {
        return { "wrongMinMembres": true };
      }
      return null;
    }



    onSubmitMembre() {
      this.membresCommissionCompetente.push(new MembreCommissionCompetente(
        this.membreCommissionCompetenteForm.value.role,
        this.membreCommissionCompetenteForm.value.nom,
        this.membreCommissionCompetenteForm.value.prenom,
        this.membreCommissionCompetenteForm.value.qualite,
        this.membreCommissionCompetenteForm.value.matricule,
        this.membreCommissionCompetenteForm.value.email,
        this.membreCommissionCompetenteForm.value.telephone,
        this.membreCommissionCompetenteForm.value.penta
      ));
      this.membresCommissionCompetente = [...this.membresCommissionCompetente];
      //this.tableMembresCommissionCompetente.renderRows();
      this.commissionCompetenteForm.patchValue({"membresCommissionCompetente" : this.membresCommissionCompetente.length});

      this.membreCommissionCompetenteForm.reset();
      this.isAjouterMembre = false;

    }


    //Data model to submit to the DB server
    commissionCompetentePostRequest: CommissionCompetentePostRequest;
    commissionCompetentePutRequest: CommissionCompetentePutRequest;
  
    loadingData: boolean= false;
    errorLoadingData: boolean = false;
  
    onSubmit() {
      this.loadingData = true;
      this.errorLoadingData = false;
      console.log("Submitted", this.commissionCompetenteForm);
  
  
  
  
      // save the data into the database
      let result:Promise<CommissionCompetente>;
      if (this.isCreate){
        this.commissionCompetentePostRequest = new CommissionCompetentePostRequest(
          this.commissionCompetenteForm.value.numero,
          this.commissionCompetenteForm.value.designation,
          this.commissionCompetenteForm.value.typeCommission,
          this.membresCommissionCompetente,
          this.commissionCompetenteForm.value.pathJustificatifDecision
        );      
        result = this.commissionCompetenteService.postCommissionCompetente(this.commissionCompetentePostRequest);
      };
  
      // update the data in the database
      if(this.isModify){
        this.commissionCompetentePutRequest = new CommissionCompetentePutRequest(
          this.commissionCompetenteForm.value.numero,
          this.commissionCompetenteForm.value.designation,
          this.commissionCompetenteForm.value.typeCommission,
          this.membresCommissionCompetente
          .filter(e => !e.dateCreationMembreCommissionCompetente)
          .map(function(e){
            return new MembreCommissionCompetente(e.role,e.nom,e.prenom,e.qualite, e.matricule, e.email,e.telephone,e.penta)
          }),
          //.map(function(e){delete e._id; return e;}),
          this.commissionCompetenteForm.value.pathJustificatifDecision
        );      
        result = this.commissionCompetenteService.putCommissionCompetente(this.commissionCompetentePutRequest, this.data.comm_compt._id);
      };
  
      // delete le S/C data from the database
      if(this.isDelete){
        result = this.commissionCompetenteService.deleteCommissionCompetente(this.data.comm_compt._id);
      };
      console.log("Result", result);
  
      result
      .then((value) => {
        this.loadingData = false;
        this.dialogRef.close(
          {
            _id: value._id , 
            numero : value.numero,
            designation: value.designation,
            typeCommission: value.typeCommission,
            dateCreationCommissionCompetente: value.dateCreationCommissionCompetente,
            modificationsCommissionCompetente: value.modificationsCommissionCompetente,
            membresCommissionCompetente: value.membresCommissionCompetente,
            pathJustificatifDecision: value.pathJustificatifDecision
          }
        )
      })
      .catch(()=>{
        this.loadingData = false;
        return this.errorLoadingData = true;
      });
  
  
  
    }
  
  
    uploadingFile = false;
    onlyPdfAllowed = false;
    failedUploadFile = false;
  
    onFileChange(event) {
      this.uploadingFile = true;
      this.failedUploadFile = false;
      this.onlyPdfAllowed = false;
      const files : File[] = event.target.files;
      if (files[0].type != 'application/pdf'){
        this.onlyPdfAllowed = true;
        this.uploadingFile = false;
      } else {
        const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
        resultUploadFile.then((val:any) => {
          if(val.hasOwnProperty("filename")){
            this.commissionCompetenteForm.patchValue({'pathJustificatifDecision' : val.filename});
          }
          this.uploadingFile = false;
        }).catch((err =>{
          this.failedUploadFile = true;
          this.uploadingFile = false;
        }));
      }
      
    }
  
  
    
  }
  