import { ServiceContractant } from 'src/app/models/service.contractant.model';
import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable, Query } from '@angular/core';
import { Candidat } from 'src/app/models/candidat.model';
import { CandidatPostRequest } from 'src/app/requests/candidat-post.request';
import { CandidatPutRequest } from 'src/app/requests/candidat-put.request';


@Injectable()
export class CandidatAccess {

  constructor(private http: HttpClient){} 

  async getAllCandidats(): Promise<Candidat[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Candidat[]>(
          `${apiEndpoint}/candidats`,
          )
        .subscribe(
          candidats => { resolve(candidats) },
          error => reject(error)
        )

      });
  }

  async getCandidatByOperEconId(idRetraitCDC: string, idOperateurEconomique: string): Promise<Candidat>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Candidat[]>(
          `${apiEndpoint}/candidats/operateurs/${idRetraitCDC}/${idOperateurEconomique}`,
        )
        .subscribe(
          candidat => {
            if(candidat.length > 0)
              resolve(candidat[0]);
            else 
              resolve(null);
          },
          error => reject(error)
        )

      });

  }

  async getCandidatByNumero(idRetraitCDC: string, numero: string): Promise<Candidat>{

    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<Candidat[]>(
          `${apiEndpoint}/candidats/numeros/${idRetraitCDC}/${numero}`,
        )
        .subscribe(
          candidat => {
            console.log(candidat);
            if(candidat.length > 0)
              resolve(candidat[0]);
            else 
              resolve(null);
          },
          error => reject(error)
        )

      });

  }
   

  async postCandidat(idRetraitCDC: string, candidatPostRequest: CandidatPostRequest): Promise<Candidat> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<Candidat>(
          `${apiEndpoint}/candidats/${idRetraitCDC}`, 
          candidatPostRequest, 
          httpOptions
          )
        .subscribe(
            service_contractant => { resolve(service_contractant) },
            error => reject(error)
        )
      }
    );
  }

  


  async putCandidat(idRetraitCDC: string, idCandidat: string, candidatPutRequest: CandidatPutRequest): Promise<Candidat> {

    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<Candidat>(
          `${apiEndpoint}/candidats/${idRetraitCDC}/${idCandidat}`, 
          candidatPutRequest, 
          httpOptions
          )
        .subscribe(
            cnd => { resolve(cnd) },
            error => reject(error)
        )
      }
    );
  }
  

  async deleteCandidat(idRetraitCDC: string, idCandidat: string): Promise<Candidat> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<Candidat>(
          `${apiEndpoint}/candidats/${idRetraitCDC}/${idCandidat}`, 
          )
        .subscribe(
            cnd => { resolve(cnd) },
            error => reject(error)
        )
      }
    );
  }
  
} 