import { Injectable } from '@angular/core'; 
import { COPEO } from 'src/app/models/copeo/copeo.model';
import { COPEOAccess } from '../data-access/copeo.access';
import { COPEOPostRequest } from 'src/app/requests/copeo-post.request';
import { COPEOPutRequest } from 'src/app/requests/copeo-put.request';
import { ModificationCOPEOPostRequest } from 'src/app/requests/modification-copeo-post.request';
import { ModificationCOPEOPutRequest } from 'src/app/requests/modification-copeo-put.request';
import { ModificationCOPEO } from 'src/app/models/copeo/modification-copeo.model';
import { MembreCOPEOPostRequest } from 'src/app/requests/membre-copeo-post.request';
import { MembreCOPEO } from 'src/app/models/copeo/membre-copeo.model';
import { MembreCOPEOPutRequest } from 'src/app/requests/membre-copeo-put.request'; 

@Injectable()
export class COPEOService {

 
  

  constructor(private copeoAccess: COPEOAccess){}

  getAllCOPEOSs(): Promise<COPEO[]> {
    return this.copeoAccess.getAllCOPEOs();
  }

  getCOPEOById(idCOPEO: string): Promise<COPEO> {
    return this.copeoAccess.getCOPEOById(idCOPEO);
  }

  /*
  getMembresActifsCommissionCompetente(idCommissionCompetente: string): Promise<MembreCommissionCompetente[]>{
    return this.copeoAccess.getMembresActifsCommissionCompetente(idCommissionCompetente);
  }
  */

  postCOPEO(copeoPostRequest: COPEOPostRequest): Promise<COPEO> {
    return this.copeoAccess.postCOPEO(copeoPostRequest);
  }

  putCOPEO(idCOPEO: string, copeoPutRequest: COPEOPutRequest): Promise<COPEO> {
    return this.copeoAccess.putCOPEO(idCOPEO, copeoPutRequest);
  }

  deleteCOPEO(idCOPEO: string):Promise<COPEO> {
    return this.copeoAccess.deleteCOPEO(idCOPEO);
  }




  postModificationCOPEO(idCOPEO: string, modificationCOPEOPostRequest: ModificationCOPEOPostRequest): Promise<ModificationCOPEO> {
    return this.copeoAccess.postModificationCOPEO(idCOPEO, modificationCOPEOPostRequest);
  }

  putModificationCOPEO(idModificationCOPEO: string, modificationCOPEOPutRequest: ModificationCOPEOPutRequest): Promise<ModificationCOPEO> {
    return this.copeoAccess.putModificationCOPEO(idModificationCOPEO, modificationCOPEOPutRequest);
  }

  deleteModificationCOPEO(idCOPEO: string, idModificationCOPEO: string): Promise<ModificationCOPEO> {
    return this.copeoAccess.deleteModificationCOPEO(idCOPEO, idModificationCOPEO);
  }
  



  postMembreCOPEO(idCOPEO: string, membreCOPEOPostRequest: MembreCOPEOPostRequest): Promise<MembreCOPEO> {
    return this.copeoAccess.postMembreCOPEO(idCOPEO, membreCOPEOPostRequest);
  }
  
  putMembreCOPEO(idMembreCOPEO: string, membreCOPEOPutRequest: MembreCOPEOPutRequest): Promise<MembreCOPEO> {
    return this.copeoAccess.putMembreCOPEO(idMembreCOPEO, membreCOPEOPutRequest);
  }
 
  deleteMembreCOPEO(idCOPEO: string, idMemebreCOPEO: string): Promise<MembreCOPEO> {
    return this.copeoAccess.deleteMembreCOPEO(idCOPEO, idMemebreCOPEO);
  }





  postMembreModificationCOPEO(idModificationCOPEO: string, membreCOPEOPostRequest: MembreCOPEOPostRequest): Promise<MembreCOPEO> {
    return this.copeoAccess.postMembreModificationCOPEO(idModificationCOPEO, membreCOPEOPostRequest);
  }

  putMembreModificationCOPEO(idMembreCOPEO: string, membreCOPEOPutRequest: MembreCOPEOPutRequest): Promise<MembreCOPEO> {
    return this.copeoAccess.putMembreModificationCOPEO(idMembreCOPEO, membreCOPEOPutRequest);
  }

  deleteMembreModificationCOPEO(idModificationCOPEO: string, idMembreCOPEO: string): Promise<MembreCOPEO> {
    return this.copeoAccess.deleteMembreModificationCOPEO(idModificationCOPEO, idMembreCOPEO);
  }
  



}



