export class AnnulationAttribution {
    attributaire: string
    dateAnnulation: Date
    moyenNotification: string
    dateNotification: Date
    justificatifPath: string
    
    
    constructor(
        attributaire: string,
        dateAnnulation: Date,
        moyenNotification: string,
        dateNotification: Date,
        justificatifPath: string
    ) {
        this.attributaire= attributaire
        this.dateAnnulation= dateAnnulation
        this.moyenNotification= moyenNotification
        this.dateNotification= dateNotification
        this.justificatifPath= justificatifPath
    }
    
    static getDummy(): AnnulationAttribution {
        return new AnnulationAttribution(
            "attributaire",
            new Date(),
            "moyenNotification",
            new Date(),
            "justificatifPath"
        )
    }
}