export class Siteweb {
    _id: string;
    dateCreation: Date;
    denomination: string;
    dateParution: Date;
    lien: string;
    pathJustificatif: string;

    constructor (
        denomination: string,
        dateParution: Date,
        lien: string 
    ){
        this.denomination=denomination
        this.dateParution = dateParution
        this.lien = lien
    }

    static getDummy():Siteweb {
        return new Siteweb(
            "NomSiteweb",
            new Date(),
            "link url"
        )
    }
}