import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms'; 
import { DialogAction } from 'src/app/enums/dialog-action.enum'; 
import { UploadService } from 'src/app/upload.service';
import { ServiceContractantService } from 'src/app/domaine/business-logic/services-contractants.service';
import { NotificationCOPEO } from 'src/app/models/copeo/notification-copeo.model';
import { DeliberationCOPEO } from 'src/app/models/copeo/deliberation-copeo.model';
import { DeliberationCOPEOPostRequest } from 'src/app/requests/deliberation-copeo-post.request';
import { DeliberationCOPEOPutRequest } from 'src/app/requests/deliberation-copeo-put.request';
import { DeliberationCOPEOService } from 'src/app/domaine/business-logic/deliberations-copeo.service'; 


@Component({
  selector: 'app-create-resultat-copeo-ouverture-plis',
  templateUrl: './create-resultat-copeo-ouverture-plis.component.html',
  styleUrls: ['./create-resultat-copeo-ouverture-plis.component.css']
})
export class CreateResultatCopeoOuverturePlisComponent implements OnInit {




  constructor(
    public dialogRef: MatDialogRef<CreateResultatCopeoOuverturePlisComponent>,
    private deliberationCOPEOService: DeliberationCOPEOService,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      notif_copeo: NotificationCOPEO,
      dlbr_copeo: DeliberationCOPEO
    },
    private uploadService: UploadService,
    public serviceContractantService: ServiceContractantService
  ) {
 
  };




  deliberationCOPEOFrom: FormGroup;

  isCreate : boolean;
  isModify: boolean; 
  isDelete: boolean; 
 
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY; 
    this.isDelete = this.data.action === DialogAction.DELETE; 

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.dlbr_copeo.dateCreation);
    }

    this.deliberationCOPEOFrom = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]), 
      'dateOuverturePlis': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'nombrePlisRecus': new FormControl({value: null, disabled: this.isDelete}, [Validators.required, Validators.min(0)]),
      'pathPVOuverturePlis': new FormControl({value: null, disabled: this.isDelete}, [Validators.required])
    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.deliberationCOPEOFrom.patchValue(this.data.dlbr_copeo);
    }
 
  };

   
 

  //Data model to submit to the DB server
  deliberationCOPEOPostRequest: DeliberationCOPEOPostRequest;
  deliberationCOPEOPutRequest: DeliberationCOPEOPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.deliberationCOPEOFrom);




    // save the data into the database
    let result:Promise<DeliberationCOPEO>;
    if (this.isCreate){
      this.deliberationCOPEOPostRequest = new DeliberationCOPEOPostRequest(
        this.deliberationCOPEOFrom.value.dateOuverturePlis,
        this.deliberationCOPEOFrom.value.nombrePlisRecus,
        this.deliberationCOPEOFrom.value.pathPVOuverturePlis 
      );      
      result = this.deliberationCOPEOService.postDeliberationCOPEO(this.data.notif_copeo._id, this.deliberationCOPEOPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.deliberationCOPEOPutRequest = new DeliberationCOPEOPutRequest(
        this.deliberationCOPEOFrom.value.dateOuverturePlis,
        this.deliberationCOPEOFrom.value.nombrePlisRecus,
        this.deliberationCOPEOFrom.value.pathPVOuverturePlis 
      );      
      result = this.deliberationCOPEOService.putDeliberationCOPEO(this.data.dlbr_copeo._id, this.deliberationCOPEOPutRequest);
    };

    if(this.isDelete){   
      result = this.deliberationCOPEOService.deleteDeliberationCOPEO(this.data.notif_copeo._id, this.data.dlbr_copeo._id);
    };

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  uploadingFile = false;
  onlyPdfAllowed = false;
  failedUploadFile = false;

  onFileChange(event) {
    this.uploadingFile = true;
    this.failedUploadFile = false;
    this.onlyPdfAllowed = false;
    const files : File[] = event.target.files;
    if (files[0].type != 'application/pdf'){
      this.onlyPdfAllowed = true;
      this.uploadingFile = false;
    } else {
      const resultUploadFile:Promise<any> = this.uploadService.upload(files[0]).toPromise();
      resultUploadFile.then((val:any) => {
        if(val.hasOwnProperty("filename")){
          this.deliberationCOPEOFrom.patchValue({'pathPVOuverturePlis' : val.filename});
        }
        this.uploadingFile = false;
      }).catch((err =>{
        this.failedUploadFile = true;
        this.uploadingFile = false;
      }));
    }
    
  }


  
}
