import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DialogAction } from 'src/app/enums/dialog-action.enum';
import { FormGroup, FormControl, AbstractControl, Validators, FormGroupDirective, NgForm } from '@angular/forms';
import { Observable } from 'rxjs'; 
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { NotificationOperateurEconomiqueService } from 'src/app/domaine/business-logic/notifications-operateurs-economiques.service';
import { OperateurEconomiqueService } from 'src/app/domaine/business-logic/operateurs-economiques.service';
import { startWith, map } from 'rxjs/operators';
import { NotificationOperateurEconomiquePostRequest } from 'src/app/requests/notification-operateur-economique-post.request';
import { NotificationOperateurEconomiquePutRequest } from 'src/app/requests/notification-operateur-economique-put.request';
import { RepresentantOperateurEconomique } from 'src/app/models/representant.operateur.economique.model';
import { RepresentantOperateurEconomiqueService } from 'src/app/domaine/business-logic/representants-operateurs-economiques.service';
import { Civilite } from 'src/app/enums/civilite.enum';
import { Candidat } from 'src/app/models/candidat.model'; 
import { CandidatService } from 'src/app/domaine/business-logic/candidats.service';
import { CandidatPostRequest } from 'src/app/requests/candidat-post.request';
import { CandidatPutRequest } from 'src/app/requests/candidat-put.request';



 
@Component({
  selector: 'app-create-candidat',
  templateUrl: './create-candidat.component.html',
  styleUrls: ['./create-candidat.component.css']
})
export class CreateCandidatComponent implements OnInit {
  INFO_TEL = "Forme: 023232323 ou +21323232323. Si plusieurs numéros alors séparez-les par des virgules ',' Ex: 023102030,021102030";
  PHONENUMBER_PATTERN: string = "^[+]*[0-9]+(,[+]*[0-9]+)*$"
  constructor(
    public dialogRef: MatDialogRef<CreateCandidatComponent>,
    @Inject(MAT_DIALOG_DATA) public data: {
      action: DialogAction, 
      cnd: Candidat, 
      id_rtr_cdc: string},
    public notificationOperateurEconomiqueService: NotificationOperateurEconomiqueService,
    public operateurEconomiqueService: OperateurEconomiqueService,
    private representantOperateurEconomiqueService: RepresentantOperateurEconomiqueService,
    private candidatService: CandidatService
  ){
  }


  candidatForm: FormGroup;

  isCreate : boolean;
  isModify: boolean;
  isDelete: boolean;

  loadingOE: boolean =false;
  //all the SC list
  optionsOE: OperateurEconomique[] = [];
  //the filtered SC list to show to the user
  filteredOptionsOE: Observable<OperateurEconomique[]>;

  private _filterOE(valueInput: string): OperateurEconomique[] {
    const filterValue = valueInput.toLowerCase();
    return this.optionsOE.filter(
      (oe : OperateurEconomique) => {
        let option:string = oe.denomination + " " + oe.formeJuridique; 
        return option.toLowerCase().includes(filterValue);
      }
    );
  }

  getSelectedOE(_id : string) : OperateurEconomique{
    if (_id){
      return this.optionsOE.find(e => e._id === _id);
    } else {
      return null;
    }
  }

  getSelectedOEDenomination(): string{
    const result = this.getSelectedOE(this.candidatForm.value.operateurEconomique._id);
    if (result) {return result.denomination;}
    return "";
  };

  getSelectedOEFormeJuridique(): string{
    const result = this.getSelectedOE(this.candidatForm.value.operateurEconomique._id);
    if (result) {return result.formeJuridique;}
    return "";
  };




  // date creation to show (if create: today, if delete of modify: date creation )
  date;

  ngOnInit() {

    this.isCreate = this.data.action === DialogAction.CREATE;
    this.isModify = this.data.action === DialogAction.MODIFY;
    this.isDelete = this.data.action === DialogAction.DELETE;

    //set the date of creation
    if (this.isCreate){
      this.date = new FormControl(new Date());
    }else{
      this.date = new FormControl(this.data.cnd.dateCreation);
    }


    

    this.candidatForm = new FormGroup({
      'dateCreation': new FormControl({value: null, disabled: true}, [Validators.required]),
      'dateRetrait': new FormControl({value: null, disabled: this.isDelete}, [Validators.required]),
      'numero': new FormControl({value: null, disabled: this.isDelete}, [Validators.required], this.numeroExists.bind(this)),

      'operateurEconomique': new FormGroup({
        '_id' : new FormControl({value: null, disabled: this.isDelete}, [Validators.required],[this.existsOE.bind(this), this.excludedOE.bind(this), this.alreadyAddedOE.bind(this)]),
        'denomination' : new FormControl({value: null, disabled: true}, [Validators.required]),
        'formeJuridique' : new FormControl({value: null, disabled: true}, [Validators.required])
      })

    });


    if (this.data.action === DialogAction.MODIFY ||
      this.data.action === DialogAction.DELETE ){
      this.candidatForm.patchValue(this.data.cnd);
    }


    // load les operateurs for admin
    this.loadingOE = true;
    const result:Promise<OperateurEconomique[]> = this.operateurEconomiqueService.getAllOperateursEconomiquesWithRepresentants();
    result
    .then((value) =>{
      this.loadingOE = false;
      this.optionsOE = value;

      // recheck once the data is loaded
      const operateurEconomiqueForm : AbstractControl =  this.candidatForm.get("operateurEconomique");
      (<FormGroup>operateurEconomiqueForm).controls["_id"].updateValueAndValidity();
      

      // generate the filtered data
      this.filteredOptionsOE = this.candidatForm.get("operateurEconomique").get("_id").valueChanges
      .pipe(
        startWith(''),
        map( valueOE => this._filterOE(valueOE) )
      );

      return;
    })
    .catch(()=>{
      this.loadingOE = false ;
      return this.optionsOE = null;
    });

  };


  existsOE(control: FormControl): Promise<any> | Observable<any> {
    const operateurEconomiqueId = control.value;
    // if action is modify od delete, and field not changed, ignore it

    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && 
    operateurEconomiqueId === this.data.cnd.operateurEconomique._id)
    return new Promise<any>((resolve, reject) =>  resolve(null));

    const result:OperateurEconomique = this.getSelectedOE(operateurEconomiqueId);
    return new Promise<any>(
      (resolve, reject) => {
          if (result != null) {
            resolve(null);
          } else {
            resolve({ "operateurEconomiqueInvalid": true });
          }
        }
    )
  }

  excludedOE(control: FormControl): Promise<any> | Observable<any> {
    const operateurEconomiqueId = control.value;
    // if action is modify od delete, and field not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && 
    operateurEconomiqueId === this.data.cnd.operateurEconomique._id)
    return new Promise<any>((resolve, reject) =>  resolve(null));

    const result:OperateurEconomique = this.getSelectedOE(operateurEconomiqueId);
    return new Promise<any>(
      (resolve, reject) => {
          if (result && result.estExclu) {
            resolve({ "operateurEconomiqueExcluded": true });
          } else {
            resolve(null);
          }
        }
    )
  }
 






  //Data model to submit to the DB server
  candidatPostRequest: CandidatPostRequest;
  candidatPutRequest: CandidatPutRequest;

  loadingData: boolean= false;
  errorLoadingData: boolean = false;

  onSubmit() {
    this.loadingData = true;
    this.errorLoadingData = false;
    console.log("Submitted", this.candidatForm);




    // save the data into the database
    let result:Promise<Candidat>;

    if (this.isCreate){
      this.candidatPostRequest = new CandidatPostRequest(
        this.candidatForm.value.operateurEconomique._id,
        this.candidatForm.value.dateRetrait,
        this.candidatForm.value.numero
      ); 
      console.log(this.candidatPostRequest);
      result = this.candidatService.postCandidat(this.data.id_rtr_cdc,this.candidatPostRequest);
    };

    // update the data in the database
    if(this.isModify){
      this.candidatPutRequest = new CandidatPutRequest(
        this.candidatForm.value.operateurEconomique._id,
        this.candidatForm.value.dateRetrait,
        this.candidatForm.value.numero
      ); 
      console.log(this.candidatPutRequest);
      result = this.candidatService.putCandidat(this.data.id_rtr_cdc,this.data.cnd._id, this.candidatPutRequest);
    };

    // delete le S/C data from the database
    if(this.isDelete){
      result = this.candidatService.deleteCandidat(this.data.id_rtr_cdc,this.data.cnd._id);
    };
    console.log("Result", result);

    result
    .then((value) => {
      this.loadingData = false;
      this.dialogRef.close(
        value
      )
    })
    .catch(()=>{
      this.loadingData = false;
      return this.errorLoadingData = true;
    });



  }


  alreadyAddedOE(control: FormControl): Promise<any> | Observable<any> {
    const operateurEconomiqueId = control.value;

    // if action is modify od delete, and fiekd not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && 
    operateurEconomiqueId === this.data.cnd.operateurEconomique._id)
    return new Promise<any>((resolve, reject) =>  resolve(null));

    const result:Promise<Candidat> = this.candidatService.getCandidatByOperEconId(this.data.id_rtr_cdc, operateurEconomiqueId);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(rpr => {
          if (rpr != null) {
            resolve({ "alreadySelectedOE": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }





  numeroExists(control: FormControl): Promise<any> | Observable<any> {

    const numero = control.value;

    // if action is modify od delete, and email not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && 
    numero === this.data.cnd.numero)
    return new Promise<any>((resolve, reject) =>  resolve(null));


    const result:Promise<Candidat> = this.candidatService.getCandidatByNumero(this.data.id_rtr_cdc, numero);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(rpr => {
          if (rpr != null) {
            resolve({ "numeroExist": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }




  emailExists(control: FormControl): Promise<any> | Observable<any> {

    const email = control.value;

    // if action is modify od delete, and email not changed, ignore it
    if ( (this.data.action === DialogAction.MODIFY || this.data.action === DialogAction.DELETE) && 
    email === this.data.cnd.representant.email)
    return new Promise<any>((resolve, reject) =>  resolve(null));

    // if the action is creation, but the user selected an existing "representant", the email can stay the same   
    if (this.data.action === DialogAction.CREATE && (<FormGroup>this.candidatForm.get("representant")).controls["_idR"].valid)
    return new Promise<any>((resolve, reject) =>  resolve(null));

    const result:Promise<RepresentantOperateurEconomique> = this.representantOperateurEconomiqueService.getRepresentantByEmail(email);

    return new Promise<any>(
      (resolve, reject) => {
        result
        .then(rpr => {
          if (rpr != null) {
            resolve({ "emailExist": true })
          } else {
            resolve(null)
          }
        })
        .catch(error => resolve({ "checking failed": true }))
      }
    )

  }

  //Civilite
  civilites = Object.keys(Civilite).slice(0);
  parseValueCivilite(value: string) {
    return Civilite[value].toString();
  }


  
}
 

