import { Injectable } from '@angular/core'; 
import { DepotOffres } from 'src/app/models/lancement/depot-offres.model';
import { DepotOffresPostRequest } from 'src/app/requests/depot-offres-post.request';
import { DepotOffresPutRequest } from 'src/app/requests/depot-offres-put.request';
import { DepotOffresAccess } from '../data-access/depot-offres.access';

@Injectable()
export class DepotOffresService {

  constructor(private depotOffresAccess: DepotOffresAccess){}

 

  getAllDepotsOffres(): Promise<DepotOffres[]>{
    return this.depotOffresAccess.getAllDepotsOffres();
  }
  
  getDepotOffres(idDepotOffres: string): Promise<DepotOffres>{
    return this.depotOffresAccess.getDepotOffres(idDepotOffres);
  }
  
  postDepotOffres(idLancement: string, depotOffresPostRequest: DepotOffresPostRequest): Promise<DepotOffres>{
    return this.depotOffresAccess.postDepotOffres(idLancement, depotOffresPostRequest);
  }

  
  putDepotOffres(idDepotOffres: string, depotOffresPutRequest: DepotOffresPutRequest): Promise<DepotOffres>{
    return this.depotOffresAccess.putDepotOffres(idDepotOffres, depotOffresPutRequest);
  }
  
  deleteDepotOffres(idLancement: string, idDepotOffres: string):Promise<DepotOffres>{
    return this.depotOffresAccess.deleteDepotOffres(idLancement, idDepotOffres);
  }
   

 
  
}



