import { Injectable } from '@angular/core';
import { UtilisateurAccess } from '../data-access/utilisateur.access';
import { Utilisateur } from 'src/app/models/utilisateur.model';

@Injectable()
export class UtilisateurService {

  constructor(private utilisateurAccess: UtilisateurAccess){}

  getAllUtilisateurs(): Promise<Utilisateur[]>{
    return this.utilisateurAccess.getAllUtilisateurs()
  }

  getUtilisateurByIdentifiant(id: String): Promise<Utilisateur>{
    return this.utilisateurAccess.getUtilisateurByIdentifiant(id)
  }

  getUtilisateurByEmail(email: String): Promise<Utilisateur>{
    return this.utilisateurAccess.getUtilisateurByEmail(email)
  }

  postUtilisateur(utilisateur: Utilisateur): Promise<Utilisateur>{
    return this.utilisateurAccess.postUtilisateur(utilisateur)
  }

  putUtilisateur(utilisateur: Utilisateur, id: String): Promise<Utilisateur>{
    return this.utilisateurAccess.putUtilisateur(utilisateur, id)
  }

  deleteUtilisateur(id: String):Promise<Utilisateur>{
    return this.utilisateurAccess.deleteUtilisateur(id)
  }
}



