import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatDialog, MatTableDataSource, MatTable } from '@angular/material';
import { PassageCommissionMarchesService } from '../domaine/business-logic/passages-commissions-marches.service';
import { PassageCommissionMarches } from '../models/passage-commission/passage.commission.marches.model';
import { ProjetEnAttentePassageCommissionMarches } from '../models/passage-commission/projet.en.attente.passage.commission.marches.model';
import { fileUploadUrl } from '../config';
import { SelectionModel } from '@angular/cdk/collections';
import { SeanceCommissionCompetente } from '../models/passage-commission/seance.commission.competente.model';
import { SeanceCommissionCompetenteService } from '../domaine/business-logic/seances-commission-competente.service';
import { CreateSeanceCommissionCompetenteComponent } from '../dialogs/create-seance-commission-competente/create-seance-commission-competente.component';
import { DialogAction } from '../enums/dialog-action.enum';
import { CreatePassageCommissionCompetenteComponent } from '../dialogs/create-passage-commission-competente/create-commission-passage-competente.component';
import { CreateDelibererCommissionCompetenteComponent } from '../dialogs/create-deliberer-commission-competente/create-deliberer-commission-competente.component';
import { datediff } from '../library';




@Component({
  selector: 'app-bilans-commissions',
  templateUrl: './bilans-commissions.component.html',
  styleUrls: ['./bilans-commissions.component.css']
})
export class BilansCommissionsComponent implements OnInit {

  idCommission: string;    
  passagesCommissionMarches: PassageCommissionMarches[];
  projetsEnAttentePassagesCommissionMarches: ProjetEnAttentePassageCommissionMarches[];
  loadingData:boolean = false;
  loadingDataProjetEnAttente:boolean = false;
  loadingDataSeances: boolean = false;

  date:Date =  new Date();


  seancesCommissionCompetente: SeanceCommissionCompetente[] ;
    
  constructor(
    private route: ActivatedRoute,
    public dialog: MatDialog, 
    public servicePassageCommissionMarches: PassageCommissionMarchesService,
    public serviceSeanceCommissionCompetente: SeanceCommissionCompetenteService
  ) {
    this.idCommission = route.snapshot.paramMap.get('idCommission');
    this.passagesCommissionMarches = [];
    this.projetsEnAttentePassagesCommissionMarches = [];
    this.seancesCommissionCompetente = [];
  }


  ngOnInit() {
    


    this.loadingData = true;
    const result:Promise<PassageCommissionMarches[]> = this.servicePassageCommissionMarches.getPassagesCommissionMarchesByCommissionId(this.idCommission);
    result
    .then((value) =>{
      this.loadingData = false;
      return this.passagesCommissionMarches = value;
    })
    .catch(()=>{
      this.loadingData = false;
      return this.passagesCommissionMarches = null;
    });



    this.loadingDataProjetEnAttente = true;
    const resultProjetEnAttente:Promise<ProjetEnAttentePassageCommissionMarches[]> = this.servicePassageCommissionMarches.getProjetEnAttenteByCommissionId(this.idCommission);
    resultProjetEnAttente
    .then((value) =>{
      console.log(value)
      this.loadingDataProjetEnAttente = false;
      this.dataSource = new MatTableDataSource<ProjetEnAttentePassageCommissionMarches>(value);
      return this.projetsEnAttentePassagesCommissionMarches = value;
    })
    .catch(()=>{
      this.loadingDataProjetEnAttente = false;
      return this.projetsEnAttentePassagesCommissionMarches = null;
    });



    this.loadingDataSeances = true;
    const resultSeances:Promise<SeanceCommissionCompetente[]> = this.serviceSeanceCommissionCompetente.getAllSeancesCommissionCompetenteByCommissionId(this.idCommission);
    resultSeances
    .then((value) =>{
      console.log(value)
      this.loadingDataSeances = false;
      return this.seancesCommissionCompetente = value;
    })
    .catch(()=>{
      this.loadingDataSeances = false;
      return this.seancesCommissionCompetente = null;
    });


    

  }

  getWaitingDays(dateFrom){
    return datediff(new Date(dateFrom), this.date);
  }
  getPathFile(filename:string){
    return `${fileUploadUrl}/sendMePDF/${filename}`;
  }








  displayedColumns: string[] = ['select', 'projet', 'sousProjet', 'serviceContractantIdentifiant', 'file', 'dateDepot','joursAttente', 'type'];
  dataSource = new MatTableDataSource<ProjetEnAttentePassageCommissionMarches>(this.projetsEnAttentePassagesCommissionMarches);
  selection = new SelectionModel<ProjetEnAttentePassageCommissionMarches>(true, []);

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
        this.selection.clear() :
        this.dataSource.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: ProjetEnAttentePassageCommissionMarches): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row._id}`;
  }

  anySelected(){
    return this.selection.selected && this.selection.selected.length > 0;
  }
  

  openDialogCreateSeanceCommissionCompetenteComponent(): void{
    console.log(this.selection.selected);
    let selectedDossiersEnAttente : ProjetEnAttentePassageCommissionMarches[] = [];
    
    for (let i = 0; i < this.projetsEnAttentePassagesCommissionMarches.length; i++){
      if (this.selection.isSelected(this.projetsEnAttentePassagesCommissionMarches[i])){
        selectedDossiersEnAttente.push(this.projetsEnAttentePassagesCommissionMarches[i]);
      }
    };

    const dialogRef = this.dialog.open(CreateSeanceCommissionCompetenteComponent, {
      width: '800px',
      data: {action: DialogAction.CREATE, doss_att: selectedDossiersEnAttente, snc: null, id_comm: this.idCommission}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log("programmed", result);
      if(this.seancesCommissionCompetente && result){
        this.seancesCommissionCompetente.push(result);
        // remove the selected items
        let tmp: ProjetEnAttentePassageCommissionMarches[] = [];
        for (let i = 0; i < this.projetsEnAttentePassagesCommissionMarches.length; i++){
          if (!this.selection.isSelected(this.projetsEnAttentePassagesCommissionMarches[i])){
            tmp.push(this.projetsEnAttentePassagesCommissionMarches[i]);
          }
        };  
        this.projetsEnAttentePassagesCommissionMarches = tmp;
        this.dataSource = new MatTableDataSource<ProjetEnAttentePassageCommissionMarches>(tmp);
      }
      /*
      if(Projet && result) {
        let sousPrj = projet.sousProjets.find(e => e._id === result._id);
        for(var k in result) sousPrj[k]=result[k];
      }
      */
    });  
  };



  openDialogCreateDelibererCommissionCompetente(passagesCommissionCompetente: string[]): void {
    const dialogRef = this.dialog.open(CreateDelibererCommissionCompetenteComponent, {
      width: '800px',
      data: {action: DialogAction.CREATE, pass_comm_comp: passagesCommissionCompetente}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      console.log(result);
      //this.operations.push(result);
    });

  }

}
