import { Injectable } from '@angular/core';
import { ModificationCommissionCompetente } from 'src/app/models/commission-competente/modification-commission-competente.model';
import { ModificationCommissionCompetentePostRequest } from 'src/app/requests/modification-commission-competente-post.request';
import { ModificationCommissionCompetentePutRequest } from 'src/app/requests/modification-commission-competente-put.request';
import { ModificationCommissionCompetenteAccess } from '../data-access/modification-commission-competente.access';

@Injectable()
export class ModificationCommissionCompetenteService {

  constructor(private modificationCommissionCompetenteAccess: ModificationCommissionCompetenteAccess){}

  getAllModificationsCommissionsCompetentes(): Promise<ModificationCommissionCompetente[]> {
    return this.modificationCommissionCompetenteAccess.getAllModificationsCommissionsCompetentes();
  }

  postModificationCommissionCompetente(modificationCommissionCompetente: ModificationCommissionCompetentePostRequest, idCommissionCompetente: string): Promise<ModificationCommissionCompetente> {
    return this.modificationCommissionCompetenteAccess.postModificationCommissionCompetente(modificationCommissionCompetente, idCommissionCompetente);
  }

  putModificationCommissionCompetente(modificationCommissionCompetente: ModificationCommissionCompetentePutRequest, idModificationCommissionCompetente: String): Promise<ModificationCommissionCompetente> {
    return this.modificationCommissionCompetenteAccess.putModificationCommissionCompetente(modificationCommissionCompetente, idModificationCommissionCompetente);
  }

  deleteModificationCommissionCompetente(idCommissionCompetente: string, idModificationCommissionCompetente: string):Promise<ModificationCommissionCompetente> {
    return this.modificationCommissionCompetenteAccess.deleteModificationCommissionCompetente(idCommissionCompetente, idModificationCommissionCompetente);
  }
}



