import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { OperateurEconomique } from 'src/app/models/operateur.economique.model';
import { OperateurEconomiqueService } from 'src/app/domaine/business-logic/operateurs-economiques.service';
import { fileUploadImageUrl } from 'src/app/config';
import { CreateRepresentantOperateurEconomiqueComponent } from 'src/app/dialogs/create-representant-operateur-economique/create-representant-operateur-economique.component';
import { MatDialog } from '@angular/material';
import { DialogAction } from 'src/app/enums/dialog-action.enum';

@Component({
  selector: 'app-details-operateurs-economiques',
  templateUrl: './details-operateurs-economiques.component.html',
  styleUrls: ['./details-operateurs-economiques.component.css']
})
export class DetailsOperateursEconomiquesComponent implements OnInit {
  
  idOperateurEconomique: string;

  constructor(
    private route: ActivatedRoute,
    public serviceOperateurEconomique: OperateurEconomiqueService,
    public dialog: MatDialog
  ) {
    this.idOperateurEconomique = route.snapshot.paramMap.get('idOperateurEconomique');
 
  }

  loadingDataOperateurEconomique: boolean;
  operateurEconomique: OperateurEconomique;

  ngOnInit() {

      //recuperer les inforamtions du projet 
      this.loadingDataOperateurEconomique = true;
      const result:Promise<OperateurEconomique> = this.serviceOperateurEconomique.getOperateurEconomiqueById(this.idOperateurEconomique);
      result
      .then((value) =>{
        this.loadingDataOperateurEconomique = false;
        return this.operateurEconomique = value;
      })
      .catch(()=>{
        this.loadingDataOperateurEconomique = false;
        return this.operateurEconomique = null;
      });    

    }











    isDefaultLogo(operateurEconomique: OperateurEconomique): boolean {
      if (operateurEconomique.pathLogo == "default-company-logo.png") return true;
      return false;
    }
  
    getLogoPath(filename:string): string {
      if (filename=="default-company-logo.png") return "assets/img/default-company-logo.png";
      else return `${fileUploadImageUrl}/sendMePNG/${filename}`;
    }
    
}


 
    


