import { RoleMembreCOPEO } from '../enums/role-membre-copeo.enum';

export class MembreCOPEOPutRequest {
  _id: string;
  dateCreation: Date;
  role : RoleMembreCOPEO;
  nom: string;
  prenom: string;
  qualite: string;
  matricule: string;
  email: string;
  telephone: string;
  penta: string;
  
  constructor(
    role : RoleMembreCOPEO,
    nom: string,
    prenom: string,
    qualite: string,
    matricule: string,
    email: string,
    telephone: string,
    penta: string
  ) {
    this.role = role;
    this.nom= nom;
    this.prenom= prenom;
    this.qualite= qualite;
    this.matricule= matricule;
    this.email= email;
    this.telephone= telephone;
    this.penta= penta;
  }
  
}