import { apiEndpoint } from 'src/app/config';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable} from '@angular/core';  
import { DepotOffres } from 'src/app/models/lancement/depot-offres.model';
import { DepotOffresPostRequest } from 'src/app/requests/depot-offres-post.request';
import { DepotOffresPutRequest } from 'src/app/requests/depot-offres-put.request';


@Injectable()
export class DepotOffresAccess {

  constructor(private http: HttpClient){}

  getDepotOffres(idDepotOffres: string): Promise<DepotOffres> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<DepotOffres>(
          `${apiEndpoint}/depots-offres/${idDepotOffres}`,
          )
        .subscribe(
          dpt_ofr => { resolve(dpt_ofr) },
          error => reject(error)
        )

      });
  }



 
  async getAllDepotsOffres(): Promise<DepotOffres[]> {
    return new Promise(
      (resolve, reject) => {

        // envoyer une requete API
        this.http.get<DepotOffres[]>(
          `${apiEndpoint}/depots-offres`,
          )
        .subscribe(
          dpt_ofr => { resolve(dpt_ofr) },
          error => reject(error)
        )

      });
  };



  async postDepotOffres(idLancement: string, depotOffrePostRequest: DepotOffresPostRequest): Promise<DepotOffres> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.post<DepotOffres>(
          `${apiEndpoint}/depots-offres/${idLancement}`, 
          depotOffrePostRequest, 
          httpOptions
          )
        .subscribe(
            dpt_oft => { resolve(dpt_oft) },
            error => reject(error)
        )
      }
    );
  };


  async putDepotOffres(idDepotOffres: string, depotOffrePutRequest:DepotOffresPutRequest): Promise<DepotOffres> {
    const httpOptions = {
      headers: new HttpHeaders({
        //'Content-Type':  'application/x-www-form-urlencoded',
        //'Authorization': `Bearer ${this.auth.getIdToken()}`,
      })
    };

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.put<DepotOffres>(
          `${apiEndpoint}/depots-offres/${idDepotOffres}`, 
          depotOffrePutRequest, 
          httpOptions
          )
        .subscribe(
            dpt_ofr => { resolve(dpt_ofr) },
            error => reject(error)
        )
      }
    );
  };
  

  async deleteDepotOffres(idLancement: string, idDepotOffres: string): Promise<DepotOffres> {

    return new Promise(
      (resolve, reject) => {
        
        //send the post query
        this.http.delete<DepotOffres>(
          `${apiEndpoint}/depots-offres/${idLancement}/${idDepotOffres}`
          )
        .subscribe(
            dpt_ofr => { resolve(dpt_ofr) },
            error => reject(error)
        )
      }
    );
  }
  
};