import { Injectable } from '@angular/core';
import { CommissionCompetenteAccess } from '../data-access/commission-competente.access';
import { CommissionCompetente } from 'src/app/models/commission-competente/commission-competente.model';
import { CommissionCompetentePostRequest } from 'src/app/requests/commission-competente-post.request';
import { CommissionCompetentePutRequest } from 'src/app/requests/commission-competente-put.request';
import { MembreCommissionCompetente } from 'src/app/models/commission-competente/membre-commission-competente.model';

@Injectable()
export class CommissionCompetenteService {

  constructor(private commissionCompetenteAccess: CommissionCompetenteAccess){}

  getAllCommissionsCompetentes(): Promise<CommissionCompetente[]> {
    return this.commissionCompetenteAccess.getAllCommissionsCompetentes();
  }

  getMembresActifsCommissionCompetente(idCommissionCompetente: string): Promise<MembreCommissionCompetente[]>{
    return this.commissionCompetenteAccess.getMembresActifsCommissionCompetente(idCommissionCompetente);
  }

  postCommissionCompetente(commissionCompetente: CommissionCompetentePostRequest): Promise<CommissionCompetente> {
    return this.commissionCompetenteAccess.postCommissionCompetente(commissionCompetente);
  }

  putCommissionCompetente(commissionCompetente: CommissionCompetentePutRequest, id: String): Promise<CommissionCompetente> {
    return this.commissionCompetenteAccess.putCommissionCompetente(commissionCompetente, id);
  }

  deleteCommissionCompetente(id: String):Promise<CommissionCompetente> {
    return this.commissionCompetenteAccess.deleteCommissionCompetente(id);
  }
}



